import { EnvInfo, utils } from '../utils';
import { eventReport } from './upload';
import {
	getOperationType,
	getTimestamp,
	getNetworkType,
	getBaseOptions,
	isMiniProgram,
	getRandom,
} from './utils';
import {
	EVENT_RPT_BIZ,
	EVENT_RPT_TOKEN,
	EVENT_IDS,
	DEFAULT_ACCESS_CHANNEL,
	NET_WORK_ERROR_CODE,
} from './const';
import {
	ApiEventData,
	ApiEventOptions,
	EmBaseOptions,
	InitOptions,
	OperateOptions,
	InitSdkOptions,
} from './types';

const DEFAULT_REQUEST_OPTIONS = {
	requestName: '',
	subrequestid: '1',
	requestMethod: 'GET',
	requestUrl: '',
	requestElapse: 0, // 请求耗时
	code: 0,
	codeDesc: '',
	isLastApi: 0,
	isSuccess: 1,
};

const SUCCESS_CODE = 200;
class DataReport {
	private uid: string | undefined;
	private options: EmBaseOptions;
	private accessChannel: string;
	private isReportDt: boolean; // 是否进行上报
	private isCollectDt: boolean; // 是否采集数据
	private eventQueue: Array<any>; // 未上报事件队列
	private platform: EnvInfo;
	private sid: string;

	private static instance: DataReport;
	constructor(opt: InitOptions) {
		this.platform = utils.getEnvInfo();
		this.isReportDt = opt.isReport || false;
		// 默认采集数据
		this.isCollectDt = true;
		if (isMiniProgram(this.platform) && !this.isReportDt) {
			// 小程序不进行数据上报则不采集数据
			this.isCollectDt = false;
		}
		this.eventQueue = [];
		this.accessChannel = DEFAULT_ACCESS_CHANNEL;
		this.options = getBaseOptions(opt, this.platform);
		this.sid = this.options.sdkServiceId;
		this.init(opt);
	}

	static getInstance(opt: InitOptions) {
		if (!DataReport.instance) {
			DataReport.instance = new DataReport(opt);
		}
		return DataReport.instance;
	}

	// 获取sid
	getServiceId() {
		return this.sid || 'sid_0';
	}

	// 设置是否上报
	setIsReportDt(isReport: boolean) {
		this.isReportDt = isReport;
		if (isReport) {
			this.rptEventQueue();
		}
	}

	// 设置是否采集数据
	setIsCollectDt(isCollect: boolean) {
		this.isCollectDt = isCollect;
		if (!isCollect) {
			// 清空采集的数据()
			this.eventQueue = [];
		}
	}

	// 如果存在未上报数据,则进行上报
	rptEventQueue() {
		if (this.eventQueue.length) {
			this.eventQueue.forEach((item, idx) => {
				eventReport.send(item.eventId, item.dt);
				if (idx >= this.eventQueue.length - 1) {
					this.eventQueue = [];
				}
			});
		}
	}

	init(opt: InitOptions) {
		if (!DataReport.instance) {
			DataReport.instance = this;
			eventReport.init({
				biz: EVENT_RPT_BIZ,
				token: EVENT_RPT_TOKEN,
				appId: opt.appkey,
				debug: false,
			});
			this.reportInit();
		}
	}

	updateAppKey(appkey: string) {
		this.options.appkey = appkey;

		this.eventQueue.forEach((item) => {
			if (item.dt.appkey === 'default#appkey') {
				item.dt.appkey = appkey;
			}
		});
	}

	// 上报初始化sdk
	reportInit() {
		if (!this.isCollectDt) {
			return;
		}
		const { did, os, sdkVersion, deviceId } = this.options;

		this.reportData(EVENT_IDS.INIT, {
			did,
			os,
			sdkVersion,
			deviceId,
		});
	}
	// 获取API操作上报方法
	geOperateFun(
		operateOpt: OperateOptions
	): (options: ApiEventOptions) => void {
		if (!this.isCollectDt) {
			// eslint-disable-next-line @typescript-eslint/no-empty-function
			return () => {};
		}
		// 重试次数从1开始
		let retryTimes = 1;
		// 汇总事件code
		let oprCode = 0;
		// 汇总事件desc
		let oprDesc = '';
		// 操作开始时间
		const startTime = getTimestamp();
		const { uid, operationName } = operateOpt;
		// 如果存在uid,则更新uid
		if (uid) {
			this.uid = uid;
		}
		const dt = {
			uid: this.uid,
			operationId: `opr_${utils.getUniqueId()}_${getRandom()}`,
			requestid: `req_${utils.getUniqueId()}_${getRandom()}`,
			operationName,
		};

		return (options: ApiEventOptions) => {
			if (options.data.isSuccess) {
				oprCode = 0;
				oprDesc = '';
			} else {
				// 操作失败 code 0 改为 -1
				if (options.data.code === 0) {
					options.data.code = NET_WORK_ERROR_CODE;
				}
				oprCode = options.data.code ?? oprCode;
				oprDesc = options.data.codeDesc ?? oprDesc;
			}
			// 存在accessChannel则更新
			if (options.data?.accessChannel) {
				this.accessChannel = options.data?.accessChannel;
			}
			// 如果重试，则不更新reqID,并增加重试次数
			if (options.isRetry) {
				retryTimes++;
				options.data.subrequestid = `${retryTimes}`;
			} else {
				dt.requestid = `req_${utils.getUniqueId()}_${getRandom()}`;
				retryTimes = 1;
			}
			// 如果操作结束，则计算该操作的耗时
			if (options.data.isLastApi) {
				const endTime = getTimestamp();
				options.data.requestElapse = endTime - startTime;
				options.data.requestMethod = '';
				options.data.subrequestid = '0';
				options.data.code = oprCode === SUCCESS_CODE ? 0 : oprCode;
				options.data.codeDesc = oprDesc;
			} else {
				// 操作未结束,且没有requestName, 则requestName = operationName
				if (!options.data.requestName) {
					options.data.requestName = operationName;
				}
			}
			// WSS操作不存在耗时,则手动添加耗时
			if (!options.data.requestElapse) {
				options.data.requestElapse = getTimestamp() - startTime;
			}
			this.reportData.call(this, EVENT_IDS.API, {
				...DEFAULT_REQUEST_OPTIONS,
				...dt,
				...options.data,
				accessChannel: this.accessChannel,
				operationType: getOperationType(operationName),
			} as ApiEventData);
			// isEndApi为true表示该api结束即操作结束，追加一个操作结束event
			if (options.isEndApi) {
				this.reportData.call(this, EVENT_IDS.API, {
					...dt,
					...DEFAULT_REQUEST_OPTIONS,
					isSuccess: options.data.isSuccess,
					isLastApi: 1,
					subrequestid: '0',
					requestMethod: '',
					code: oprCode === SUCCESS_CODE ? 0 : oprCode,
					codeDesc: oprDesc,
					requestElapse: options.data.requestElapse,
					accessChannel: this.accessChannel,
					operationType: getOperationType(operationName),
				});
			}
		};
	}

	// 上报数据
	async reportData(eventId: number, opt?: ApiEventData | InitSdkOptions) {
		try {
			const lts = getTimestamp();
			let net: number | string = 0;
			// api操作获取net类型
			if (eventId === EVENT_IDS.API) {
				net = await getNetworkType(this.platform);
			}
			const { appkey, sdkServiceId, sdkProduct } = this.options;
			const dt = {
				lts,
				net,
				appkey,
				sdkServiceId,
				sdkProduct,
				...opt,
			};
			if (this.isReportDt) {
				eventReport.send(eventId, dt);
			} else {
				if (this.isCollectDt) {
					// 收集未上报数据
					this.eventQueue.push({ eventId, dt });
				}
			}
		} catch (error) {
			console.warn(error);
		}
	}
}

export default DataReport;
