/**
 * EventHandler is used to register listening events
 * @module EventHandler
 */
import {
	Event,
	EventHandlerType,
	HandlerData,
	OnPresenceMsg,
} from '../types/eventHandler';
import { ThreadChangeInfo } from '../types/threadApi';
import {
	ThreadMultiDeviceInfo,
	ConversationChangedInfo,
	RoamingDeleteMultiDeviceInfo,
	GroupMemberAttributesUpdateMultiDeviceInfo,
	NotificationMultiDeviceInfo,
} from '../types/engineCore';
import ErrorEvent from '../error/error';
import { logger } from '../logger';
import { RosterData } from '../types/restApis';
import { Connection } from './connection';
import {
	MessagesType,
	MuteMsgBody,
	ReceivedMsgBody,
	RecallMsgBody,
	ChannelMsgBody,
	ModifiedMsg,
	ExcludeAckMessageBody,
} from '../types/message';
import { PresenceType } from '../types/presenceApi';
import { ReactionMessage } from '../types/reactionApi';
import { EventData } from '../types/common';
import { MessagePinEvent } from '../types/eventHandler';
type Parameters =
	| MessagesType
	| OnPresenceMsg
	| ThreadChangeInfo
	| ThreadMultiDeviceInfo
	| ConversationChangedInfo
	| RoamingDeleteMultiDeviceInfo
	| GroupMemberAttributesUpdateMultiDeviceInfo
	| RosterData
	| ErrorEvent
	| MuteMsgBody
	| ReceivedMsgBody
	| RecallMsgBody
	| ChannelMsgBody
	| PresenceType[]
	| ReactionMessage
	| EventData
	| ModifiedMsg
	| MessagePinEvent
	| NotificationMultiDeviceInfo;

const sensitiveEvents = [
	'onTextMessage',
	'onFileMessage',
	'onAudioMessage',
	'onVideoMessage',
	'onImageMessage',
	'onLocationMessage',
	'onCustomMessage',
	'onCMDMessage',
];
class EventHandler {
	handlerData: HandlerData = {};
	constructor(
		Connection: Connection,
		eventHandlerId: string,
		eventHandler: EventHandlerType
	) {
		this.handlerData = {};
		Connection.addEventHandler = this.addEventHandler.bind(this);
		Connection.removeEventHandler = this.removeEventHandler.bind(this);
	}

	/** Adds listening events. */
	/** 添加监听事件。 */
	addEventHandler(eventHandlerId: string, eventHandler: EventHandlerType) {
		this.handlerData[eventHandlerId] = eventHandler;
	}

	/** Removes listening events. */
	/** 移除监听事件。 */
	removeEventHandler(eventHandlerId: string) {
		delete this.handlerData[eventHandlerId];
	}

	/** @hidden */
	dispatch(this: EventHandler, event: Event, parameters?: Parameters) {
		if (sensitiveEvents.includes(event) && parameters) {
			logger.debug('dispatch event: ' + event, {
				id: (parameters as ExcludeAckMessageBody).id,
				type: (parameters as ExcludeAckMessageBody).type,
				time: (parameters as ExcludeAckMessageBody).time,
				from: (parameters as ExcludeAckMessageBody).from,
				to: (parameters as ExcludeAckMessageBody).to,
				chatType: (parameters as ExcludeAckMessageBody).chatType,
			});
		} else if (event === 'onMessage') {
			logger.debug(
				'dispatch event: ' + event,
				(parameters as any[])?.length
			);
		} else {
			logger.debug(
				'dispatch event: ' + event,
				parameters ? parameters : ''
			);
		}

		for (const handler in this.handlerData) {
			const cb = this.handlerData[handler][event];
			cb && cb(parameters);
			// if (typeof this.handlerData[handler][event] !== 'function') return
			// this.handlerData[handler][event](parameters)
		}
	}
}

export { EventHandler };
