import { defaultConfigParams } from './utils';
import { EventQueue } from './event-queue';
import { ConfigParams, EventConfigSchema } from './types';

export class EventReport {
	private inited = false;
	private config: ConfigParams;
	private appId = '';
	private biz = '';
	private eventQueue: EventQueue = {} as EventQueue;

	constructor(params?: ConfigParams) {
		this.config = defaultConfigParams;
		if (params) {
			this.init(params);
		}
	}

	init(params: ConfigParams = {} as ConfigParams) {
		if (!params.biz || !params.token) {
			throw new Error('Event Report: biz or token is not defined');
		}

		try {
			this.appId = params.appId ?? '';
			this.biz = params.biz;
			this.config = { ...defaultConfigParams, ...params };
			this.eventQueue = new EventQueue(this.config); // 初始化事件队列
			this.log(params);
			this.inited = true;
		} catch (err) {
			console.error(err);
		}
	}

	updateAppId(appId: string) {
		this.appId = appId;
	}

	send(eventId: number, data: object = {}, config?: EventConfigSchema) {
		if (!this.inited) {
			console.error('Event Report: init is not called');
			return;
		}

		const event = {
			...data,
			eventId: Number(eventId),
			biz: this.biz,
			appId: config?.appId ?? this.appId,
		};
		this.eventQueue.add(event);
		this.log(event);
	}

	private log(params: Record<string, any>) {
		try {
			if (this.config.debug) {
				const { payload, ...restParams } = params;
				console.log(
					`%c Event Report: ${this.config.biz} `,
					'background: #8A97FC; color: #fff'
				);
				console.table(restParams);
				if (payload) {
					console.info('payload:');
					console.table(payload);
				}
			}
		} catch (err) {
			console.error(err);
		}
	}
}

export const eventReport = new EventReport();
