/**
 * Gets the chat room list with pagination.
 *
 * ```typescript
 * connection.getChatRooms({pagenum: 1, pagesize: 20})
 * ```
 */

/**
 * 分页获取聊天室列表。
 *
 * ```typescript
 * connection.getChatRooms({pagenum: 1, pagesize: 20})
 * ```
 */
function getChatRooms(
	this: any,
	params: {
		/** The page number, starting from 1. */
		/** 当前页码，从 1 开始。*/
		pagenum: number;
		/** The number of chat rooms that you expect to get on each page. The default value is 20. */
		/** 每页期望返回的聊天室数量，默认为 20。*/
		pagesize: number;
		success?: (res: AsyncResult<ChatRoomTypes.GetChatRoomsResult>) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<ChatRoomTypes.GetChatRoomsResult>> {
	if (
		typeof params.pagenum !== 'number' ||
		typeof params.pagesize !== 'number'
	) {
		throw Error('Invalid parameter');
	}
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}

	const pageInfo = {
		pagenum: params.pagenum || 1,
		pagesize: params.pagesize || 20,
	};
	const { orgName, appName, accessToken } = this.context;

	const suc = (data: any) => {
		typeof params.success === 'function' && params.success(data);
	};
	const errorCb = (res: any) => {
		// 逐渐把这种回调删除
		if (res.error && res.error_description) {
			this.onError &&
				this.onError({
					type: Code.WEBIM_CONNCTION_LOAD_CHATROOM_ERROR,
					message: res.error_description,
					data: res,
				});
		}
		typeof params.error === 'function' && params.error(res);
	};
	const opts = {
		url: this.apiUrl + '/' + orgName + '/' + appName + '/chatrooms',
		dataType: 'json',
		type: 'GET',
		headers: {
			Authorization: 'Bearer ' + accessToken,
		},
		data: pageInfo,
		success: suc,
		error: errorCb,
	};
	logger.debug('Call getChatRooms', params);
	return ajax.call(this, opts, OperationName.GET_CHATROOM_LIST);
}

/**
 * Creates a chat room.
 *
 * ```typescript
 * connection.createChatRoom({name: 'myChatRoom', description: 'this is my chatroom', maxusers: 200, members: ['user1'], token: 'your token'})
 * ```
 */

/**
 * 创建聊天室。
 *
 * ```typescript
 * connection.createChatRoom({name: 'myChatRoom', description: 'this is my chatroom', maxusers: 200, members: ['user1'], token: 'your token'})
 * ```
 */
function createChatRoom(
	this: any,
	params: {
		/** The chat room name. */
		/** 聊天室名称。*/
		name: string;
		/** The description of the chat room. */
		/** 聊天室描述。 */
		description: string;
		/** The maximum number of members in the chat room, including the chat room creator. The value is an integer in the range of [1,5000], with 200 as the default. */
		/** 聊天室最大成员数（包括聊天室创建者），值为数值类型，默认值 200，最大值 5,000。 */
		maxusers: number;
		/** (Optional) The members in the chat room. An array of user IDs of members. If you set this parameter, ensure that the array contains at least one user ID. */
		/** （可选）聊天室成员，由用户 ID 组成的数组。如果设置了该参数，请确保数组内至少包含一个用户。 */
		members?: UserId[];
		/** The super admin token. */
		/** 超级管理员 token。*/
		token?: string;
		success?: (
			res: AsyncResult<ChatRoomTypes.CreateChatRoomResult>
		) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<ChatRoomTypes.CreateChatRoomResult>> {
	if (typeof params.name !== 'string') {
		throw Error('Invalid parameter');
	}
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	const data = {
		name: params.name,
		description: params.description,
		maxusers: params.maxusers,
		owner: this.user,
		members: params.members,
	};
	const { orgName, appName, accessToken, jid } = this.context;
	const opts = {
		url: `${this.apiUrl}/${orgName}/${appName}/chatrooms?resource=${jid.clientResource}`,
		dataType: 'json',
		type: 'POST',
		data: JSON.stringify(data),
		headers: {
			Authorization: 'Bearer ' + (params.token || accessToken),
			'Content-Type': 'application/json',
		},
		success: params.success,
		error: params.error,
	};
	logger.debug('Call createChatRoom', params);
	return ajax.call(this, opts, OperationName.CREATE_CHATROOM);
}

