import { Connection } from '../engineCore/connection';
import { Code } from '../status';
import ErrorEvent from '../error/error';
import { logger } from '../logger/index';
/** @hidden */
function checkAppkey(this: Connection): boolean {
	const appName = this.context.appName;
	const orgName = this.context.orgName;
	if (!appName || !orgName) {
		this.onError &&
			this.onError({
				type: Code.WEBIM_CONNCTION_AUTH_ERROR,
				message: 'appName or orgName is illegal',
			});
		return false;
	}
	return true;
}
/** @hidden */
function checkToken(this: Connection): boolean {
	const token = this.context.accessToken;
	if (!token) {
		const error = ErrorEvent.create({
			type: Code.WEBIM_CONNCTION_TOKEN_NOT_ASSIGN_ERROR,
			message: 'token not assign error',
		});
		logger.debug('token not assign error', token);
		this.onError && this.onError(error);
		this.eventHandler?.dispatch('onError', error);
		return false;
	}
	return true;
}
/** @hidden */
function checkStatus(this: Connection):
	| {
			error: ErrorEvent;
	  }
	| {
			error?: undefined;
	  } {
	if (this._initWithAppId) {
		if (!checkAppkey.call(this)) {
			const error = ErrorEvent.create({
				type: Code.WEBIM_CONNECTION_CLOSED,
				message: 'not login',
			});
			return { error };
		}
		if (!checkToken.call(this)) {
			const error = ErrorEvent.create({
				type: Code.WEBIM_CONNCTION_TOKEN_NOT_ASSIGN_ERROR,
				message: 'token not assign error',
			});
			return { error };
		}
	} else {
		if (!checkAppkey.call(this)) {
			const error = ErrorEvent.create({
				type: Code.REST_PARAMS_STATUS,
				message: 'appkey or token error',
			});
			return { error };
		}
	}
	return {};
}

interface UnAttributesData {
	data: {
		[key: string]: any;
	};
	type: Code;
}
interface AttributesInfo {
	data: {
		status: 'success' | 'fail';
		errorKeys: {
			[key: string]: string;
		};
		successKeys: Array<string>;
	};
	type: Code;
}

export function formatAttributesData(attributesData: UnAttributesData) {
	const { data, type } = attributesData;
	let status: 'success' | 'fail';
	Object.keys(data.errorKeys).length > 0
		? (status = 'fail')
		: (status = 'success');
	const resp: AttributesInfo = {
		data: {
			status: status,
			errorKeys: data.errorKeys,
			successKeys: data.successKeys,
		},
		type,
	};
	return resp;
}

export function formatAttributeInfo(
	attributesData: UnAttributesData
): ErrorEvent | void {
	const { data } = attributesData;
	let error: ErrorEvent | undefined = undefined;
	if (Object.keys(data.errorKeys).length > 0) {
		Object.keys(data.errorKeys).forEach((item) => {
			const errorMessage: string = data.errorKeys[item];
			if (errorMessage.includes('is not part of you')) {
				error = ErrorEvent.create({
					type: Code.NO_PERMISSION,
					message: errorMessage,
				});
			} else if (
				errorMessage.includes(
					'size of metadata for this single chatroom exceeds the user defined limit'
				) ||
				errorMessage.includes(
					'total size of chatroom metadata for this app exceeds the user defined limit'
				) ||
				errorMessage.includes('is exceeding maximum limit')
			) {
				error = ErrorEvent.create({
					type: Code.MAX_LIMIT,
					message: errorMessage,
				});
			} else if (errorMessage.includes('is not Legal')) {
				error = ErrorEvent.create({
					type: Code.REQUEST_PARAMETER_ERROR,
					message: errorMessage,
				});
			} else if (
				errorMessage.includes(
					'Failed to update userMetadata. Concurrent updates not allowed'
				)
			) {
				error = ErrorEvent.create({
					type: Code.OPERATION_NOT_ALLOWED,
					message: errorMessage,
				});
			} else {
				error = ErrorEvent.create({
					type: Code.WEBIM_CONNCTION_AJAX_ERROR,
					message: errorMessage,
				});
			}
		});
	}
	return error;
}

export { checkStatus };
