import { Code } from '../status';
import ErrorEvent from '../error/error';
import { GroupModifyInfo } from './groupApi';

interface AsyncResult<T = any> {
	/** Status of the request. */
	/** 请求状态码。 */
	type: Code;
	/** Data returned by successful request. */
	/** 请求成功返回的数据。 */
	data?: T;
	/** Data returned by successful request. */
	/** 请求成功返回的数据。 */
	entities?: T;
	/** message. */
	/** 描述信息。 */
	message?: string;
	[key: string]: any;
}

type UserId = string;
type GroupId = string;
interface CommonRequestResult {
	/** The result of request. */
	/** 请求结果是否成功。 */
	result: boolean;
	/** Action. */
	/** 请求动作。 */
	action: string;
	/** The reason of failure. */
	/** 失败的原因。 */
	reason?: string;
	/** The user ID. */
	/** 用户 ID。 */
	user: string;
	/** The group/chat room ID. */
	/** 群组/聊天室 ID。 */
	id: string;
}

interface Jid {
	appKey: string;
	clientResource: string;
	domain: string;
	name: string;
}

interface EventData {
	/** The type of operation. */
	/** 操作类型。 */
	operation: string;
	/** The ID of a group or chatroom. */
	/** 群组或者聊天室 ID。 */
	id: string;
	/** Message sender. */
	/** 消息发送者。 */
	from: string;
	/** The modified group info. */
	/** 更新的群组信息 */
	detail?: GroupModifyInfo;
	/** The name of a group or chatroom.  */
	/** 群组或聊天室名称。 */
	name?: string;
	/** The userId of a group event.  */
	/** 被操作的用户 ID。 */
	userId?: string;
	/** Custom Attributes.  */
	/** 自定义属性。 */
	attributes?: Array<string> | { [key: string]: string };
	/** The member count. For a chat room with more than 2000 members, `memberCount` will not be returned. */
	/** 成员人数。当聊天室成员人数超过 2000 时, `memberCount` 将不会返回  */
	memberCount?: number;
	/** The extension fields. */
	/** 扩展信息。 */
	ext?: string;
	/** The reason for the event. */
	/** 事件触发的原因。*/
	reason?: string;
	/** The updated announcement. */
	/** 更新后的公告。 */
	announcement?: string;
	/** Expiration time for user mute. */
	/** 用户禁言到期的时间。 */
	muteTimestamp?: { [key: string]: number };
}

interface AttributeRespData {
	is_forced: boolean;
	operator: string;
	properties: {
		[key: string]: string;
	};
	result: {
		errorKeys: {
			[key: string]: string;
		};
		successKeys: Array<string>;
	};
	keys?: Array<string>;
	auto_delete?: boolean;
}

export enum OfflineMessagePullState {
	SYNC_INIT = 0,
	SYNC_START,
	SYNC_FINISH,
}

export type {
	AsyncResult,
	UserId,
	CommonRequestResult,
	GroupId,
	ErrorEvent,
	Jid,
	EventData,
	AttributeRespData,
};
