import { utils } from '../utils/index';
import { MessagePriority } from '../types/message';
// TODO: ackId mId 统一
export interface DeliveryParameters {
	/** The ID of the delivery receipt. */
	/** 本条送达回执的 ID。 */
	id: string;
	/** The message type. */
	/** 消息类型。 */
	type: 'delivery';
}
export interface DeliveryMsgSetParameters {
	/** The ID of the delivered message. This parameter has the same value as mid. */
	/** 已送达消息的 ID。该参数与 mid 参数的值相同。*/
	ackId: string;
	/** The recipient. */
	/** 消息接收方。 */
	to: string;
	/** The sender, which can only be the current user and can not be changed. */
	/** 消息发送方，只能是当前用户，无法修改。*/
	from?: string;
}
export interface DeliveryMsgBody {
	/** The ID of the delivery receipt. */
	/** 本条送达回执的 ID。 */
	id: string;
	/** The ID of the delivered message. */
	/** 已送达消息的 ID。 */
	mid?: string;
	/** The ID of the delivered message. This parameter has the same value as mid. */
	/** 已送达消息的 ID。该参数与 mid 参数的值相同。*/
	ackId: string;
	/** The message type. */
	/** 消息类型。 */
	type: 'delivery';
	/** The recipient. */
	/** 消息接收方。 */
	to: string;
	/** The sender, which can only be the current user and can not be changed. */
	/** 消息发送方，只能是当前用户，无法修改。*/
	from?: string;
	/** Whether the message is a thread message. */
	/** 消息是否为子区消息 */
	isChatThread?: boolean;
	/** Message priority. */
	/** 消息优先级 */
	priority?: MessagePriority;
	/** Whether the message is delivered only when the recipient(s) is/are online:
	 *  - `true`: The message is delivered only when the recipient(s) is/are online. If the recipient is offline, the message is discarded.
	 *  - (Default) `false`: The message is delivered when the recipient(s) is/are online. If the recipient(s) is/are offline, the message will not be delivered to them until they get online.
	 */
	/** 消息是否只投递给在线用户：
	 * - `true`: 只有消息接收方在线时才能投递成功。若接收方离线，则消息会被丢弃。
	 * - （默认）`false`: 如果用户在线，则直接投递；如果用户离线，消息会在用户上线时投递。
	 */
	deliverOnlineOnly?: boolean;
}

export interface CreateDeliveryMsgParameters {
	/** The ID of the delivered message. */
	/** 送达消息 ID。 */
	ackId: string;
	/** The message type. */
	/** 消息类型。 */
	type: 'delivery';
	/** The recipient. */
	/** 消息接收方。 */
	to: string;
	/** The sender, which can only be the current user and can not be changed.*/
	/** 消息发送方，只能是当前用户，无法修改。*/
	from?: string;
	/** Whether the message is a thread message. */
	/** 消息是否为子区消息 */
	isChatThread?: boolean;
	/** Message priority. */
	/** 消息优先级 */
	priority?: MessagePriority;
	/** Whether the message is delivered only when the recipient(s) is/are online:
	 *  - `true`: The message is delivered only when the recipient(s) is/are online. If the recipient is offline, the message is discarded.
	 *  - (Default) `false`: The message is delivered when the recipient(s) is/are online. If the recipient(s) is/are offline, the message will not be delivered to them until they get online.
	 */
	/** 消息是否只投递给在线用户：
	 * - `true`: 只有消息接收方在线时才能投递成功。若接收方离线，则消息会被丢弃。
	 * - （默认）`false`: 如果用户在线，则直接投递；如果用户离线，消息会在用户上线时投递。
	 */
	deliverOnlineOnly?: boolean;
}

export class Delivery {
	id: string;
	type: 'delivery';
	body?: DeliveryMsgBody;
	constructor(parameters: DeliveryParameters) {
		this.id = parameters.id;
		this.type = parameters.type;
	}
	/**
	 * @hidden
	 */
	set(options: DeliveryMsgSetParameters) {
		this.body = {
			id: this.id,
			ackId: options.ackId,
			type: 'delivery',
			to: options.to,
			from: options.from || '',
		};
	}
	static create(options: CreateDeliveryMsgParameters): DeliveryMsgBody {
		return {
			id: utils.getUniqueId(),
			ackId: options.ackId,
			type: 'delivery',
			to: options.to,
			from: options.from || '',
			isChatThread: options.isChatThread,
			priority: options.priority,
			deliverOnlineOnly: options.deliverOnlineOnly,
		};
	}
}

// export default Delivery;
