import { Code } from '../status';
import { logger } from '../logger';
import { Connection } from '../engineCore/connection';
import { MAX_POOL, PART_SIZE, SERVER_SLICE_INFO } from './const';
import {
	OperationName,
	RequestName,
	ApiEventOptions,
} from '../dataReport/types';
import {
	multipartInit,
	multipartUpload,
	multipartComplete,
	multipartAbort,
} from './multiPartApi';

interface SliceUploadFileProgress {
	loaded: number;
	total: number;
	isTrusted: boolean;
	type: string;
	lengthComputable: boolean;
}

interface SliceUploadFileOptions {
	file: File;
	onFileUploadProgress?: (e: SliceUploadFileProgress) => void;
	onFileUploadComplete?: (data: any) => void;
	onFileUploadError?: (err: any) => void;
	onFileUploadCanceled?: () => void;
	onInitFail?: () => void;
	thumbnailInfo?: {
		width?: number;
		height?: number;
	};
}

class SliceUploadFile {
	private uuid: string;
	private partSize: number;
	private pool: Record<string, any>[];
	private connection: Connection;
	private options: SliceUploadFileOptions;
	private progressArr: number[];
	private file: File;
	private rpt: (options: ApiEventOptions) => void;

	constructor(connection: Connection, opt: SliceUploadFileOptions) {
		this.uuid = '';
		this.pool = [];
		this.progressArr = [];
		this.connection = connection;
		this.options = opt;
		this.partSize = PART_SIZE;
		this.file = opt.file;
		this.init();
		this.rpt = this.connection.dataReport.geOperateFun({
			operationName: OperationName.REST_UPLOAD_FILE_IN_PARTS,
		});
	}
	/**
	 * 初始化分块上传
	 */
	async init() {
		try {
			const res = await multipartInit.call(this.connection);
			const {
				fileMaxSize = 0,
				partMinSize = PART_SIZE,
				uuid = '',
			} = res.data || {};
			const { elapse, httpCode, url } = res.extraInfo;
			this.partSize = partMinSize;
			this.uuid = uuid;
			SERVER_SLICE_INFO.size = partMinSize;
			logger.debug(
				'multipartInit success',
				`uuid: ${uuid}`,
				`fileMaxSize: ${fileMaxSize}`,
				`partMinSize: ${partMinSize}`
			);
			if (this.file.size > fileMaxSize) {
				// 终止上传
				await this.multipartAbort();
				this.options.onFileUploadError?.({
					code: Code.WEBIM_UPLOADFILE_ERROR,
					message: 'The file size exceeds the maximum limit',
				});
				return;
			}
			this.rpt({
				data: {
					requestUrl: url,
					requestName: RequestName.REST_INIT_UPLOAD_TASK_IN_PARTS,
					requestElapse: elapse,
					requestMethod: 'POST',
					isSuccess: 1,
					code: httpCode,
				},
			});
			// 初始化完成之后开始上传分块
			this.upload();
		} catch (error: any) {
			const { elapse, httpCode, errDesc, url } = error?.extraInfo || {};
			this.rpt({
				data: {
					requestUrl: url,
					requestName: RequestName.REST_INIT_UPLOAD_TASK_IN_PARTS,
					requestElapse: elapse,
					requestMethod: 'POST',
					isSuccess: 0,
					codeDesc: errDesc,
					code: httpCode,
				},
			});
			await this.multipartAbort();
			this.options?.onInitFail?.();
		}
	}
	/**
	 * 上传分片
	 */
	async upload() {
		const reader = new FileReader();
		reader.readAsArrayBuffer(this.file);
		const fileSize = this.file.size;
		// 总分片数
		const parts = Math.ceil(fileSize / this.partSize);
		try {
			// 遍历上传分片
			for (let idx = 0; idx < parts; idx++) {
				const start = idx * this.partSize;
				const end = Math.min(fileSize, start + this.partSize);
				const part = this.file?.slice(start, end);
				const task = multipartUpload.call(this.connection, {
					uuid: this.uuid,
					partNumber: `${idx + 1}`,
					part: part,
					onProgress: (e) => {
						this.handleUploadProgress(e, idx);
					},
				});
				// 上传成功之后，从并发池中移除该任务
				task.then((res) => {
					const { elapse, httpCode, url } = res?.extraInfo || {};
					this.rpt({
						data: {
							requestUrl: url,
							requestName: RequestName.REST_UPLOAD_PART,
							requestElapse: elapse,
							isSuccess: 1,
							requestMethod: 'PUT',
							code: httpCode,
						},
					});
					this.handleTask(task);
				});

				task.catch((error) => {
					const { elapse, httpCode, url, errDesc } =
						error?.extraInfo || {};
					this.rpt({
						data: {
							requestUrl: url,
							requestName: RequestName.REST_UPLOAD_PART,
							requestElapse: elapse,
							isSuccess: 0,
							requestMethod: 'PUT',
							code: httpCode,
							codeDesc: errDesc,
						},
					});
					delete error.extraInfo;
				});
				// 并发池中塞入一个新任务
				this.pool.push(task);
				// 并发池中任务数达到最大值
				if (this.pool.length === MAX_POOL) {
					// 等待最快的任务完成之后再继续执行for循环
					await Promise.race(this.pool);
				}
			}
			// 等待所有分片上传完成
			await Promise.all(this.pool);
			// 合并文件
			this.multipartComplete();
		} catch (error) {
			await this.multipartAbort();
			this.rpt({
				data: {
					isLastApi: 1,
					isSuccess: 0,
				},
			});
			this.options.onFileUploadError?.(error);
		}
	}
	/**
	 * 完成分片上传
	 */
	async multipartComplete() {
		try {
			const { width, height } = this.options.thumbnailInfo || {};
			const res = await multipartComplete.call(this.connection, {
				uuid: this.uuid,
				thumbnailHeight: height,
				thumbnailWidth: width,
			});
			const { elapse, httpCode, url } = res?.extraInfo || {};
			this.rpt({
				data: {
					requestUrl: url,
					requestName: RequestName.REST_COMPLETE_UPLOAD_PART,
					requestElapse: elapse,
					requestMethod: 'POST',
					isSuccess: 1,
					code: httpCode,
				},
			});
			this.rpt({
				data: {
					isLastApi: 1,
					isSuccess: 1,
				},
			});
			this.options.onFileUploadComplete?.(res);
		} catch (error: any) {
			const { elapse, httpCode, errDesc, url } = error?.extraInfo || {};
			this.rpt({
				data: {
					requestUrl: url,
					requestName: RequestName.REST_COMPLETE_UPLOAD_PART,
					requestElapse: elapse,
					requestMethod: 'POST',
					isSuccess: 0,
					codeDesc: errDesc,
					code: httpCode,
				},
			});
			this.rpt({
				data: {
					isLastApi: 1,
					isSuccess: 0,
				},
			});
			this.options.onFileUploadError?.(error);
		}
	}
	/**
	 * 终止分片上传
	 */
	async multipartAbort() {
		if (!this.uuid) return;
		try {
			const res = await multipartAbort.call(this.connection, {
				uuid: this.uuid,
			});
			const { elapse, httpCode, url } = res?.extraInfo || {};
			this.rpt({
				data: {
					requestUrl: url,
					requestName: RequestName.REST_ABORT_UPLOAD_PART,
					requestElapse: elapse,
					requestMethod: 'DELETE',
					isSuccess: 1,
					code: httpCode,
				},
			});
		} catch (error: any) {
			const { elapse, httpCode, errDesc, url } = error?.extraInfo || {};
			this.rpt({
				data: {
					requestUrl: url,
					requestName: RequestName.REST_ABORT_UPLOAD_PART,
					requestElapse: elapse,
					requestMethod: 'DELETE',
					isSuccess: 0,
					codeDesc: errDesc,
					code: httpCode,
				},
			});
		}
	}
	/**
	 * 从并发池中移除一个任务
	 * @param task 任务
	 */
	handleTask(task: Promise<Record<string, any>>) {
		const index = this.pool.findIndex((t) => t === task);
		this.pool.splice(index, 1);
	}
	/** 处理上传进度 */
	handleUploadProgress = (
		progressEvent: ProgressEvent<XMLHttpRequestEventTarget>,
		partIndex: number
	) => {
		if (progressEvent.total) {
			this.progressArr[partIndex] = progressEvent.loaded;
			// 计算当前已上传的总大小
			const curTotal = this.progressArr.reduce(
				(accumulator, currentValue) => accumulator + currentValue,
				0
			);
			this.options.onFileUploadProgress?.({
				isTrusted: progressEvent.isTrusted,
				type: progressEvent.type,
				loaded: curTotal > this.file.size ? this.file.size : curTotal,
				total: this.file.size,
				lengthComputable: progressEvent.lengthComputable,
			});
		}
	};
}

export default SliceUploadFile;
