const platForm = utils.getEnvInfo();

/**
 * @internal
 */
function generateProvision(this: any): any {
	let deviceId = 'webim';
	let deviceName = '';
	let deviceUuid = '';
	const emptyMessage: [] = [];
	const time = new Date().valueOf();
	const devicePlatform = utils.getDevicePlatform(platForm);
	if (this.isFixedDeviceId) {
		const deviceInfo = utils.getLocalDeviceInfo();
		// if localStorage has device info, use it
		if (deviceInfo) {
			const info = JSON.parse(deviceInfo);
			deviceId = info.deviceId;
			deviceName = info.deviceName;
			deviceUuid = info.deviceUuid;
		} else {
			if (this.deviceId === 'webim') {
				deviceUuid = `${devicePlatform}_${time.toString()}`;
				deviceId = `${this.deviceId}_${deviceUuid}`;
				deviceName = this.deviceId;
			} else {
				deviceId =
					deviceName =
					deviceUuid =
						`webim_${devicePlatform}_${this.deviceId}`;
			}
			utils.setLocalDeviceInfo(
				JSON.stringify({ deviceId, deviceName, deviceUuid })
			);
		}
	} else {
		// random device id
		if (this.deviceId === 'webim') {
			deviceUuid = `random_${devicePlatform}_${time.toString()}`;
			deviceId = `${this.deviceId}_${deviceUuid}`;
			deviceName = this.deviceId;
		} else {
			deviceId =
				deviceName =
				deviceUuid =
					`webim_${devicePlatform}_${this.deviceId}`;
		}
	}

	if (this.context.jid) {
		this.context.jid.clientResource = deviceId;
	}
	const provisionMessage = this.root.lookup('easemob.pb.Provision') as any;
	let secondMessage = provisionMessage.decode(emptyMessage);

	secondMessage.compressType = this.compressType;
	secondMessage.encryptType = this.encryptType;
	secondMessage.osType = this.osType;
	secondMessage.version = this.version;
	secondMessage.deviceName = deviceName;
	secondMessage.resource = deviceId;
	secondMessage.deviceUuid = deviceUuid;
	secondMessage.authToken = '{"token":"$t$' + this.token + '"}';
	secondMessage.sessionId = Date.now().toString() + ':';
	// set custom ext for  login info
	if (this.loginInfoCustomExt) {
		secondMessage.reason = this.loginInfoCustomExt;
	}
	logger.debug('provision info', {
		version: this.version,
		resource: deviceId,
		isFixedDeviceId: this.isFixedDeviceId,
		loginInfoCustomExt: !!this.loginInfoCustomExt,
		token: this.token?.slice?.(0, 10) + '...',
		userId: this.context?.jid?.name,
	});
	secondMessage.actionVersion = 'v5.0';
	secondMessage = provisionMessage.encode(secondMessage).finish();
	const firstLookUpMessage = this.root.lookup('easemob.pb.MSync') as any;
	let firstMessage = firstLookUpMessage.decode(emptyMessage);

	firstMessage.version = this.version;
	firstMessage.guid = this.context.jid;
	firstMessage.auth = '$t$' + this.token;
	firstMessage.command = 3;
	firstMessage.deviceId = deviceName; //conn.deviceId;
	firstMessage.serviceId = this.dataReport.getServiceId();
	firstMessage.encryptType = this.encryptType;
	firstMessage.payload = secondMessage;
	firstMessage = firstLookUpMessage.encode(firstMessage).finish();
	return firstMessage;
}

/**
 * @internal
 */
function base64transform(this: any, str: number[], msgId?: string) {
	const envInfo = utils.getEnvInfo();
	if (envInfo.platform === 'zfb' || envInfo.platform === 'dd') {
		// 支付宝 WEB
		let strr = '';
		for (let i = 0; i < str.length; i++) {
			const str0 = String.fromCharCode(str[i]);
			strr = strr + str0;
		}
		strr = Base64.btoa(strr);
		// if (envInfo.platform === 'web') {
		// 	return strr;
		// }
		const obj = {
			data: strr,
			isBuffer: false,
			complete: () => {
				// do nothing
			},
			fail: (e: any) => {
				// console.log('小程序 msg Obj fail 发送失败', e)
				if (
					e.errMsg === 'sendSocketMessage:fail taskID not exist' ||
					e.errMsg ===
						'SocketTast.send:fail SocketTask.readyState is not OPEN'
				) {
					logger.debug('send message fail and reconnect');
					if (!this.reconnecting) {
						this.reconnect();
					}
				}
				if (msgId) {
					this._msgHash &&
						this._msgHash[msgId] &&
						this._msgHash[msgId].fail({
							id: msgId,
						});
				}
			},
		};
		return obj;
	} else {
		// 小程序
		// let init8arr = new Uint8Array(str) // send init8arr.buffer
		const int8Array: Int8Array = str as unknown as Int8Array;
		const obj = {
			data: int8Array.buffer.slice(
				int8Array.byteOffset,
				int8Array.byteOffset + int8Array.byteLength
			),
			fail: (e: any) => {
				// 使用uniapp生成的微信小程序断网重连没有触发 online 发消息时报下面错误
				if (
					e.errMsg === 'sendSocketMessage:fail taskID not exist' ||
					e.errMsg ===
						'SocketTast.send:fail SocketTask.readyState is not OPEN'
				) {
					if (!this.reconnecting) {
						this.reconnect();
					}
				}
				if (msgId) {
					this._msgHash &&
						this._msgHash[msgId] &&
						this._msgHash[msgId].fail({
							id: msgId,
						});
				}
			},
		};
		return obj;
	}
}

/**
 * @internal
 * 将meta 消息分发到各个类型的回调
 */
async function distributeMeta(this: Connection, metas: any, status: any) {
	logger.debug('distributeMeta, metas length: ', metas.length);
	const msgPromises = [];
	for (let i = 0; i < metas.length; i++) {
		const meta = metas[i];
		const msgId = new Long(
			meta.id.low,
			meta.id.high,
			meta.id.unsigned
		).toString();
		const from: string = meta.from.name;
		const to: string = meta.to ? meta.to.name : '';
		const isGroup: boolean = meta.to
			? meta.to.domain.indexOf('conference') !== -1
			: false;

		const isInCache: boolean = this._load_msg_cache.some((item: any) => {
			return item.msgId === msgId;
		});
		if (isInCache) {
			continue;
		}
		if (this._load_msg_cache.length <= this.max_cache_length) {
			this._load_msg_cache.push({ msgId, from, to, isGroup });
		} else {
			this._load_msg_cache.shift();
			this._load_msg_cache.push({ msgId, from, to, isGroup });
		}
		switch (meta.ns) {
			case 0:
				MsgHandler.handleStatisticsMsg.call(this, meta);
				break;
			case 1:
				msgPromises.push(
					MsgHandler.handleChatMsg.call(
						this,
						meta,
						status,
						false,
						true,
						true
					)
				);
				break;
			case 2:
				MsgHandler.handleMucMsg.call(this, meta);
				break;
			case 3:
				MsgHandler.rosterMgr.handleRosterMsg.call(this, meta);
				break;
			case 4:
				this.registerConfrIQHandler &&
					this.registerConfrIQHandler(meta, status, this);
				break;
			case 5:
				MsgHandler.handleNotify.call(this, meta);
				break;
			default:
				logger.error('distributeMeta', meta);
				break;
		}
	}
	const msgs = await Promise.all(msgPromises);
	const receivedMsgs = msgs.filter((msg) => msg && msg.type !== 'cmd');
	receivedMsgs.length > 0 &&
		this.eventHandler?.dispatch('onMessage', receivedMsgs);
}

/**
 * @internal
 * 接收 metas 数组，循环解析
 */
function decodeMeta(this: any, metas: any[], status: number) {
	distributeMeta.call(this, metas, status);
}

// 解析下行 Unread
interface mainMessage {
	command: number;
	payload: [];
}

function startOfflineMessageSync(this: Connection, unreadMessages: any) {
	if (this._offlineMessagePullState === OfflineMessagePullState.SYNC_INIT) {
		this._offlineMessagePullState = OfflineMessagePullState.SYNC_START;
		//  use the first offline message queue
		this._offlineMessagePullQueue = unreadMessages.unread.reduce(
			(acc: any[], current: any) => {
				// removing duplicates queue
				if (!acc.find((item) => item.name === current.queue.name)) {
					acc.push(current.queue);
				}
				return acc;
			},
			[]
		);
		this.eventHandler?.dispatch('onOfflineMessageSyncStart');
	}
}

function processOfflineMessageQueue(this: Connection, incomingMessage: any) {
	if (this._offlineMessagePullState === OfflineMessagePullState.SYNC_START) {
		// Find the index of the queue that matches the incoming message's queue
		const queueIndex = this._offlineMessagePullQueue.findIndex(
			(queueItem) => {
				return queueItem.name === incomingMessage.queue.name;
			}
		);
		// If the queue is found and has been processed, remove it from the queue list
		if (queueIndex > -1) {
			this._offlineMessagePullQueue.splice(queueIndex, 1);
		}
		// If the queue list is empty, update the state to indicate the sync is finished
		if (this._offlineMessagePullQueue.length === 0) {
			this._offlineMessagePullState = OfflineMessagePullState.SYNC_FINISH;
			this.eventHandler?.dispatch('onOfflineMessageSyncFinish');
		}
	}
}

/**
 * @internal
 */
function decodeUnreadDL(this: Connection, result: mainMessage) {
	let CommUnreadDLMessage: any = this.root.lookup('easemob.pb.CommUnreadDL');
	CommUnreadDLMessage = CommUnreadDLMessage.decode(result.payload);
	const time = new Long(
		CommUnreadDLMessage.timestamp.low,
		CommUnreadDLMessage.timestamp.high,
		CommUnreadDLMessage.timestamp.unsigned
	).toString();
	this.expirationTime &&
		this.compareTokenExpireTime(Number(time), this.expirationTime);
	if (CommUnreadDLMessage.unread.length === 0) {
		sendRebuild.call(this);
		//发sync 同步statistic
	} else {
		startOfflineMessageSync.call(this, CommUnreadDLMessage);
		logger.debug('pull unread message', CommUnreadDLMessage.unread);

		for (let i = 0; i < CommUnreadDLMessage.unread.length; i++) {
			sendBackqueue.call(this, CommUnreadDLMessage.unread[i].queue);
		}
	}
	sendRebuild.call(this);
}

/**
 * @internal
 * 如何没有未读消息的处理 （发 Statistic）
 */
function _rebuild(this: any) {
	const emptyMessage: [] = [];
	//再次发送信息
	const StatisticsMessage: any = this.root.lookup(
		'easemob.pb.StatisticsBody'
	);
	let fourthMessage = StatisticsMessage.decode(emptyMessage);
	fourthMessage.operation = 0;
	// statisticsmessage.imTime=123;
	// statisticsmessage.chatTime=123;
	fourthMessage = StatisticsMessage.encode(fourthMessage).finish();

	const MetaMessage: any = this.root.lookup('easemob.pb.Meta');
	const thirdMessage = MetaMessage.decode(emptyMessage);
	thirdMessage.id = new Date().valueOf();
	thirdMessage.ns = 0;
	thirdMessage.payload = fourthMessage;
	// metamessage = MetaMessage.encode(metamessage).finish();
	const commsynculMessage: any = this.root.lookup('easemob.pb.CommSyncUL');
	let secondMessage: any = commsynculMessage.decode(emptyMessage);
	secondMessage.meta = thirdMessage;
	secondMessage = commsynculMessage.encode(secondMessage).finish();

	const mainMessage: any = this.root.lookup('easemob.pb.MSync');
	let firstMessage = mainMessage.decode(emptyMessage);
	firstMessage.version = this.version;
	firstMessage.encryptType = [0];
	firstMessage.command = 0;
	firstMessage.payload = secondMessage;
	firstMessage = mainMessage.encode(firstMessage).finish();
	return firstMessage;
}

/**
 * @internal
 * 构建 queue 拉取消息
 */
function _backqueue(this: any, backqueue: any): Buffer {
	const emptyMessage: [] = [];
	const commsynculMessage: any = this.root.lookup('easemob.pb.CommSyncUL');
	let secondMessage = commsynculMessage.decode(emptyMessage);
	secondMessage.queue = backqueue;
	secondMessage = commsynculMessage.encode(secondMessage).finish();
	const mainMessage: any = this.root.lookup('easemob.pb.MSync');
	let firstMessage = mainMessage.decode(emptyMessage);
	firstMessage.version = this.version;
	firstMessage.encryptType = this.encryptType;
	firstMessage.command = 0;
	firstMessage.payload = secondMessage;
	firstMessage = mainMessage.encode(firstMessage).finish();
	return firstMessage;
}

/** @internal */
function sendBackqueue(this: any, backqueue: any): void {
	logger.debug('sendBackqueue', backqueue);
	// let getBackqueueMsg = utils.flow([_backqueue.bind(this), base64transform])
	// let backqueueMsg = getBackqueueMsg(backqueue)
	// this.sock.send(backqueueMsg)
	const backqueueMsg = _backqueue.call(this, backqueue);
	sendMSync.call(this, backqueueMsg);
}

/**
 * @internal
 * 确定收到消息给erlang反馈//跟服务端确认是否为最后一条消息comm消息islast = true
 */
function _lastsession(this: any, nexkey: any, queue: any) {
	const emptyMessage: [] = [];
	const commSyncULMessage = this.root.lookup('easemob.pb.CommSyncUL') as any;
	let secondMessage = commSyncULMessage.decode(emptyMessage);
	secondMessage.queue = queue;
	secondMessage.key = new Long(
		nexkey.low,
		nexkey.high,
		nexkey.unsigned
	).toString();

	secondMessage = commSyncULMessage.encode(secondMessage).finish();
	const mSyncMessage: any = this.root.lookup('easemob.pb.MSync');

	let firstMessage = mSyncMessage.decode(emptyMessage);
	firstMessage.version = this.version;
	firstMessage.encryptType = this.encryptType;
	firstMessage.command = 0;
	firstMessage.payload = secondMessage;
	firstMessage = mSyncMessage.encode(firstMessage).finish();
	return firstMessage;
}

function registerUniPush(this: Connection) {
	// 存在推送插件并且未注册推送插件，则注册
	if (this.uniPush && this.isRegisterPush === false) {
		this.isRegisterPush = true;
		this.uniPush.onRegister((res: RegisterPushResult) => {
			logger.debug('push token onRegister', res);
			const {
				APNsCertificateName = '',
				HMSCertificateName = '',
				HONORCertificateName = '',
				MEIZUCertificateName = '',
				MICertificateName = '',
				OPPOCertificateName = '',
				VIVOCertificateName = '',
			} = this.uniPushConfig || {};
			switch (res.push_type) {
				case UniPushType.APNs:
					this.pushCertificateName = APNsCertificateName;
					break;
				case UniPushType.HMSPUSH:
					this.pushCertificateName = HMSCertificateName;
					break;
				case UniPushType.HONORPUSH:
					this.pushCertificateName = HONORCertificateName;
					break;
				case UniPushType.MEIZUPUSH:
					this.pushCertificateName = MEIZUCertificateName;
					break;
				case UniPushType.MIPUSH:
					this.pushCertificateName = MICertificateName;
					break;
				case UniPushType.OPPOPUSH:
					this.pushCertificateName = OPPOCertificateName;
					break;
				case UniPushType.VIVOPUSH:
					this.pushCertificateName = VIVOCertificateName;
					break;
				default:
					logger.error('unexpected push type', res.push_type);
					break;
			}
			if (this.pushCertificateName && res.push_token) {
				uploadPushTokenToServer
					.call(this, {
						deviceId: this.clientResource,
						deviceToken: res.push_token,
						notifierName: this.pushCertificateName,
					})
					.then(() => {
						logger.debug('uploadPushToken success');
					})
					.catch((e) => {
						logger.debug('uploadPushToken failed', e);
					});
			}
		});
	}
}

/** @internal  */
function onConnected(this: Connection) {
	this._offlineMessagePullState = OfflineMessagePullState.SYNC_INIT;
	this._offlineMessagePullQueue = [];
	this.times = 1;
	this.autoReconnectNumTotal = 0;
	this.onOpened && this.onOpened();
	registerUniPush.call(this);
	this.eventHandler?.dispatch('onConnected');
	reSendMsg.call(this);
	startHeartBeat.call(this);
	sendUnreadDeal.call(this);
	sendRebuild.call(this);
}

/** @internal */
function sendLastSession(this: any, nexkey: any, queue: any) {
	logger.debug('sendLastSession', nexkey); // 离线变成收到 清空离线
	// let getLastSessionMsg = utils.flow([_lastsession.bind(this), base64transform])
	// let lastSessionMsg = getLastSessionMsg(nexkey, queue)
	// this.sock.send(lastSessionMsg)
	const lastSessionMsg = _lastsession.call(this, nexkey, queue);
	sendMSync.call(this, lastSessionMsg);
}

// 处理收到的 Provision 消息
/** @internal */
function receiveProvision(this: Connection, result: any) {
	const provisionMessage: any = this.root.lookup('easemob.pb.Provision');
	const receiveProvisionResult = provisionMessage.decode(result.payload);

	if (this.context.jid) {
		this.context.jid.clientResource = receiveProvisionResult.resource;
	}
	this.clientResource = receiveProvisionResult.resource;
	this.provisionTimer && clearTimeout(this.provisionTimer);
	logger.debug('receiveProvisionResult', receiveProvisionResult.status);
	this._isLogging = false;
	if (receiveProvisionResult.status.errorCode === ProvisionErrorCode.OK) {
		this.disconnectedReason = undefined;
		if (receiveProvisionResult.authToken) {
			const { expires_in: expirationTime } = JSON.parse(
				receiveProvisionResult.authToken
			);
			if (!this.tokenExpiredTimer && !this.tokenWillExpireTimer) {
				const currentTime = Date.now();
				this.expirationTime = expirationTime;
				const expiresIn = this.expirationTime - currentTime;
				this.expiresIn = expiresIn < 0 ? 0 : expiresIn;
				this.tokenExpireTimeCountDown(this.expiresIn);
			}
		}
		this.reconnecting = false;
		this.logOut = false;
		// set ali socket readyState open
		if (this.needSetReadyState) {
			this.sock = {
				...this.sock,
				close: this.sock.close,
				send: this.sock.send,
				readyState: 1,
			};
		}
		// 如果引入了localCache插件
		if (this._localCache) {
			if (!this._localCache?.getInstance()) {
				this._localCache &&
					new this._localCache({
						user: this.user,
						dbName: `cache_${Math.abs(
							hashCode((this.appKey || this.appId) as string)
						)}_${this.user}`,
						version: DB_VERSION,
						onInit: async () => {
							logger.debug('localCache init success');
							onConnected.call(this);
						},
					});
			} else {
				// 重新连接成功执行回调
				onConnected.call(this);
			}
		} else {
			onConnected.call(this);
		}
		this.connectionResolve?.();
	} else {
		let err: ErrorEvent;
		const { reason, errorCode } = receiveProvisionResult.status;
		logger.debug('provision errorCode: ', errorCode);
		logger.debug('provision reason: ', reason);
		switch (errorCode) {
			case ProvisionErrorCode.FAIL:
				if (reason === 'Sorry, user register limit') {
					err = ErrorEvent.create({
						type: Code.MAX_LIMIT,
						message:
							'Sorry, the number of user registrations limit has been reached',
					});
				} else if (reason === 'Sorry, user register rate limit') {
					err = ErrorEvent.create({
						type: Code.MAX_LIMIT,
						message: 'Sorry, user register rate limit',
					});
				} else if (reason === 'Sorry, token expired') {
					err = ErrorEvent.create({
						type: Code.WEBIM_TOKEN_EXPIRED,
						message: 'Sorry, token expired',
					});
				} else if (
					reason ===
					'Sorry, token or password does not match login info'
				) {
					err = ErrorEvent.create({
						type: Code.WEBIM_CONNCTION_TOKEN_NOT_ASSIGN_ERROR,
						message: 'INVALID_TOKEN',
					});
				} else if (reason === 'Sorry, user not found') {
					err = ErrorEvent.create({
						type: Code.USER_NOT_FOUND,
						message: 'User not found',
					});
				} else {
					err = ErrorEvent.create({
						type: Code.WEBIM_CONNCTION_AUTH_ERROR,
						message: 'Auth failed',
					});
				}
				this.connectionReject?.(err);
				this.eventHandler?.dispatch('onError', err);
				break;
			case ProvisionErrorCode.WRONG_PARAMETER:
				err = ErrorEvent.create({
					type: Code.REQUEST_PARAMETER_ERROR,
					message: 'Invalid parameter',
				});
				this.connectionReject?.(err);
				this.eventHandler?.dispatch('onError', err);
				break;
			case ProvisionErrorCode.UNAUTHORIZED:
				err = ErrorEvent.create({
					type: Code.WEBIM_CONNCTION_AUTH_ERROR,
					message: 'Auth failed',
				});
				this.connectionReject?.(err);
				this.eventHandler?.dispatch('onError', err);
				break;
			case ProvisionErrorCode.IM_FORBIDDEN:
				err = ErrorEvent.create({
					type: Code.WEBIM_SERVER_SERVING_DISABLED,
					message: 'Server serving disabled.',
				});
				this.connectionReject?.(err);
				this.eventHandler?.dispatch('onError', err);
				break;
			case ProvisionErrorCode.PERMISSION_DENIED:
				if (reason === 'Sorry, the app month live count limit') {
					err = ErrorEvent.create({
						type: Code.MAX_LIMIT,
						message:
							'Sorry, the monthly active user limit for this app has been reached',
					});
				} else if (reason === 'Sorry, the app day live count limit') {
					err = ErrorEvent.create({
						type: Code.MAX_LIMIT,
						message:
							'Sorry, the daily active user limit for this app has been reached',
					});
				} else if (reason === 'Sorry, the app online count limit') {
					err = ErrorEvent.create({
						type: Code.MAX_LIMIT,
						message:
							'Sorry, the maximum number limit of online users for this app has been reached',
					});
				} else {
					err = ErrorEvent.create({
						type: Code.WEBIM_CONNCTION_AUTH_ERROR,
						message: 'Auth failed',
					});
				}
				this.connectionReject?.(err);
				this.eventHandler?.dispatch('onError', err);
				break;
			default:
				err = ErrorEvent.create({
					type: Code.WEBIM_CONNCTION_AUTH_ERROR,
					message: reason,
				});
				this.connectionReject?.(err);
				this.eventHandler?.dispatch('onError', err);
				break;
		}
		this.disconnectedReason = err;
	}
}

// 登录成功事重新发送未发送成功的消息
/** @internal */
function reSendMsg(this: any) {
	if (this.unMSyncSendMsgMap?.size > 0) {
		const keys = Array.from<string>(this.unMSyncSendMsgMap.keys());
		for (let i = 0; i < keys.length; i++) {
			const msg = this.unMSyncSendMsgMap.get(keys[i]);
			sendMSync.call(this, msg, keys[i]);
		}
		this.unMSyncSendMsgMap?.clear();
	}
}

/** @internal */
function sendRebuild(this: any) {
	// let getRebuildMsg = utils.flow([_rebuild.bind(this), base64transform])
	// let rebuildMsg: string = getRebuildMsg()
	// this.sock.send(rebuildMsg)
	const rebuildMsg = _rebuild.call(this);
	sendMSync.call(this, rebuildMsg);
}

/** @internal */
function sendUnreadDeal(this: any) {
	// let getUnreadDealMsg = utils.flow([generateUnreadDeal.bind(this), base64transform])
	// let unreadDealMsg: string = getUnreadDealMsg()
	// this.sock.send(unreadDealMsg)
	logger.debug('sendUnreadDeal');
	const unreadDealMsg = generateUnreadDeal.call(this);
	sendMSync.call(this, unreadDealMsg);
}

/** @internal */
function startHeartBeat(this: any): void {
	stopHeartBeat.call(this);
	this.heartBeatID = setInterval(() => {
		const now = Date.now();
		const secondsSinceLastHeartbeat =
			(now - (this.lastHeartbeat as number)) / 1000;
		if (secondsSinceLastHeartbeat >= 30) {
			logger.debug('send heart beat');
			sendUnreadDeal.call(this);
			this.lastHeartbeat = Date.now();
		}
	}, this.heartBeatWait);
}

/** @internal */
function stopHeartBeat(this: any): void {
	clearInterval(this.heartBeatID);
}

/** @internal */
function generateUnreadDeal(this: any): Buffer {
	const emptyMessage: [] = [];
	const MSyncMessage = this.root.lookup('easemob.pb.MSync');
	let firstMessage = MSyncMessage.decode(emptyMessage);
	firstMessage.version = this.version;
	firstMessage.encryptType = this.encryptType;
	firstMessage.command = 1;
	// logger.debug('⬆︎unreadDeal: ', firstMessage)
	firstMessage = MSyncMessage.encode(firstMessage).finish();
	return firstMessage;
}

/** @internal */
function isInQueue(
	_queues: { name: string }[],
	queue: { name: string }
): boolean {
	return _queues.some((item) => {
		return item.name === queue.name;
	});
}

// 处理下行的 Notice 消息
/** @internal */
function decodeNotice(this: any, result: mainMessage) {
	const Message: any = this.root.lookup('easemob.pb.CommNotice');
	const noticeMessage = Message.decode(result.payload);
	const isInQ = isInQueue(this._queues, noticeMessage.queue);
	logger.debug('receive notice', noticeMessage.queue, this._queues);

	if (isInQ) {
		return;
	}
	if (
		this.clientResource === noticeMessage.queue.clientResource &&
		noticeMessage.queue.name === this.context.userId
	) {
		// logger.debug('your self queue')
		return;
	}
	// this.qTimer && clearTimeout(this.qTimer);
	// this.qTimer = setTimeout(() => {
	// 	const q = noticeMessage.queue;
	// 	const isInQ = isInQueue(this._queues, q);
	// 	if (isInQ) {
	// 		sendBackqueue.call(this, q);
	// 		logger.debug('⬇q:', q);
	// 	}
	// }, 10000);
	this._queues.push(noticeMessage.queue);
	if (this._queues.length === 1) {
		sendBackqueue.call(this, noticeMessage.queue);
	}
}

/** @internal */
async function decodeMSync(this: any, message: any): Promise<mainMessage> {
	const envInfo = utils.getEnvInfo();
	const mainMessage = this.root.lookup('easemob.pb.MSync');
	let result;
	if (this.name === 'miniCore' || envInfo.platform === 'web') {
		try {
			const msg = new Uint8Array(message.data);
			result = mainMessage.decode(msg);
		} catch (error) {
			throw new Error('decode message fail.');
		}
		return result;
	} else if (envInfo.platform === 'zfb' || envInfo.platform === 'dd') {
		const getmessage = Base64.atob(message.data);
		const arr = [];
		for (let i = 0, j = getmessage.length; i < j; ++i) {
			arr.push(getmessage.charCodeAt(i));
		}
		const result: mainMessage = mainMessage.decode(arr);
		return result;
	} else {
		try {
			result = mainMessage.decode(message.data);
		} catch (error) {
			throw new Error('decode message fail.');
		}
		return result;
	}
}

/** @internal */
function distributeMSync(this: any, resultPromise: any) {
	this.lastHeartbeat = Date.now();
	this.probingTimer && clearTimeout(this.probingTimer);
	resultPromise.then((result: any) => {
		if (result) {
			switch (result.command) {
				case 0:
					decodeCommSyncDLMsg.call(this, result);
					break;
				case 1:
					decodeUnreadDL.call(this, result);
					break;
				case 2:
					decodeNotice.call(this, result);
					break;
				case 3:
					receiveProvision.call(this, result);
					break;
				default:
					logger.error(`unexpected msync command: ${result.command}`);
					// throw Error('unexpected msg')
					break;
			}
		} else {
			logger.error(`unexpected msync result: ${result}`);
		}
	});
}

/** @internal */
async function decodeCommSyncDLMsg(this: Connection, result: mainMessage) {
	let CommSyncDLMessage = this.root.lookup('easemob.pb.CommSyncDL') as any;
	CommSyncDLMessage = CommSyncDLMessage.decode(result.payload);
	const msgId = new Long(
		CommSyncDLMessage.serverId.low,
		CommSyncDLMessage.serverId.high,
		CommSyncDLMessage.serverId.unsigned
	).toString();
	const metaId = new Long(
		CommSyncDLMessage.metaId.low,
		CommSyncDLMessage.metaId.high,
		CommSyncDLMessage.metaId.unsigned
	).toString();
	// 如果存在本地消息id
	if (Number(metaId) > 0) {
		clearTimeout(this._msgHash[metaId]?.messageTimer);
		// 判断下行消息状态
		if (CommSyncDLMessage.status) {
			// 消息状态正常
			if (CommSyncDLMessage.status.errorCode === 0) {
				// 消息发送成功
				const msgInfo: {
					chatType: ChatType;
					to: string | number;
					type: MessageType;
					isHandleChatroom: boolean;
				} = this._msgHash[metaId];
				// 加入、退出聊天室回调
				if (msgInfo?.isHandleChatroom) {
					try {
						const isJoin =
							this._msgHash[metaId]?.operation === 'join';
						// 如果是加入聊天室
						if (isJoin) {
							let chatRoomInfo: JoinChatRoomInfo =
								{} as JoinChatRoomInfo;
							try {
								// 如果 metas 不为空
								if (CommSyncDLMessage.metas.length > 0) {
									// 解析聊天室信息
									const messageBodyMessage =
										this.root.lookup('easemob.pb.MUCBody');
									const thirdMessage =
										messageBodyMessage.decode(
											CommSyncDLMessage.metas[0].payload
										);
									const dt = JSON.parse(
										thirdMessage.status.chatroomInfo || '{}'
									)[this.user];
									// 如果聊天室信息不为空
									if (dt) {
										chatRoomInfo = {
											isAllMembersMuted: dt.is_all_mute,
											createTimestamp:
												dt.create_timestamp,
											isInAllowlist: dt.is_in_white_list,
											memberCount: dt.member_count,
											muteExpireTimestamp:
												dt.mute_duration
													? dt.mute_duration
													: -1,
										};
									}
								}
							} catch (error) {
								logger.error(
									'decode chat room info error',
									error
								);
							}
							this._msgHash[metaId]?.resolve instanceof
								Function &&
								this._msgHash[metaId].resolve({
									type: 0,
									data: {
										action: 'apply',
										id: this._msgHash[metaId].roomId,
										result: true,
										user: this.context.userId,
										info: chatRoomInfo,
									},
								});
							this._msgHash[metaId]?.success instanceof
								Function &&
								this._msgHash[metaId].success({
									type: 0,
									data: {
										action: 'apply',
										id: this._msgHash[metaId].roomId,
										result: true,
										user: this.context.userId,
										info: chatRoomInfo,
									},
								});
						}
						this._msgHash[metaId]?.resolve instanceof Function &&
							!isJoin &&
							this._msgHash[metaId].resolve({
								type: 0,
								data: {
									result: true,
								},
							});
						this._msgHash[metaId]?.success instanceof Function &&
							!isJoin &&
							this._msgHash[metaId].success({
								type: 0,
								data: {
									result: true,
								},
							});
						if (CHAT_ROOM_OPT_REPORTS.has(metaId)) {
							const { rpt, requestName } =
								CHAT_ROOM_OPT_REPORTS.get(metaId);
							rpt({
								isEndApi: true,
								data: {
									isSuccess: 1,
									requestName,
									requestMethod: 'WEBSOCKET',
									requestUrl: this.url,
									code: WSS_CODE.success,
								},
							});
							CHAT_ROOM_OPT_REPORTS.delete(metaId);
						}
					} catch (e) {
						if (CHAT_ROOM_OPT_REPORTS.has(metaId)) {
							const { rpt, requestName } =
								CHAT_ROOM_OPT_REPORTS.get(metaId);
							rpt({
								isEndApi: true,
								data: {
									isSuccess: 0,
									requestName,
									requestMethod: 'WEBSOCKET',
									requestUrl: this.url,
									code: WSS_CODE.failed,
									codeDesc:
										'when executing success function error',
								},
							});
							CHAT_ROOM_OPT_REPORTS.delete(metaId);
						}

						const error = ErrorEvent.create({
							type: Code.WEBIM_CONNCTION_CALLBACK_INNER_ERROR,
							message: 'when executing success function error',
							data: e,
						});
						this.onError && this.onError(error);
						this.eventHandler?.dispatch('onError', error);
					}
					delete this._msgHash[metaId];
				}
				// 普通消息
				if (msgInfo && !msgInfo.isHandleChatroom) {
					let modifiedInfo = null;
					let modifiedMsgFrom = '';
					let modifiedMsgTime = 0;
					let modifiedMsgChatType = '' as ChatType;
					let message: any = null;

					if (this._msgHash[metaId].thirdMessage) {
						this._msgHash[metaId].thirdMessage.id =
							CommSyncDLMessage.serverId;
						this._msgHash[metaId].thirdMessage.timestamp =
							CommSyncDLMessage.timestamp;
						// 格式化本地发送的消息
						message = await MsgHandler.handleChatMsg.call(
							this,
							this._msgHash[metaId].thirdMessage,
							CommSyncDLMessage.status,
							true,
							true
						);
					}

					// 消息回执返回的metas不为空
					if (CommSyncDLMessage.metas.length !== 0) {
						try {
							const meta = CommSyncDLMessage.metas[0];
							const status = CommSyncDLMessage.status;
							switch (meta.ns) {
								case 1:
									if (this.useReplacedMessageContents) {
										message =
											await MsgHandler.handleChatMsg.call(
												this,
												meta,
												status,
												true,
												true
											);
										// update local db msg
										const dbMsg = await this._localCache
											?.getInstance()
											?.getMessageByServerMsgId(metaId);
										if (dbMsg) {
											await this._localCache
												?.getInstance()
												?.putMessageToDB({
													...dbMsg,
													...message,
													id: metaId,
												});
										}
									}
									break;
								case 5:
									// eslint-disable-next-line no-case-declarations
									const res = utils.parseNotify(
										CommSyncDLMessage.metas[0].payload
									);
									// 编辑消息回调
									if (res.edit_msg) {
										const {
											count,
											operator,
											edit_time,
											sender,
											send_time,
											chat_type
										} = res.edit_msg;
										modifiedInfo = {
											operationTime: edit_time,
											operatorId: operator,
											operationCount: count,
										};
										modifiedMsgFrom = sender;
										modifiedMsgTime = Number(send_time);
										modifiedMsgChatType = MsyncEditMsgChatType[chat_type as keyof typeof MsyncEditMsgChatType];
									}
									break;
								default:
									logger.error(
										'decode local meta error',
										meta
									);
									break;
							}
						} catch (e) {
							const error = ErrorEvent.create({
								type: Code.WEBIM_LOAD_MSG_ERROR,
								message: 'decode local meta message error',
								data: e,
							});
							this.onError && this.onError(error);
							this.eventHandler?.dispatch('onError', error);
						}
					}
					if (SEND_MSG_REPORTS.has(metaId)) {
						SEND_MSG_REPORTS.get(metaId).rpt({
							isEndApi: true,
							data: {
								isSuccess: 1,
								requestMethod: 'WEBSOCKET',
								requestUrl: this.url,
								code: WSS_CODE.success,
								msgId,
							},
						});
						SEND_MSG_REPORTS.delete(metaId);
					}
					try {
						const result: any = {
							localMsgId: metaId,
							serverMsgId: msgId,
						};
						// 表示为编辑消息
						if (modifiedInfo) {
							const { editMessageId, ext, customExts, customEvent } = this._msgHash[metaId];
							message.modifiedInfo = modifiedInfo;
							message.from = modifiedMsgFrom;
							message.time = Number(modifiedMsgTime);
							message.chatType = modifiedMsgChatType;
							message.id = editMessageId;
							result.message = message;
							this._localCache
								?.getInstance()
								?.getMessageByServerMsgId(editMessageId)
								?.then((dbMsg: any) => {
									if (dbMsg) {
										if (dbMsg.type === 'txt') {
											this._localCache
												?.getInstance()
												?.putMessageToDB({
													...dbMsg,
													msg: message.msg,
													modifiedInfo: message.modifiedInfo,
													translations: message.translations,
													ext: ext ? message.ext : dbMsg.ext,
												});
										} else if (dbMsg.type === 'custom') {
											this._localCache
												?.getInstance()
												?.putMessageToDB({
													...dbMsg,
													ext: ext ? message.ext : dbMsg.ext,
													customExts: customExts,
													customEvent: customEvent,
													modifiedInfo: message.modifiedInfo,
												});
										} else {
											this._localCache
												?.getInstance()
												?.putMessageToDB({
													...dbMsg,
													ext: ext ? message.ext : dbMsg.ext,
													modifiedInfo: message.modifiedInfo,
												});
										}
									}
								});
						}

						if (message) {
							result.message = message;
						}
						// 消息发送成功, 更新本地消息状态
						await this._localCache
							?.getInstance()
							?.updateLocalMessage(metaId, {
								serverMsgId: msgId,
								status: MessageStatus.SUCCESS,
							});
						this._msgHash[metaId]?.success instanceof Function &&
							(await this._msgHash[metaId].success(
								metaId,
								msgId
							));
						this._msgHash[metaId]?.resolve instanceof Function &&
							this._msgHash[metaId].resolve(result);
					} catch (e) {
						const error = ErrorEvent.create({
							type: Code.WEBIM_CONNCTION_CALLBACK_INNER_ERROR,
							message: 'when executing success function error',
							data: e,
						});
						this.onError && this.onError(error);
						this.eventHandler?.dispatch('onError', error);
					}
					this.onReceivedMessage &&
						this.onReceivedMessage({
							id: metaId,
							mid: msgId,
							to: this._msgHash[metaId].to,
							time: 0,
						});

					this.eventHandler?.dispatch('onReceivedMessage', {
						id: metaId,
						mid: msgId,
						to: this._msgHash[metaId].to,
					});
					delete this._msgHash[metaId];
				}
			} else if (CommSyncDLMessage.status.errorCode === 15) {
				// 在群组中被禁言，消息不会发送给其他人
				this._msgHash[metaId]?.fail instanceof Function &&
					this._msgHash[metaId].fail({
						type: Code.SERVICE_NOT_ALLOW_MESSAGING_MUTE,
						reason: 'you were muted',
					});
				// @since 4.0
				this._msgHash[metaId].reject instanceof Function &&
					this._msgHash[metaId].reject({
						type: Code.SERVICE_NOT_ALLOW_MESSAGING_MUTE,
						reason: 'you were muted',
					});
				this._localCache?.getInstance()?.updateLocalMessage(metaId, {
					serverMsgId: msgId,
					status: MessageStatus.FAIL,
				});
			} else if (CommSyncDLMessage.status.errorCode === 1) {
				let type: number;
				switch (CommSyncDLMessage.status.reason) {
					case 'blocked':
						type = Code.PERMISSION_DENIED;
						break;
					case 'group not found':
						type = Code.GROUP_NOT_EXIST;
						break;
					case 'not in group or chatroom':
						type = Code.GROUP_NOT_JOINED;
						break;
					case 'exceed recall time limit':
						type = Code.MESSAGE_RECALL_TIME_LIMIT;
						break;
					case 'message recall disabled':
						type = Code.SERVICE_NOT_ENABLED;
						break;
					case 'not in group or chatroom white list':
						type = Code.SERVICE_NOT_ALLOW_MESSAGING;
						break;
					case 'nonroster':
						type = Code.USER_NOT_FRIEND;
						CommSyncDLMessage.status.reason = 'not contact';
						break;
					case 'group is disabled':
						type = Code.GROUP_IS_DISABLED;
						CommSyncDLMessage.status.reason = 'group is disabled';
						break;
					case 'limit directed users':
						type = Code.MAX_LIMIT;
						break;
					case 'Sorry, edit limit reached':
						type = Code.MAX_LIMIT;
						CommSyncDLMessage.status.reason =
							'Modify message limit reached';
						break;
					case 'Sorry, message does not exist':
						type = Code.MODIFY_MESSAGE_NOT_EXIST;
						CommSyncDLMessage.status.reason =
							'The message does not exist.';
						break;
					case 'Sorry, You do not have permission':
						type = Code.PERMISSION_DENIED;
						CommSyncDLMessage.status.reason =
							'You do not have the modified permission.';
						break;
					case 'Sorry, format is incorrect':
						type = Code.MODIFY_MESSAGE_FORMAT_ERROR;
						CommSyncDLMessage.status.reason =
							'The modify messaged format error.';
						break;
					case 'Sorry, edit is not available':
						type = Code.SERVICE_NOT_ENABLED;
						CommSyncDLMessage.status.reason =
							'The message modify function is not activated.';
						break;
					case 'Sorry, edit fail':
						type = Code.MODIFY_MESSAGE_FAILED;
						CommSyncDLMessage.status.reason =
							'Modify message failed.';
						break;

					default:
						if (
							CommSyncDLMessage.status.reason.includes('grpID') &&
							CommSyncDLMessage.status.reason.includes(
								'does not exist!'
							)
						) {
							type = Code.CHATROOM_NOT_EXIST;
							CommSyncDLMessage.status.reason =
								'The chat room dose not exist.';
						} else if (
							CommSyncDLMessage.status.reason.includes(
								'username'
							) &&
							CommSyncDLMessage.status.reason.includes(
								// eslint-disable-next-line quotes
								"doesn't exist!"
							)
						) {
							type = Code.USER_NOT_FOUND;
						} else if (
							CommSyncDLMessage.status.reason ===
							'group member list is full!'
						) {
							type = Code.CHATROOM_MEMBERS_FULL;
						} else if (
							CommSyncDLMessage.status.reason.includes(
								'can not join this group'
							) &&
							CommSyncDLMessage.status.reason.includes(
								'is in the blacklist'
							)
						) {
							type = Code.PERMISSION_DENIED;
							CommSyncDLMessage.status.reason =
								'permission denied';
						} else if (
							CommSyncDLMessage.status.reason ===
							'can not operate this group, reason: group is disabled'
						) {
							type = Code.GROUP_IS_DISABLED;
						} else if (
							CommSyncDLMessage.status.reason.includes(
								'moderation'
							)
						) {
							type = Code.MESSAGE_MODERATION_BLOCKED;
						} else if (
							CommSyncDLMessage.status.reason.includes(
								'group ID illegal, please check it'
							)
						) {
							type = Code.REQUEST_PARAMETER_ERROR;
							CommSyncDLMessage.status.reason =
								'Invalid parameter';
						} else if (
							CommSyncDLMessage.status.reason.includes(
								'has create too many chatrooms'
							) ||
							CommSyncDLMessage.status.reason.includes(
								'has joined too many chatrooms'
							)
						) {
							type = Code.MAX_LIMIT;
						} else if (
							CommSyncDLMessage.status.reason.includes(
								'auto create failed'
							)
						) {
							type = Code.SERVER_BUSY;
						} else {
							type = Code.SERVER_UNKNOWN_ERROR;
						}
						break;
				}
				if (this._msgHash[metaId]) {
					if (this._msgHash[metaId]?.isHandleChatroom) {
						//加入、退出聊天室失败回调
						const sendMsgError = ErrorEvent.create({
							type: type,
							message: CommSyncDLMessage.status.reason || '',
							data: '',
						});
						this._msgHash[metaId].reject instanceof Function &&
							this._msgHash[metaId].reject(sendMsgError);
						this._msgHash[metaId].error instanceof Function &&
							this._msgHash[metaId].error(sendMsgError);
						if (CHAT_ROOM_OPT_REPORTS.has(metaId)) {
							let code = WSS_CODE.failed;
							if (type === Code.CHATROOM_NOT_EXIST) {
								code = WSS_CODE.notFound;
							} else if (type === Code.CHATROOM_MEMBERS_FULL) {
								code = WSS_CODE.reachLimit;
							}
							const { rpt, requestName } =
								CHAT_ROOM_OPT_REPORTS.get(metaId);
							rpt({
								isEndApi: true,
								data: {
									isSuccess: 0,
									requestName,
									requestMethod: 'WEBSOCKET',
									requestUrl: this.url,
									code: code,
									codeDesc: CommSyncDLMessage.status.reason,
								},
							});
							CHAT_ROOM_OPT_REPORTS.delete(metaId);
						}
					} else {
						const sendMsgError = ErrorEvent.create({
							type: type,
							message: CommSyncDLMessage.status.reason || '',
							data: {
								id: metaId,
								mid: msgId,
							},
						});
						this._msgHash[metaId].reject instanceof Function &&
							this._msgHash[metaId].reject(sendMsgError);
						this._msgHash[metaId].fail instanceof Function &&
							this._msgHash[metaId].fail({
								type: type,
								reason: CommSyncDLMessage.status.reason
									? CommSyncDLMessage.status.reason
									: '',
								data: {
									id: metaId,
									mid: msgId,
								},
							});
						this._localCache
							?.getInstance()
							?.updateLocalMessage(metaId, {
								serverMsgId: msgId,
								status: MessageStatus.FAIL,
							});
					}
					delete this._msgHash[metaId];
				}
			} else if (CommSyncDLMessage.status.errorCode === 7) {
				if (
					CommSyncDLMessage.status.reason === 'sensitive words' &&
					this._msgHash[metaId]
				) {
					const sendMsgError = ErrorEvent.create({
						type: Code.MESSAGE_INCLUDE_ILLEGAL_CONTENT,
						message: 'sensitive words',
						data: {
							id: metaId,
							mid: msgId,
						},
					});
					this._msgHash[metaId].reject instanceof Function &&
						this._msgHash[metaId].reject(sendMsgError);
					this._msgHash[metaId].fail instanceof Function &&
						this._msgHash[metaId].fail({
							type: Code.MESSAGE_INCLUDE_ILLEGAL_CONTENT,
							data: {
								id: metaId,
								mid: msgId,
								reason: 'sensitive words',
							},
						});
					this._localCache
						?.getInstance()
						?.updateLocalMessage(metaId, {
							serverMsgId: msgId,
							status: MessageStatus.FAIL,
						});
				} else if (
					CommSyncDLMessage.status.reason ===
						'blocked by mod_antispam' &&
					this._msgHash[metaId]
				) {
					const sendMsgError = ErrorEvent.create({
						type: Code.MESSAGE_INCLUDE_ILLEGAL_CONTENT,
						message: 'blocked by mod_antispam',
						data: {
							id: metaId,
							mid: msgId,
						},
					});
					this._msgHash[metaId].reject instanceof Function &&
						this._msgHash[metaId].reject(sendMsgError);
					this._msgHash[metaId].fail instanceof Function &&
						this._msgHash[metaId].fail({
							type: Code.MESSAGE_INCLUDE_ILLEGAL_CONTENT,
							data: {
								id: metaId,
								mid: msgId,
								reason: 'blocked by mod_antispam',
							},
						});
					this._localCache
						?.getInstance()
						?.updateLocalMessage(metaId, {
							serverMsgId: msgId,
							status: MessageStatus.FAIL,
						});
				} else if (
					CommSyncDLMessage.status.reason === 'user is mute' &&
					this._msgHash[metaId]
				) {
					const sendMsgError = ErrorEvent.create({
						type: Code.USER_MUTED_BY_ADMIN,
						message: 'user is mute',
						data: {
							id: metaId,
							mid: msgId,
						},
					});
					this._msgHash[metaId].reject instanceof Function &&
						this._msgHash[metaId].reject(sendMsgError);
					this._msgHash[metaId].fail instanceof Function &&
						this._msgHash[metaId].fail(sendMsgError);
					this._localCache
						?.getInstance()
						?.updateLocalMessage(metaId, {
							serverMsgId: msgId,
							status: MessageStatus.FAIL,
						});
				} else if (
					CommSyncDLMessage.status.reason === 'traffic limit' &&
					this._msgHash[metaId]
				) {
					const sendMsgError = ErrorEvent.create({
						type: Code.MESSAGE_CURRENT_LIMITING,
						message: 'traffic limit',
						data: {
							id: metaId,
							mid: msgId,
						},
					});
					this._msgHash[metaId].reject instanceof Function &&
						this._msgHash[metaId].reject(sendMsgError);
					this._msgHash[metaId].fail instanceof Function &&
						this._msgHash[metaId].fail(sendMsgError);
					this._localCache
						?.getInstance()
						?.updateLocalMessage(metaId, {
							serverMsgId: msgId,
							status: MessageStatus.FAIL,
						});
				} else if (
					CommSyncDLMessage.status.reason ===
						'Sorry, data is too large' &&
					this._msgHash[metaId]
				) {
					const sendMsgError = ErrorEvent.create({
						type: Code.MESSAGE_SIZE_LIMIT,
						message: 'Sorry, data is too large',
						data: {
							id: metaId,
							mid: msgId,
						},
					});
					this._msgHash[metaId].reject instanceof Function &&
						this._msgHash[metaId].reject(sendMsgError);
					this._msgHash[metaId].fail instanceof Function &&
						this._msgHash[metaId].fail(sendMsgError);
					this._localCache
						?.getInstance()
						?.updateLocalMessage(metaId, {
							serverMsgId: msgId,
							status: MessageStatus.FAIL,
						});
				}
			} else if (CommSyncDLMessage.status.errorCode === 19) {
				if (this._msgHash[metaId]) {
					// 上报发送消息失败
					if (SEND_MSG_REPORTS.has(metaId)) {
						SEND_MSG_REPORTS.get(metaId).rpt({
							isEndApi: true,
							data: {
								isSuccess: 0,
								requestMethod: 'WEBSOCKET',
								requestUrl: this.url,
								code: Code.MESSAGE_EXTERNAL_LOGIC_BLOCKED,
								codeDesc: CommSyncDLMessage.status.reason || '',
								msgId,
							},
						});
						SEND_MSG_REPORTS.delete(metaId);
					}
					const sendMsgError = ErrorEvent.create({
						type: Code.MESSAGE_EXTERNAL_LOGIC_BLOCKED,
						message: CommSyncDLMessage.status.reason || '',
						data: {
							id: metaId,
							mid: msgId,
						},
					});
					this._msgHash[metaId].reject instanceof Function &&
						this._msgHash[metaId].reject(sendMsgError);
					this._msgHash[metaId].fail instanceof Function &&
						this._msgHash[metaId].fail({
							type: Code.MESSAGE_EXTERNAL_LOGIC_BLOCKED,
							data: {
								id: metaId,
								mid: msgId,
								reason: CommSyncDLMessage.status.reason,
							},
						});
					this._localCache
						?.getInstance()
						?.updateLocalMessage(metaId, {
							serverMsgId: msgId,
							status: MessageStatus.FAIL,
						});
				}
			} else {
				if (this._msgHash[metaId]) {
					// 上报发送消息失败
					if (SEND_MSG_REPORTS.has(metaId)) {
						SEND_MSG_REPORTS.get(metaId).rpt({
							isEndApi: true,
							data: {
								isSuccess: 0,
								requestMethod: 'WEBSOCKET',
								requestUrl: this.url,
								code: Code.WEBIM_LOAD_MSG_ERROR,
								codeDesc:
									CommSyncDLMessage.status?.reason || '',
								msgId,
							},
						});
						SEND_MSG_REPORTS.delete(metaId);
					}
					try {
						const sendMsgError = ErrorEvent.create({
							type: Code.WEBIM_LOAD_MSG_ERROR,
							message: CommSyncDLMessage.status?.reason || '',
							data: {
								id: metaId,
								mid: msgId,
								reason:
									CommSyncDLMessage.status &&
									CommSyncDLMessage.status.reason,
							},
						});
						this._msgHash[metaId].reject instanceof Function &&
							this._msgHash[metaId].reject(sendMsgError);
						this._localCache
							?.getInstance()
							?.updateLocalMessage(metaId, {
								serverMsgId: msgId,
								status: MessageStatus.FAIL,
							});
						this._msgHash[metaId].fail instanceof Function &&
							this._msgHash[metaId].fail({
								type: Code.WEBIM_LOAD_MSG_ERROR,
								data: {
									errorCode:
										CommSyncDLMessage.status &&
										CommSyncDLMessage.status.errorCode,
									reason:
										CommSyncDLMessage.status &&
										CommSyncDLMessage.status.reason,
								},
							});
					} catch (e) {
						const error = ErrorEvent.create({
							type: Code.WEBIM_CONNCTION_CALLBACK_INNER_ERROR,
							message: 'when executing fail function error',
							data: e,
						});
						this.onError && this.onError(error);
						this.eventHandler?.dispatch('onError', error);
					}
					delete this._msgHash[metaId];
				} else {
					if (SEND_MSG_REPORTS.has(metaId)) {
						SEND_MSG_REPORTS.get(metaId).rpt({
							isEndApi: true,
							data: {
								isSuccess: 0,
								requestMethod: 'WEBSOCKET',
								requestUrl: this.url,
								code: Code.WEBIM_CONNCTION_CALLBACK_INNER_ERROR,
								codeDesc: 'on message error',
								msgId,
							},
						});
						SEND_MSG_REPORTS.delete(metaId);
					}
					const error = ErrorEvent.create({
						type: Code.WEBIM_CONNCTION_CALLBACK_INNER_ERROR,
						message: 'on message error',
					});
					this.onError && this.onError(error);
					this.eventHandler?.dispatch('onError', error);
				}
			}
		}
		return;
	}
	// 不存在metaId, 表示为收到的消息,分发事件
	if (CommSyncDLMessage.metas && CommSyncDLMessage.metas.length !== 0) {
		try {
			decodeMeta.call(
				this,
				CommSyncDLMessage.metas,
				CommSyncDLMessage.status
			);
		} catch (err) {
			const error = ErrorEvent.create({
				type: Code.WEBIM_LOAD_MSG_ERROR,
				message: 'decode message error',
				data: err,
			});
			logger.error('decode message error', err);
			this.onError && this.onError(error);
			this.eventHandler?.dispatch('onError', error);
		} finally {
			if (CommSyncDLMessage.isLast) {
				logger.debug(
					'metas & CommSyncDLMessage.isLast',
					CommSyncDLMessage.isLast
				);
				let queuesIndex = -1;
				const isInQ = this._queues.some(
					(item: any, index: number): boolean => {
						if (item.name === CommSyncDLMessage.name) {
							queuesIndex = index;
							return true;
						}
						return false;
					}
				);
				if (isInQ && queuesIndex > 0) {
					this._queues.splice(queuesIndex, 1);
				}
				if (this._queues.length > 0) {
					sendBackqueue.call(this, this._queues[0]);
					// this.qTimer && clearTimeout(this.qTimer);
				}

				processOfflineMessageQueue.call(this, CommSyncDLMessage);
				// return; // 大型聊天室会把消息和isLast=true一同下发下来
			} else {
				if (CommSyncDLMessage.nextKey) {
					const nexKey = new Long(
						CommSyncDLMessage.nextKey.low,
						CommSyncDLMessage.nextKey.high,
						CommSyncDLMessage.nextKey.unsigned
					).toString();
					logger.debug(
						'nexKey:',
						nexKey,
						'this.nextKey:',
						this.nexKey
					);
					if (nexKey !== this.nexKey) {
						this.nexKey = nexKey;
						sendLastSession.call(
							this,
							CommSyncDLMessage.nextKey,
							CommSyncDLMessage.queue
						);
					}
				}
			}
		}
	} else if (CommSyncDLMessage.isLast) {
		// 两个 CommSyncDLMessage.isLast 判断
		logger.debug('CommSyncDLMessage.isLast', CommSyncDLMessage.isLast);
		let queuesIndex = -1;
		const isInQ = this._queues.some((item: any, index: number): boolean => {
			if (item.name === CommSyncDLMessage.queue.name) {
				queuesIndex = index;
				return true;
			}
			return false;
		});
		if (isInQ) {
			this._queues.splice(queuesIndex, 1);
		}
		if (this._queues.length > 0) {
			sendBackqueue.call(this, this._queues[0]);
			// this.qTimer && clearTimeout(this.qTimer);
		}

		processOfflineMessageQueue.call(this, CommSyncDLMessage);
	} else if (CommSyncDLMessage.nextKey) {
		const nexKey = new Long(
			CommSyncDLMessage.nextKey.low,
			CommSyncDLMessage.nextKey.high,
			CommSyncDLMessage.nextKey.unsigned
		).toString();
		logger.debug('nexKey:', nexKey, 'this.nextKey:', this.nexKey);
		if (nexKey !== this.nexKey) {
			this.nexKey = nexKey;
			sendLastSession.call(
				this,
				CommSyncDLMessage.nextKey,
				CommSyncDLMessage.queue
			);
		}
	}
}

interface msgExt {
	[key: string]: any;
}

/** @internal */
function encodeKeyValueMsg(this: any, msg: msgExt): msgExt[] {
	const emptyMessage: [] = [];
	const keyValueBody = this.root.lookup('easemob.pb.KeyValue');
	const keyValueBodyArr = [];
	for (const key in msg) {
		const keyValueBodyMessage = keyValueBody.decode(emptyMessage);
		keyValueBodyMessage.key = key;
		if (typeof msg[key] === 'undefined') {
			continue;
		} else if (typeof msg[key] === 'object') {
			keyValueBodyMessage.type = 8;
			keyValueBodyMessage.stringValue = JSON.stringify(msg[key]);
		} else if (typeof msg[key] === 'string') {
			keyValueBodyMessage.type = 7;
			keyValueBodyMessage.stringValue = msg[key];
		} else if (typeof msg[key] === 'boolean') {
			keyValueBodyMessage.type = 1;
			keyValueBodyMessage.varintValue = msg[key] === true ? 1 : 0;
		} else if (Number.isInteger(msg[key])) {
			keyValueBodyMessage.type = 2;
			keyValueBodyMessage.varintValue = msg[key];
		} else if (
			typeof msg[key] === 'bigint' ||
			typeof msg[key] === 'symbol' ||
			typeof msg[key] === 'function' ||
			Number.isNaN(msg[key])
		) {
			keyValueBodyMessage.type = 7;
			keyValueBodyMessage.stringValue = msg[key].toString();
		} else if (
			typeof msg[key] === 'number' &&
			!Number.isInteger(msg[key])
		) {
			keyValueBodyMessage.type = 6;
			keyValueBodyMessage.doubleValue = msg[key];
		} else {
			continue;
		}
		keyValueBodyArr.push(keyValueBodyMessage);
	}
	return keyValueBodyArr;
}

interface MessageMeta {
	translations?: object;
	thread?: object;
}
function buildMessageMeta(messageOption: any): string {
	const meta: MessageMeta = {};
	if ('translations' in messageOption) {
		meta.translations = messageOption.translations;
	}
	//'isChatThread:true' is need  when user send or recall a message of thread;
	if ('isChatThread' in messageOption && messageOption['isChatThread']) {
		meta.thread = {};
	}
	return Object.keys(meta).length > 0 ? JSON.stringify(meta) : '';
}

function buildMessageExt(messageOption: any): MetaExts {
	const priority: number =
		messageOption.priority === 'high'
			? 0
			: messageOption.priority === 'low'
				? 2
				: 1;
	return {
		chatroom_msg_tag: priority,
	};
}

// TODO messageOption 类型  需解决兼容  校验消息
function encodeChatMsg(this: any, messageOption: any): any {
	const emptyMessage: [] = [];
	if (!this.root) {
		messageOption.fail &&
			messageOption.fail({
				type: Code.WEBIM_CONNCTION_CLIENT_OFFLINE,
				message: 'Not logged in',
			});
		return;
	}

	const contentMessage = this.root.lookup('easemob.pb.MessageBody.Content');
	const fifthMessage = contentMessage.decode(emptyMessage);
	// let keyValueBody = this.root.lookup("easemob.pb.KeyValue");

	let chatType: ChatType;
	if (
		(messageOption.group ||
			messageOption?.chatType?.toLowerCase() === 'groupchat') &&
		!messageOption.roomType
	) {
		chatType = 'groupChat';
	} else if (
		(messageOption.group && messageOption.roomType) ||
		messageOption?.chatType?.toLowerCase() === 'chatroom'
	) {
		chatType = 'chatRoom';
	} else {
		chatType = 'singleChat';
	}
	switch (messageOption.type) {
		case 'txt':
			fifthMessage.type = 0;
			fifthMessage.text = messageOption.msg;
			break;
		case 'img':
			fifthMessage.type = 1;
			if (messageOption.body) {
				fifthMessage.displayName = messageOption.body.filename;
				fifthMessage.remotePath = messageOption.body.url;
				fifthMessage.secretKey = messageOption.body.secret;
				fifthMessage.fileLength = messageOption.body.file_length;
				fifthMessage.size = messageOption.body.size;
				fifthMessage.thumbnailDisplayName = messageOption.body.filename;
			} else if (messageOption.file) {
				fifthMessage.displayName = messageOption.file.filename;
				fifthMessage.remotePath = messageOption.file.url;
				fifthMessage.secretKey = messageOption.file.secret;
				fifthMessage.fileLength = messageOption.file.file_length;
				fifthMessage.size = messageOption.file.size;
				fifthMessage.thumbnailDisplayName = messageOption.file.filename;
			} else {
				// url message
				fifthMessage.displayName = messageOption.filename;
				fifthMessage.remotePath = messageOption.url;
				fifthMessage.secretKey = messageOption.secret;
				fifthMessage.fileLength = messageOption.file_length;
				fifthMessage.size = messageOption.size;
				fifthMessage.thumbnailDisplayName = messageOption.filename;
			}
			if (messageOption.isBuildCombinedMsg) {
				//编码已经收到的一条图片消息
				fifthMessage.remotePath = messageOption.url;
				fifthMessage.secretKey = messageOption.secret;

				fifthMessage.size = {
					height: messageOption.height,
					width: messageOption.width,
				};
			}

			break;
		case 'video':
			fifthMessage.type = 2;
			if (messageOption.body) {
				fifthMessage.displayName = messageOption.body.filename;
				fifthMessage.remotePath = messageOption.body.url;
				fifthMessage.secretKey = messageOption.body.secret;
				fifthMessage.fileLength = messageOption.body.file_length;
				fifthMessage.duration = messageOption.body.length;
				fifthMessage.thumbnailDisplayName = messageOption.body.filename;
				fifthMessage.thumbnailRemotePath = this.useOwnUploadFun
					? ''
					: `${messageOption.body.url}?vframe=true`;
				fifthMessage.thumbnailSecretKey = this.useOwnUploadFun
					? ''
					: messageOption.body.secret;
			} else if (messageOption.isBuildCombinedMsg) {
				fifthMessage.displayName = messageOption.filename;
				fifthMessage.remotePath = messageOption.url;
				fifthMessage.secretKey = messageOption.secret;
				fifthMessage.fileLength = messageOption.file_length;
				fifthMessage.duration = messageOption.length;
				fifthMessage.thumbnailDisplayName = messageOption.filename;
				fifthMessage.thumbnailRemotePath = messageOption.thumb;
				fifthMessage.thumbnailSecretKey = messageOption.thumb_secret;
			}
			break;
		case 'loc':
			fifthMessage.type = 3;
			fifthMessage.latitude = messageOption.lat;
			fifthMessage.longitude = messageOption.lng;
			fifthMessage.address = messageOption.addr;
			fifthMessage.buildingName = messageOption.buildingName;
			fifthMessage.latitude = messageOption.lat;
			break;
		case 'audio':
			fifthMessage.type = 4;
			if (messageOption.body) {
				fifthMessage.displayName = messageOption.body.filename;
				fifthMessage.remotePath = messageOption.body.url;
				fifthMessage.secretKey = messageOption.body.secret;
				fifthMessage.fileLength = messageOption.body.file_length;
				fifthMessage.duration = messageOption.body.length;
				// fifthMessage.size = messageOption.body.size;
				fifthMessage.thumbnailDisplayName = messageOption.body.filename;
			} else if (messageOption.isBuildCombinedMsg) {
				fifthMessage.displayName = messageOption.filename;
				fifthMessage.remotePath = messageOption.url;
				fifthMessage.secretKey = messageOption.secret;
				fifthMessage.fileLength = messageOption.file_length;
				fifthMessage.duration = messageOption.length;
				fifthMessage.thumbnailDisplayName = messageOption.filename;
			}
			break;
		case 'file':
			fifthMessage.type = 5;
			if (messageOption.body) {
				fifthMessage.displayName = messageOption.body.filename;
				fifthMessage.remotePath = messageOption.body.url;
				fifthMessage.secretKey = messageOption.body.secret;
				fifthMessage.fileLength = messageOption.body.file_length;
				// fifthMessage.size = messageOption.body.size;
				fifthMessage.thumbnailDisplayName = messageOption.body.filename;
			} else if (messageOption.isBuildCombinedMsg) {
				fifthMessage.displayName = messageOption.filename;
				fifthMessage.remotePath = messageOption.url;
				fifthMessage.secretKey = messageOption.secret;
				fifthMessage.fileLength = messageOption.file_length;
				fifthMessage.thumbnailDisplayName = messageOption.filename;
			}
			break;
		case 'cmd':
			fifthMessage.type = 6;
			fifthMessage.action = messageOption.action;
			// fifthMessage.params =
			break;
		case 'custom':
			fifthMessage.type = 7;
			fifthMessage.customEvent = messageOption.customEvent;
			fifthMessage.customExts = encodeKeyValueMsg.call(
				this,
				messageOption.customExts
			);
			break;
		case 'combine':
			fifthMessage.type = 0;
			fifthMessage.subType = 0;
			fifthMessage.text = messageOption.compatibleText;
			fifthMessage.displayName = messageOption.filename;
			fifthMessage.remotePath = messageOption.url;
			fifthMessage.secretKey = messageOption.secret;
			fifthMessage.fileLength = messageOption.file_length;
			fifthMessage.title = messageOption.title;
			fifthMessage.summary = messageOption.summary;
			fifthMessage.combineLevel = messageOption.combineLevel;
			break;
		default:
			break;
	}
	let msgExt: msgExt = [];
	if (messageOption.ext) {
		msgExt = encodeKeyValueMsg.call(this, messageOption.ext);
	}

	const messageBody = this.root.lookup('easemob.pb.MessageBody');
	let fourthMessage = messageBody.decode(emptyMessage);
	const fromUserId = messageOption.from || this.context.jid.name;
	fourthMessage.from = {
		name: messageOption.isBuildCombinedMsg
			? fromUserId
			: this.context.jid.name,
	};

	const idx = messageOption.to.indexOf('/');
	const messageTo =
		idx > -1 ? messageOption.to.substring(0, idx) : messageOption.to;
	fourthMessage.to = {
		name: messageTo,
	};
	if (messageOption.type === 'channel') {
		fourthMessage.type = MsyncMessageType.CHANNEL_ACK;
	} else if (messageOption.type === 'recall') {
		fourthMessage.type = MsyncMessageType.RECALL;
		fourthMessage.ackMessageId = messageOption.ackId;
	} else if (messageOption.type === 'delivery') {
		//目前为单聊的delivery
		fourthMessage.type = MsyncMessageType.DELIVER_ACK;
		fourthMessage.ackMessageId = messageOption.ackId;
	} else if (messageOption.type === 'read') {
		fourthMessage.type = MsyncMessageType.READ_ACK;
		fourthMessage.ackMessageId = messageOption.ackId;
		if (chatType === 'groupChat') {
			fourthMessage.msgConfig = { allowGroupAck: true };
			fourthMessage.ackContent = messageOption.ackContent;
		}
	} else if (chatType === 'chatRoom') {
		fourthMessage.type = MsyncMessageType.CHATROOM;
	} else if (chatType === 'groupChat') {
		fourthMessage.type = MsyncMessageType.GROUPCHAT;
		if (messageOption.msgConfig) {
			const { allowGroupAck } = messageOption.msgConfig;
			fourthMessage.msgConfig = { allowGroupAck: !!allowGroupAck };
		}
	} else if (chatType === 'singleChat') {
		fourthMessage.type = MsyncMessageType.SINGLECHAT;
	}
	// 如果存在editMessageId, 则为编辑消息
	if (messageOption.editMessageId) {
		fourthMessage.type = MsyncMessageType.EDIT;
		fourthMessage.editMessageId = messageOption.editMessageId;
		fourthMessage.editScope = EditScope.BODY;
		if (messageOption.ext) {
			fourthMessage.editScope = EditScope.BODY_AND_EXT;
		}
	}
	fourthMessage.contents = [fifthMessage];
	fourthMessage.ext = msgExt;
	const messageMeta = buildMessageMeta(messageOption);
	if (messageMeta) {
		fourthMessage.meta = messageMeta;
	}
	fourthMessage = messageBody.encode(fourthMessage).finish();
	const MetaMessage = this.root.lookup('easemob.pb.Meta');
	const thirdMessage = MetaMessage.decode(emptyMessage);
	thirdMessage.id = messageOption.id;

	let domain = 'easemob.com';
	if (chatType === 'chatRoom' || chatType === 'groupChat') {
		domain = 'conference.easemob.com';
	}
	// thirdMessage.from = this.context.jid;
	thirdMessage.to = {
		appKey: this.appKey,
		name: messageTo,
		domain: domain,
	};
	if (idx > -1) {
		thirdMessage.to.clientResource = messageOption.to.substring(
			idx + 1,
			messageOption.to.length
		);
	}

	if (chatType === 'chatRoom') {
		thirdMessage.ext = encodeKeyValueMsg.call(
			this,
			buildMessageExt(messageOption)
		);
	}
	// recall message add meta extension field
	if (messageOption.type === 'recall') {
		if (messageOption.metaExt) {
			thirdMessage.ext = encodeKeyValueMsg.call(this, {
				recallMessageExtensionInfo: messageOption.metaExt,
			});
		}
	}
	thirdMessage.ns = 1;
	thirdMessage.payload = fourthMessage;
	thirdMessage.routetype = messageOption.deliverOnlineOnly ? 1 : 0;
	if (
		chatType !== 'singleChat' &&
		Array.isArray(messageOption.receiverList) &&
		messageOption.receiverList?.length > 0
	) {
		thirdMessage.directedUsers = messageOption.receiverList;
		thirdMessage.routetype = 2;
	}
	if (messageOption.isBuildCombinedMsg) {
		thirdMessage.timestamp = Long.fromValue(messageOption.time);
		return MetaMessage.encode(thirdMessage).finish();
	}
	const commSyncULMessage = this.root.lookup('easemob.pb.CommSyncUL');
	let secondMessage = commSyncULMessage.decode(emptyMessage);
	secondMessage.meta = thirdMessage;
	!messageOption.isPing &&
		setMsgHashThirdMessage.call(this, messageOption, thirdMessage);
	secondMessage = commSyncULMessage.encode(secondMessage).finish();
	const msyncMessage = this.root.lookup('easemob.pb.MSync');
	let firstMessage = msyncMessage.decode(emptyMessage);
	firstMessage.version = this.version;
	firstMessage.encryptType = this.encryptType;
	firstMessage.command = 0;
	// firstMessage.guid = this.context.jid;
	firstMessage.payload = secondMessage;
	// logger.debug('⬆︎ MSync: ', firstMessage)
	firstMessage = msyncMessage.encode(firstMessage).finish();

	return firstMessage;
}
function upLoadFile(this: any, message: any): Promise<any> | Buffer | void {
	const messageOption = { ...message };
	// 如果是编辑合并消息，则直接发送编辑消息
	if (messageOption.editMessageId) {
		const msg = encodeChatMsg.call(this, messageOption);
		sendMSync.call(this, msg);
		return;
	}

	if (message.file) {
		messageOption.accessToken = this.token;
		messageOption.appKey = this.appKey;
		messageOption.apiUrl = this.apiUrl;
		if (messageOption.body && messageOption.body.url) {
			// Only send msg
			return encodeChatMsg.call(this, messageOption);
		}
		return new Promise((resolve, reject) => {
			const _tmpComplete = messageOption.onFileUploadComplete;
			const _complete = (data: any) => {
				if (data.entities[0]['file-metadata']) {
					const file_len =
						data.entities[0]['file-metadata']['content-length'];
					messageOption.file_length = file_len;
					messageOption.filetype =
						data.entities[0]['file-metadata']['content-type'];
					if (file_len > 204800) {
						messageOption.thumbnail = true;
					}
				}
				const url = `${this.apiUrl}/${this.orgName}/${this.appName}/chatfiles/${data.entities[0]['uuid']}`;
				messageOption.body = {
					type: messageOption.type || 'file',
					secret: data.entities[0]['share-secret'],
					filename:
						messageOption.file.filename || messageOption.filename,
					url,
					length: messageOption.length || 0,
					filetype:
						messageOption.filetype || messageOption.file.filetype,
					file_length: messageOption.file?.data?.size || 0,
					size: {
						width: messageOption.width || 0,
						height: messageOption.height || 0,
					},
				};
				messageOption.file.url = data.uri;
				message.secret = data.entities[0]['share-secret'];
				data.url =
					message.url = `${url}?em-redirect=true&share-secret=${data.entities[0]['share-secret']}`;
				message.file_length = messageOption.file_length =
					messageOption.file?.data?.size || 0;

				//  图片消息增加缩略图字段
				if (messageOption.type === 'img') {
					message.thumb = `${message.url}&thumbnail=true`;
					data.thumb = `${data.url}&thumbnail=true`;
				}
				// 视频消息增加封面
				if (messageOption.type === 'video') {
					message.thumb = `${message.url}&vframe=true`;
					message.thumb_secret = data.entities[0]['share-secret'];
					data.thumb = `${data.url}&vframe=true`;
				}
				_tmpComplete instanceof Function &&
					_tmpComplete(data, messageOption.id);
				// let getSendMsg = utils.flow([encodeChatMsg.bind(this), base64transform])
				// let msg = getSendMsg(messageOption)
				// this.sock.send(msg)
				const msg = encodeChatMsg.call(this, messageOption);
				// 附件消息上报
				SEND_MSG_REPORTS.size <= RPT_MAP_MAX_SIZE &&
					SEND_MSG_REPORTS.set(messageOption.id, {
						rpt: this.dataReport.geOperateFun({
							operationName: OperationName.SEND_MSG,
						}),
					});
				this._localCache
					?.getInstance()
					?.storeMessage(message, MessageStatus.INPROGRESS);

				sendMSync.call(this, msg);
				//this._msgHash[messageOption.id] = { ...messageOption, resolve, reject };
			};
			messageOption.onFileUploadComplete = _complete;
			utils.uploadFile.call(
				this,
				messageOption,
				OperationName.UPLOAD_MSG_ATTACH
			);
			// return messageOption
		});
	} else {
		if (message.type === 'combine') {
			//处理合并转发消息
			// const _this = this;
			messageOption.accessToken = this.token;
			messageOption.appKey = this.appKey;
			messageOption.apiUrl = this.apiUrl;
			const _onFileUploadComplete = messageOption.onFileUploadComplete;
			const _onFileUploadError = messageOption.onFileUploadError;
			return new Promise((resolve, reject) => {
				//合并的消息条数最多300条
				if (
					message.messageList?.length > 300 ||
					message.messageList?.length === 0
				) {
					return this._msgHash[messageOption.id].reject({
						type: Code.MAX_LIMIT,
						message:
							'The number of combined messages exceeded the limit.',
					});
				}
				const messageList = [...message.messageList];
				let level = 0;
				messageList.forEach((item) => {
					if (item?.combineLevel > level) {
						level = item?.combineLevel;
					}
				});
				//设置合并消息层级--web端用户创建合并消息时候可以修改combineLevel字段？
				messageOption.combineLevel = level + 1;
				if (messageOption.combineLevel > 10) {
					return this._msgHash[messageOption.id].reject({
						type: Code.MAX_LIMIT,
						message:
							'The level of the merged message exceeded the limit.',
					});
				}
				//消息排序，升序
				// messageList.sort((a: any, b: any) => {
				// 	return a.time - b.time;
				// });
				const forwardMsg = encodeForwardMsg.call(this, messageList);

				// 发送文件消息
				const sendFileMsg = (
					data: {
						uri: string;
						entities: { [x: string]: any }[];
					},
					file?: { fileName: string; fileLength: number }
				) => {
					const url = `${this.apiUrl}/${this.orgName}/${this.appName}/chatfiles/${data.entities[0]['uuid']}`;
					const secret = data.entities[0]?.['share-secret'];
					let fileUrl = url + '?em-redirect=true';
					if (secret) {
						fileUrl = `${fileUrl}&share-secret=${secret}`;
					}

					_onFileUploadComplete instanceof Function &&
						_onFileUploadComplete({
							url: fileUrl,
							secret,
						});
					message.url = fileUrl;
					message.secret = secret;
					messageOption.url = fileUrl;
					messageOption.secret = data.entities[0]['share-secret'];
					messageOption.filename =
						messageOption.file?.filename || file?.fileName;
					messageOption.file_length =
						messageOption.file?.data?.size || file?.fileLength || 0;
					this._localCache
						?.getInstance()
						?.storeMessage(message, MessageStatus.INPROGRESS);
					const msg = encodeChatMsg.call(this, messageOption);
					sendMSync.call(this, msg);
				};

				const platForm = utils.getEnvInfo();
				if (
					platForm.platform !== 'web' &&
					platForm.platform !== 'node' &&
					platForm.platform !== 'quick_app' &&
					platForm.global?.canIUse?.('getFileSystemManager')
				) {
					const fs = platForm.global.getFileSystemManager();
					const uploadUrl = `${this.apiUrl}/${this.orgName}/${this.appName}/chatfiles`;
					fs.writeFile({
						filePath: `${platForm.global.env.USER_DATA_PATH}/combine`,
						data: forwardMsg.buffer,
						encoding: 'binary',
						success(res: any) {
							platForm.global.uploadFile({
								url: uploadUrl,
								filePath: `${platForm.global.env.USER_DATA_PATH}/combine`,
								name: 'file',
								header: {
									'Content-Type': 'multipart/form-data',
									Authorization:
										'Bearer ' + messageOption.accessToken,
								},
								success(res: any) {
									if (res.statusCode === 200) {
										logger.debug('upload success', res);
										const data = JSON.parse(res.data);

										sendFileMsg(data, {
											fileName: 'combine',
											fileLength: forwardMsg.length,
										});
									} else {
										logger.debug('upload fail');
										messageOption.onFileUploadError instanceof
											Function &&
											messageOption.onFileUploadError(
												res
											);
										this._msgHash[messageOption.id].reject({
											//上传失败报错
											type: Code.WEBIM_UPLOADFILE_ERROR,
											message:
												'Failed to upload the merge message.Please try again',
											data: res,
										});
									}
								},
								fail(err: any) {
									logger.debug('upload fail');
									messageOption.onFileUploadError instanceof
										Function &&
										messageOption.onFileUploadError(res);
									this._msgHash[messageOption.id].reject({
										//上传失败报错
										type: Code.WEBIM_UPLOADFILE_ERROR,
										message:
											'Failed to upload the merge message.Please try again',
										data: err,
									});
								},
							});
						},
						fail(res: any) {
							logger.debug('write file fail', res);
							this._msgHash[messageOption.id].reject({
								//上传失败报错
								type: Code.WEBIM_UPLOADFILE_ERROR,
								message:
									'Failed to upload the merge message.Please try again',
								data: res,
							});
						},
					});
					return;
				}
				const newFile = new File([forwardMsg], 'combine', {
					type: 'application/octet-stream',
				});
				const newFileObj = {
					url: URL.createObjectURL(newFile),
					filename: messageOption.id,
					data: newFile,
				};
				messageOption.file = newFileObj;

				const _complete = (data: any) => {
					sendFileMsg(data);
				};
				const _failed = (data: any) => {
					_onFileUploadError instanceof Function &&
						_onFileUploadError(data);

					this._msgHash[messageOption.id].reject({
						//上传失败报错
						type: Code.WEBIM_UPLOADFILE_ERROR,
						message:
							'Failed to upload the merge message.Please try again',
						data,
					});
				};

				messageOption.onFileUploadComplete = _complete;
				messageOption.onFileUploadError = _failed;
				utils.uploadFile.call(
					this,
					messageOption,
					OperationName.UPLOAD_MSG_ATTACH
				);
			});
		}
		if (message.type === 'img') {
			if (!messageOption.body) {
				messageOption.body = {
					...messageOption,
					size: {
						width: messageOption.width || 0,
						height: messageOption.height || 0,
					},
				};
			}
		}
		this._localCache
			?.getInstance()
			?.storeMessage(message, MessageStatus.INPROGRESS);
		const msg = encodeChatMsg.call(this, messageOption);
		sendMSync.call(this, msg);
	}
}
/** @internal  */
function setMsgHashThirdMessage(
	this: Connection,
	messageOption: any,
	thirdMessage: any
) {
	if (messageOption.editMessageId) {
		thirdMessage.ignoreCallback = true;
		this._msgHash[messageOption.id].thirdMessage = thirdMessage;
		return;
	}
	if (GENERAL_MESSAGE_TYPES.includes(messageOption.type)) {
		this._msgHash[messageOption.id].thirdMessage = thirdMessage;
	}
}

/** @internal */
function encodeForwardMsg(this: any, messageList: any[]): Uint8Array {
	const startStr = 'cm'; //文件头
	let combinedArray = Uint8Array.from(startStr, (c) => c.charCodeAt(0));
	for (let i = 0; i < messageList.length; i++) {
		const item: any = messageList[i];
		//isBuildCombinedMsg 该属性标记是编码合并消息，只返回meta即可
		const messageOption: object = { ...item, isBuildCombinedMsg: true };
		//消息编码 meta
		const thirdMessage: any = this.mSync.encodeChatMsg.call(
			this,
			messageOption
		);
		//消息长度
		const number = thirdMessage.length;
		const uint8ArrayLength = new Uint8Array(4);
		for (let i = 0; i < 4; i++) {
			uint8ArrayLength[i] = (number >> (8 * (3 - i))) & 0xff;
		}
		logger.debug('message length:', uint8ArrayLength);
		//构建新的Uint8Array
		const newCombinedArray = new Uint8Array(
			combinedArray.length + uint8ArrayLength.length + thirdMessage.length
		);
		newCombinedArray.set(combinedArray);
		newCombinedArray.set(uint8ArrayLength, combinedArray.length);
		newCombinedArray.set(
			thirdMessage,
			combinedArray.length + uint8ArrayLength.length
		);
		combinedArray = newCombinedArray;
	}
	// 校验算法，跳过cm头，每隔一个字节，做一下异或，（排除前两个字节后，计算第2，4，6，8...字节的异或结果）最后的结果写入文件尾部
	const fcombinedArray = new Uint8Array(combinedArray.length + 1);
	let checkResult = 0x00;

	for (let i = 2; i < combinedArray.length; i++) {
		if (i % 2 === 1) {
			checkResult ^= combinedArray[i];
		}
	}
	fcombinedArray.set(combinedArray);
	logger.debug('checkResult:', checkResult);
	fcombinedArray[combinedArray.length] = checkResult;
	return fcombinedArray;
}
/**
 * @internal
 * 构建加入聊天室消息
 */
function buildHandleChatRoomMsg(
	this: any,
	params: any,
	msgId: string,
	operation: 'join' | 'leave'
) {
	const emptyMessage: [] = [];
	const mucBodyMessage: any = this.root.lookup('easemob.pb.MUCBody');
	const { roomId, ext = '', leaveOtherRooms = false } = params;
	let fourthMessage = mucBodyMessage.decode(emptyMessage);
	fourthMessage.mucId = {
		appKey: this.appKey,
		name: roomId,
		domain: 'conference.easemob.com',
	};
	fourthMessage.operation = operation === 'join' ? 2 : 3;
	fourthMessage.from = { name: this.context.userId };
	fourthMessage.isChatroom = true;
	// 加入聊天室时，需要传入ext和leaveOtherRooms
	if (operation === 'join') {
		fourthMessage.ext = ext;
		fourthMessage.leaveOtherRooms = leaveOtherRooms;
	}

	fourthMessage = mucBodyMessage.encode(fourthMessage).finish();
	const MetaMessage: any = this.root.lookup('easemob.pb.Meta');
	const thirdMessage = MetaMessage.decode(emptyMessage);
	thirdMessage.id = msgId;
	thirdMessage.from = {
		appKey: this.appKey,
		name: this.context.userId,
		domain: 'easemob.com',
		client_resource: this.context.jid.clientResource,
	};
	thirdMessage.to = {
		domain: 'easemob.com',
	};
	thirdMessage.ns = 2;
	thirdMessage.payload = fourthMessage;
	const commSyncULMessage = this.root.lookup('easemob.pb.CommSyncUL');
	let secondMessage = commSyncULMessage.decode(emptyMessage);
	secondMessage.meta = thirdMessage;
	secondMessage = commSyncULMessage.encode(secondMessage).finish();
	const msyncMessage = this.root.lookup('easemob.pb.MSync');
	let firstMessage = msyncMessage.decode(emptyMessage);
	firstMessage.version = this.version;
	firstMessage.encryptType = this.encryptType;
	firstMessage.command = 0;
	firstMessage.payload = secondMessage;
	firstMessage = msyncMessage.encode(firstMessage).finish();
	return firstMessage;
}
function handleChatRoom(this: any, params: any, operation: 'join' | 'leave') {
	const msgId = utils.getUniqueId();
	const joinMsg = buildHandleChatRoomMsg.call(this, params, msgId, operation);
	// msync 加入退出聊天室上报
	const operationName =
		operation === 'join'
			? OperationName.JOIN_CHATROOM
			: OperationName.QUIT_CHATROOM;
	const rpt = this.dataReport.geOperateFun({
		operationName,
	});
	CHAT_ROOM_OPT_REPORTS.size <= RPT_MAP_MAX_SIZE &&
		CHAT_ROOM_OPT_REPORTS.set(msgId, {
			rpt,
			requestName: operationName,
		});
	return sendMSync.call(
		this,
		{
			...params,
			isHandleChatroom: true,
			joinMsg,
			id: msgId,
			operation,
		},
		msgId
	);
}

// 3.0 send
// function send(this: Connection, params: any) {
//     this._msgHash[params.id] = params;
//     let getSendMsg = utils.flow([encodeChatMsg.bind(this), base64transform])
//     let msg = getSendMsg(params)
//     this.sock.send(msg)
// }

interface SendMsgResult {
	localMsgId: string; // local message id
	serverMsgId: string; // server message id
	message?: ExcludeAckMessageBody; // message
}

// 4.0 send message
function send(
	this: any,
	params: MessagesType & { editMessageId?: string }
): Promise<SendMsgResult> {
	return new Promise((resolve, reject) => {
		logger.debug('call send');
		if (this.logOut) {
			logger.debug('send message failed', Code.WEBIM_CONNECTION_CLOSED);
			return reject({
				type: Code.WEBIM_CONNECTION_CLOSED,
				message: 'not login',
			});
		}
		if (!params.id || typeof params.id !== 'string' || params.id === '') {
			return reject({
				type: Code.MESSAGE_PARAMETER_ERROR,
				message: 'Missing required parameter: "id"',
			});
		}
		if (!params.to || typeof params.to !== 'string' || params.to === '') {
			return reject({
				type: Code.MESSAGE_PARAMETER_ERROR,
				message: 'Missing required parameter: "to"',
			});
		}
		const isAttachMsg =
			params.type === 'file' ||
			params.type === 'img' ||
			params.type === 'audio' ||
			params.type === 'video';

		const noRptMsg =
			params.type === 'delivery' ||
			params.type === 'read' ||
			params.type === 'channel';

		const isCmdMsg = params.type === 'cmd';

		const isRecallMsg = (params.type as any) === 'recall';

		const isModifiedMsg = params?.editMessageId;

		const isCacheMsg =
			!noRptMsg && !isRecallMsg && !isCmdMsg && !isModifiedMsg;

		if (params.id) {
			// 不是附件消息并且不是ack消息，则上报数据
			// 或者是附件消息，并且useOwnUploadFun为true,则表示用户自行上传附件，直接上报数据
			if (
				(!isAttachMsg && !noRptMsg) ||
				(isAttachMsg && this.useOwnUploadFun)
			) {
				let operationName =
					(params.type as any) === 'recall'
						? OperationName.SEND_RECALL_MSG
						: OperationName.SEND_MSG;
				// 编辑消息
				if (isModifiedMsg) operationName = OperationName.MODIFY_MESSAGE;
				SEND_MSG_REPORTS.size <= RPT_MAP_MAX_SIZE &&
					SEND_MSG_REPORTS.set(params.id, {
						rpt: this.dataReport.geOperateFun({
							operationName: operationName,
						}),
					});
			}

			if (isCacheMsg) {
				// 存储消息
				this._localCache
					?.getInstance()
					?.storeMessage(params, MessageStatus.CREATE);
			}

			this._msgHash[params.id] = {
				...params,
				resolve,
				reject,
			};
		}

		if (isAttachMsg || params.type === 'combine') {
			return upLoadFile.call(this, params);
		}

		if (
			params.type === 'txt' &&
			params.msgConfig?.languages &&
			Array.isArray(params.msgConfig?.languages) &&
			params.msgConfig.languages.length > 0
		) {
			const translateMessage =
				this.translateMessage || this.translation.translateMessage;

			if (!translateMessage) {
				throw new Error('there is no method to translate message');
			}
			translateMessage
				.call(this, {
					text: params.msg,
					languages: params.msgConfig.languages,
				})
				.then((res: any) => {
					let translations = res.data[0]?.translations;
					translations = translations.map(
						(value: { to: string; text: string }) => {
							return { code: value.to, text: value.text };
						}
					);
					params.translations = translations;
					this._msgHash[params.id].translations = translations;
					this._localCache
						?.getInstance()
						?.storeMessage(params, MessageStatus.INPROGRESS);
					const chatMsg = encodeChatMsg.call(this, params);
					sendMSync.call(this, chatMsg, params.id);
				})
				.catch((err: ErrorEvent) => {
					reject(err);
				});
			return;
		}
		if (isCacheMsg) {
			// 存储消息
			this._localCache
				?.getInstance()
				?.storeMessage(params, MessageStatus.INPROGRESS);
		}
		const chatMsg = encodeChatMsg.call(this, params);
		sendMSync.call(this, chatMsg, params.id);
	});
}

/** @internal */
function sendMSync(this: any, params: any, msgId?: string) {
	//处理加入、退出聊天室消息
	if (params.isHandleChatroom) {
		if (!this.isOpened()) {
			const error = {
				data: '',
				type: Code.WEBIM_CONNCTION_DISCONNECTED,
				message: 'websocket has been disconnected',
			};
			if (CHAT_ROOM_OPT_REPORTS.has(params.id)) {
				const { rpt, requestName } = CHAT_ROOM_OPT_REPORTS.get(
					params.id
				);
				rpt({
					isEndApi: true,
					data: {
						isSuccess: 0,
						requestName,
						requestMethod: 'WEBSOCKET',
						requestUrl: this.url,
						code: WSS_CODE.disconnect,
						codeDesc: 'websocket has been disconnected',
					},
				});
				CHAT_ROOM_OPT_REPORTS.delete(params.id);
			}
			return Promise.reject(error);
		}
		return new Promise((resolve, reject) => {
			this._msgHash[params.id] = { ...params, resolve, reject };
			let msg;
			const envInfo = utils.getEnvInfo();
			if (this.name === 'miniCore' || envInfo.platform === 'web') {
				msg = params.joinMsg;
			} else {
				msg = base64transform.call(this, params.joinMsg, msgId);
			}

			const messageTimer =
				msgId &&
				setTimeout(() => {
					if (this._msgHash[params.id]) {
						const error = {
							type: Code.REQUEST_TIMEOUT,
							message: 'request timeout',
						};
						this._msgHash[params.id].reject?.(error);
						clearTimeout(this._msgHash[params.id]?.messageTimer);
						delete this._msgHash[params.id];
						if (!this.reconnecting) {
							this.reconnect(true);
						}
					}
				}, MESSAGE_TIMEOUT);
			this._msgHash[params.id].messageTimer = messageTimer;
			this.sock.send(msg);
		});
	}
	if (!this.isOpened()) {
		this.unMSyncSendMsgMap?.set(msgId, params);
		if (
			!this.logOut &&
			this.autoReconnectNumTotal < this.autoReconnectNumMax
		) {
			logger.debug(
				'need to reconnect',
				this.autoReconnectNumTotal,
				this.autoReconnectNumMax
			);
			this.offLineSendConnecting = true;
			if (!this.reconnecting) {
				this.reconnect();
			}
		}
		this.onError &&
			this.onError({
				type: Code.WEBIM_CONNCTION_DISCONNECTED,
				message: 'websocket has been disconnected',
			});
		return;
	}
	const messageTimer =
		msgId &&
		setTimeout(() => {
			if (this._msgHash[msgId ?? '']) {
				const error = {
					type: Code.MESSAGE_SEND_TIMEOUT,
					message: 'send message timeout',
				};
				this._msgHash[msgId ?? ''].reject?.(error);
				clearTimeout(this._msgHash[msgId ?? '']?.messageTimer);
				delete this._msgHash[msgId ?? ''];
				if (!this.reconnecting) {
					this.reconnect(true);
				}
			}
		}, MESSAGE_TIMEOUT);

	if (this._msgHash[msgId ?? '']) {
		this._msgHash[msgId ?? ''].messageTimer = messageTimer;
	}

	// let getSendMsg = utils.flow([encodeChatMsg.bind(this), base64transform])
	// let msg = getSendMsg(params)
	let msg;
	const envInfo = utils.getEnvInfo();
	if (this.name === 'miniCore' || envInfo.platform === 'web') {
		msg = params;
	} else {
		msg = base64transform.call(this, params, msgId);
	}
	this.sock.send(msg);
}

class Msync {
	// this.root: any;
	constructor(Connection: any, isMini: boolean) {
		Connection.send = send;
		Connection.sendMsg = send;
		// Connection.prototype.send = send
		logger.debug(`init Msync by ${Connection.name}`);
		return {
			generateProvision: generateProvision.bind(Connection),
			base64transform: base64transform.bind(Connection),
			distributeMeta: distributeMeta.bind(Connection),
			decodeMeta: decodeMeta.bind(Connection),
			decodeUnreadDL: decodeUnreadDL.bind(Connection),
			_rebuild: _rebuild.bind(Connection),
			_lastsession: _lastsession.bind(Connection),
			receiveProvision: receiveProvision.bind(Connection),
			isInQueue: isInQueue.bind(Connection),
			decodeNotice: decodeNotice.bind(Connection),
			decodeMSync: decodeMSync.bind(Connection),
			distributeMSync: distributeMSync.bind(Connection),
			encodeChatMsg: encodeChatMsg.bind(Connection),
			upLoadFile: upLoadFile.bind(Connection),
			send: send.bind(Connection),
			stopHeartBeat: stopHeartBeat.bind(Connection),
			handleChatRoom: handleChatRoom.bind(Connection),
			sendUnreadDeal: sendUnreadDeal.bind(Connection),
		};
	}
}

export default Msync;
