/**
 * Translation contains the APIs for translation operations.
 * @module Translation
 */

import { checkStatus } from './utils';
import { Code } from '../status';
import { Connection } from '../engineCore/connection';
import ErrorEvent from '../error/error';
import { logger } from '../logger';
import { ajax } from '../utils';
import * as TranslationTypes from '../types/translation';
import { AsyncResult } from '../types/common';
/**
 * Gets the list of supported languages for translation.
 *
 * ```typescript
 * connection.getSupportedLanguages()
 * ```
 */

/**
 * 获取翻译服务支持的语言。
 *
 * ```typescript
 * connection.getSupportedLanguages()
 * ```
 */
function getSupportedLanguages(
	this: Connection
): Promise<AsyncResult<TranslationTypes.SupportLanguage[]>> {
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken } = this.context;
	const url = `${this.apiUrl}/${orgName}/${appName}/users/${this.user}/translate/support/language`;
	const opts = {
		url: url,
		dataType: 'json',
		type: 'GET',
		headers: {
			Authorization: 'Bearer ' + accessToken,
		},
	};
	logger.debug('Call getSupportedLanguages');
	return ajax.call(this, opts);
}

/**
 * Translates the text.
 *
 * ```typescript
 * connection.translateMessage({text: 'hello', languages: [zh-Hans]})
 * ```
 */

/**
 * 翻译消息。
 *
 * ```typescript
 * connection.translateMessage({text: 'hello', languages: [zh-Hans]})
 * ```
 */
function translateMessage(
	this: Connection,
	params: {
		/** The text to be translated. */
		/** 要翻译的文本。 */
		text: string;
		/** The list of target language codes.*/
		/** 要翻译的目标语言代码列表。 */
		languages: string[];
	}
): Promise<AsyncResult<TranslationTypes.TranslationResult>> {
	if (typeof params.text !== 'string' || params.text === '') {
		throw Error('Invalid parameter: "text"');
	} else if (!Array.isArray(params.languages)) {
		throw Error('Invalid parameter: "language"');
	}
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken } = this.context;
	const url = `${this.apiUrl}/${orgName}/${appName}/users/${this.user}/translate`;
	const postData = {
		text: params.text,
		to: params.languages,
	};
	const opts = {
		url: url,
		dataType: 'json',
		type: 'POST',
		data: JSON.stringify(postData),
		headers: {
			Authorization: 'Bearer ' + accessToken,
		},
	};
	logger.debug('Call translateMessage');
	return ajax.call(this, opts);
}

export { getSupportedLanguages, translateMessage };
