import ErrorEvent from './error';
import { Code } from '../status';
function handleError(
	xhr: XMLHttpRequest,
	reject: (error: ErrorEvent) => void,
	error: (error: ErrorEvent) => void,
	elapse: number // 请求耗时
) {
	let res = xhr.response;
	if (res && typeof res === 'string') {
		res = JSON.parse(res);
	}
	const status = xhr.status;
	const extraInfo = {
		elapse: elapse,
		httpCode: status,
		errDesc: res?.error_description,
	};
	if (status === 400) {
		if (res.error_code === 40002) {
			reject({
				type: Code.THREAD_ALREADY_EXIST,
				message: res?.error_description,
				extraInfo,
			});
			return;
		} else if (res.error_code === 40009) {
			reject({
				type: Code.OPERATION_UNSUPPORTED,
				message: res?.error_description,
				extraInfo,
			});
			return;
		} else if (res.error_code === 60005) {
			reject({
				type: Code.GROUP_MEMBER_ATTRIBUTES_SET_FAILED,
				message: res?.desc || res?.error_description,
				extraInfo,
			});
			return;
		} else if (res.error_code === 60010) {
			if (
				res?.error_description.includes(
					'exceeds chatgroup user metadata single value limit'
				)
			) {
				// group member attributes value limit
				reject({
					type: Code.MAX_LIMIT,
					message: res?.desc || res.error_description,
					extraInfo,
				});
			} else {
				reject({
					type: Code.NO_PERMISSION,
					message: res?.desc || res.error_description,
					extraInfo,
				});
			}

			return;
		} else if (res.error_code === 60011) {
			reject({
				type: Code.CHATROOM_NOT_JOINED,
				message: res?.desc,
				extraInfo,
			});
			return;
		} else if (res.error_code === 14403) {
			// 完成文件上传时, 分片大小不合法
			reject({
				type: Code.WEBIM_UPLOADFILE_ERROR,
				message: res?.error_description,
				data: res,
				extraInfo,
			});
			return;
		} else if (
			res.error_code === 60006 ||
			res.error_code === 60007 ||
			res.error_code === 60009 ||
			res.error_code === 60012
		) {
			reject({
				type: Code.MAX_LIMIT,
				message: res?.desc || res?.error_description,
				extraInfo,
			});
			return;
		} else if (res.error_code === 91104) {
			reject({
				type: Code.NO_PERMISSION,
				message: res?.error_description,
				extraInfo,
			});
			return;
		}

		if (res.error_description?.includes('are not members of this group')) {
			if (xhr.responseURL?.includes('chatgroups')) {
				reject({
					type: Code.GROUP_NOT_JOINED,
					data: xhr.response || xhr.responseText,
					message: res.error_description,
					extraInfo,
				});
			} else {
				// 操作的人不在聊天室
				reject({
					type: Code.CHATROOM_NOT_JOINED,
					data: xhr.response || xhr.responseText,
					message: res.error_description,
					extraInfo,
				});
			}
			error({
				type: Code.WEBIM_CONNCTION_AJAX_ERROR,
				message: res?.error_description,
				data: xhr.responseText,
				extraInfo,
			});
			return;
		}
		if (res?.result === 'the app not open presence') {
			reject({
				type: Code.SERVICE_NOT_ENABLED,
				message: res?.result,
				extraInfo,
			});
			return;
		}

		if (res?.error_description.includes('remark length must less')) {
			reject({
				type: Code.MAX_LIMIT,
				message: res?.error_description,
				extraInfo,
			});
			return;
		}

		switch (res?.error_description) {
			case 'the user is already operation this message':
				// 该用户已经操作过这个reaction
				reject({
					type: Code.REACTION_ALREADY_ADDED,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'The quantity has exceeded the limit!':
				// reaction 数量已达到限制
				reject({
					type: Code.MAX_LIMIT,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'The user not in this group!':
				// add reaction 当前用户不在这个群组当中
				reject({
					type: Code.GROUP_NOT_JOINED,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'the user operation is illegal!':
				// 当前用户不是这条消息的发送者或接收者：
				reject({
					type: Code.REACTION_OPERATION_IS_ILLEGAL,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'this appKey is not open reaction service!':
				// 当前appkey没有开通reaction服务：
				reject({
					type: Code.SERVICE_NOT_ENABLED,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'this message is creating reaction, please try again.':
				// 正在创建 reaction：
				reject({
					type: Code.REACTION_CREATING,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'groupId can not be null!':
				// 获取群组reaction时群组id为空：
				reject({
					type: Code.GROUP_NOT_EXIST,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'The input text is too long.':
				// 翻译文本过长
				reject({
					type: Code.TRANSLATION_TEXT_TOO_LONG,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'The target language is not valid.':
				// 翻译传入的语言 code 不合法
				reject({
					type: Code.TRANSLATION_NOT_VALID,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'report failed, get message by id failed':
				reject({
					type: Code.MESSAGE_NOT_FOUND,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'ext is too big ':
				reject({
					type: Code.PRESENCE_PARAM_EXCEED,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'Request body not readable.Please check content type is correct!':
				reject({
					type: Code.REQUEST_PARAMETER_ERROR,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'this appKey not open message roaming':
				// 没开通删除漫游服务
				reject({
					type: Code.SERVICE_NOT_ENABLED,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'updateRemark | they are not friends, please add as a friend first.':
				// 设置好友备注时，当前用户不是好友关系
				reject({
					type: Code.USER_NOT_FRIEND,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'param mark must be not empty, please check!':
				// 会话标记为空
				reject({
					type: Code.REQUEST_PARAMETER_ERROR,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'param mark illegal, please check it!':
				// 会话标记不合法
				reject({
					type: Code.REQUEST_PARAMETER_ERROR,
					message: res?.error_description,
					extraInfo,
				});
				break;
			case 'param pin_msg_id illegal, please check it!':
				// pin消息ID不合法
				reject({
					type: Code.REQUEST_PARAMETER_ERROR,
					message: res?.error_description,
					extraInfo,
				});
				break;
			default:
				reject({
					type: Code.WEBIM_CONNCTION_AJAX_ERROR,
					message: res?.error_description,
					data: xhr.responseText,
					extraInfo,
				});
				error({
					type: Code.WEBIM_CONNCTION_AJAX_ERROR,
					message: res?.error_description,
					data: xhr.responseText,
					extraInfo,
				});
				break;
		}
	} else if (status === 401) {
		if (
			res.error_code === 40001 ||
			res.error_code === 60001 ||
			res.error_description === 'Unable to authenticate (OAuth)'
		) {
			reject({
				type: Code.NO_PERMISSION,
				message: res?.error_description,
				extraInfo,
			});
		} else {
			error({
				type: Code.WEBIM_CONNCTION_AJAX_ERROR,
				data: xhr.responseText,
				message: xhr.responseText,
				extraInfo,
			});
			reject({
				type: Code.WEBIM_CONNCTION_AJAX_ERROR,
				data: xhr.responseText,
				message: xhr.responseText,
				extraInfo,
			});
		}
	} else if (status === 403) {
		if (
			res.error_code === 40000 ||
			res.error_code === 60004 ||
			res.error_code === 15002
		) {
			reject({
				type: Code.SERVICE_NOT_ENABLED,
				message: res?.error_description,
				extraInfo,
			});
		} else if (res.error_code === 40003) {
			reject({
				type: Code.THREAD_ALREADY_EXIST,
				message: res?.error_description,
				extraInfo,
			});
		} else if (res.error_code === 40004) {
			reject({
				type: Code.THREAD_ALREADY_EXIST,
				message: res?.error_description,
				extraInfo,
			});
		} else if (
			res.error_code === 40005 ||
			res.error_code === 40007 ||
			res.error_code === 91002
		) {
			reject({
				type: Code.MAX_LIMIT,
				message: res?.error_description,
				extraInfo,
			});
		} else if (res.error_code === 60002) {
			reject({
				type: Code.PERMISSION_DENIED,
				message: res?.error_description,
				extraInfo,
			});
		} else if (res.error_code === 91101) {
			// Pin 消息数量超限
			reject({
				type: Code.MAX_LIMIT,
				message: res?.error_description,
				extraInfo,
			});
		} else if (res.error_code === 91102) {
			reject({
				type: Code.REQUEST_PARAMETER_ERROR,
				message: res.error_description,
				extraInfo,
			});
		}
		// 聊天室 群组人数达到上限
		if (res.error_description === 'group member list is full!') {
			if (xhr.responseURL?.includes('chatgroups')) {
				reject({
					type: Code.GROUP_MEMBERS_FULL,
					data: xhr.response || xhr.responseText,
					message: res.error_description,
					extraInfo,
				});
			} else {
				reject({
					type: Code.CHATROOM_MEMBERS_FULL,
					data: xhr.response || xhr.responseText,
					message: res.error_description,
					extraInfo,
				});
			}
		}
		// 邀请的成员已经在群组
		else if (
			res.error_description?.includes(
				'invite users to join group failed' &&
					res.error_description.includes('already in group')
			)
		) {
			if (xhr.responseURL?.includes('chatgroups')) {
				reject({
					type: Code.GROUP_ALREADY_JOINED,
					data: xhr.response || xhr.responseText,
					message: res.error_description,
					extraInfo,
				});
			}
		}
		// 禁言的人不在群组
		else if (
			res.error_description?.includes('are not members of this group')
		) {
			if (xhr.responseURL?.includes('chatgroups')) {
				reject({
					type: Code.GROUP_NOT_JOINED,
					data: xhr.response || xhr.responseText,
					message: res.error_description,
					extraInfo,
				});
			} else {
				// 操作的人不在聊天室
				reject({
					type: Code.CHATROOM_NOT_JOINED,
					data: xhr.response || xhr.responseText,
					message: res.error_description,
					extraInfo,
				});
			}
		}
		// 没开通翻译服务
		else if (res.error_description?.includes('service not open!')) {
			reject({
				type: Code.SERVICE_NOT_ENABLED,
				data: xhr.response || xhr.responseText,
				message: res.error_description,
				extraInfo,
			});
		}
		// 没开通举报服务
		else if (
			res.error_description?.includes('message report not open') ||
			res.error_description?.includes('messageroaming function not open')
		) {
			reject({
				type: Code.SERVICE_NOT_ENABLED,
				data: xhr.response || xhr.responseText,
				message: res.error_description,
				extraInfo,
			});
		}
		// 创建群组超出群组人数限制
		else if (
			res.error_description?.includes(
				'members size is greater than max user size !'
			)
		) {
			reject({
				type: Code.GROUP_MEMBERS_LIMIT,
				data: xhr.response || xhr.responseText,
				message: res.error_description,
				extraInfo,
			});
		}
		// 群组被禁用
		else if (
			res.error_description?.includes(
				'can not operate this group, reason: group is disabled'
			)
		) {
			reject({
				type: Code.GROUP_IS_DISABLED,
				data: xhr.response || xhr.responseText,
				message: res.error_description,
				extraInfo,
			});
		} else if (
			// eslint-disable-next-line prettier/prettier, quotes
			res.error_description?.includes("Invitee's contact max count") ||
			// eslint-disable-next-line prettier/prettier, quotes
			res.error_description?.includes("Inviter's contact max count")
		) {
			reject({
				type: Code.MAX_LIMIT,
				data: xhr.response || xhr.responseText,
				message: res.error_description,
				extraInfo,
			});
		} else {
			reject({
				type: Code.PERMISSION_DENIED,
				data: xhr.response || xhr.responseText,
				message: 'permission denied',
				extraInfo: {
					...extraInfo,
					errDesc: 'permission denied',
				},
			});
		}
		error({
			type: Code.WEBIM_CONNCTION_AJAX_ERROR,
			data: xhr.responseText,
			message: xhr.responseText,
			extraInfo,
		});
	} else if (status === 404) {
		if (res.error_code === 40011) {
			reject({
				type: Code.THREAD_NOT_EXIST,
				message: res?.error_description,
				extraInfo,
			});
		} else if (res.error_code === 40012) {
			reject({
				type: Code.NO_PERMISSION,
				message: res?.error_description,
				extraInfo,
			});
		} else if (res.error_code === 60003 || res.error_code === 20004) {
			reject({
				type: Code.GROUP_NOT_JOINED,
				message: res?.error_description,
				extraInfo,
			});
		} else if (res.error_code === 91001) {
			reject({
				type: Code.CONVERSATION_NOT_EXIST,
				message: res?.error_description,
				extraInfo,
			});
		}
		if (
			res.error_description?.includes('do not find this group') ||
			res.error_description?.includes('does not exist')
		) {
			if (xhr.responseURL?.includes('chatgroups')) {
				reject({
					type: Code.GROUP_NOT_EXIST,
					data: xhr.response || xhr.responseText,
					message: 'The chat room dose not exist.',
					extraInfo: {
						...extraInfo,
						errDesc: 'The chat room dose not exist.',
					},
				});
			} else {
				reject({
					type: Code.CHATROOM_NOT_EXIST,
					data: xhr.response || xhr.responseText,
					message: 'The chat room dose not exist.',
					extraInfo: {
						...extraInfo,
						errDesc: 'The chat room dose not exist.',
					},
				});
			}
		} else if (
			(res.error_description?.includes('username') &&
				res.error_description?.includes(
					// eslint-disable-next-line quotes
					"doesn't exist!'"
				)) ||
			res.error_description?.includes('user not found') ||
			(res.error_description?.includes('Service resource not found') &&
				res?.exception === 'UserNotFoundException')
		) {
			// 1. 创建群时拉的人不存在 || 2. 登录时用户不存在 || 3. 好友操作用户不存在
			reject({
				type: Code.USER_NOT_FOUND,
				data: xhr.response || xhr.responseText,
				message: res.error_description,
				extraInfo,
			});
		} else if (
			res.error_description.includes('user session pin message not exist')
		) {
			reject({
				type: Code.MESSAGE_NOT_FOUND,
				message: res.error_description,
				extraInfo,
			});
		} else {
			reject({
				type: Code.WEBIM_CONNCTION_AJAX_ERROR,
				data: xhr.response || xhr.responseText,
				message: xhr.responseText,
				extraInfo,
			});
		}
		error({
			type: Code.WEBIM_CONNCTION_AJAX_ERROR,
			data: xhr.response || xhr.responseText,
			message: xhr.responseText,
			extraInfo,
		});
	} else if (status === 406) {
		if (res.error_code === 90004) {
			reject({
				type: Code.OPERATION_NOT_ALLOWED,
				message: res?.error_description,
				extraInfo,
			});
		}
	} else if (status === 429 || status === 503) {
		// 翻译用量达到上限
		if (
			res.error_description?.includes(
				'The request has reached the maximum limit'
			)
		) {
			reject({
				type: Code.MAX_LIMIT,
				message: xhr.responseText,
				extraInfo,
			});
			return;
		}

		// 上传日志次数超限
		if (
			res.error_description?.includes('upload client logs reached limit')
		) {
			reject({
				type: Code.MAX_LIMIT,
				message: xhr.responseText,
			});
			return;
		}

		reject({
			type: Code.SERVER_BUSY,
			data: xhr.response || xhr.responseText,
			message: 'Server is busy.',
			extraInfo: {
				...extraInfo,
				errDesc: 'Server is busy.',
			},
		});
		error({
			type: Code.WEBIM_CONNCTION_AJAX_ERROR,
			data: xhr.responseText,
			message: 'Server is busy.',
			extraInfo: {
				...extraInfo,
				errDesc: 'Server is busy.',
			},
		});
	} else if (status === 500) {
		if (
			res.error_code === 40006 ||
			res.error_code === 40008 ||
			res.error_code === 40010
		) {
			reject({
				type: Code.SERVER_UNKNOWN_ERROR,
				message: res?.error_description,
				extraInfo,
			});
			return;
		}
		if (res.error_code === 90005 || res.error_code === 99999) {
			reject({
				type: Code.REQUEST_UNKNOWN,
				message: res?.error_description,
				extraInfo,
			});
			return;
		}
		if (res.error_description?.includes('translte failed!')) {
			reject({
				type: Code.TRANSLATION_FAILED,
				message: xhr.responseText,
				extraInfo,
			});
			return;
		}
		reject({
			type: Code.WEBIM_CONNCTION_AJAX_ERROR,
			data: xhr.responseText,
			message: '',
			extraInfo,
		});
		error({
			type: Code.WEBIM_CONNCTION_AJAX_ERROR,
			data: xhr.responseText,
			message: '',
			extraInfo,
		});
	} else {
		reject({
			type: Code.WEBIM_CONNCTION_AJAX_ERROR,
			data: xhr.responseText,
			message: xhr.responseText,
			extraInfo: {
				...extraInfo,
				errDesc: 'ajax error',
			},
		});
		error({
			type: Code.WEBIM_CONNCTION_AJAX_ERROR,
			data: xhr.responseText,
			message: xhr.responseText,
			extraInfo: {
				...extraInfo,
				errDesc: 'ajax error',
			},
		});
	}
}

export default handleError;
