/**
 * 获取联系人列表。
 *
 * ```typescript
 * connection.getContacts()
 * ```
 */
function getContacts(
	this: any,
	params?: {
		success?: (res: IndexTypes.RosterData[]) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<UserId[]>> {
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken } = this.context;

	const suc = (data: any) => {
		const friends: IndexTypes.RosterData[] = [];
		data.data.forEach((v: string) => {
			friends.push({
				name: v,
				subscription: 'both',
				jid: this.context.jid,
			});
		});
		typeof params?.success === 'function' && params.success(friends);
	};

	const errorCb = function (res: any) {
		typeof params?.error === 'function' && params.error(res);
	};

	const opts = {
		url:
			this.apiUrl +
			'/' +
			orgName +
			'/' +
			appName +
			'/users/' +
			this.user +
			'/contacts/users',
		dataType: 'json',
		type: 'GET',
		headers: {
			Authorization: 'Bearer ' + accessToken,
		},
		success: suc,
		error: errorCb,
	};
	logger.debug('Call getContacts');
	return ajax.call(this, opts, OperationName.GET_CONTACTS);
}

