/**
 * @hidden
 * @deprecated 已废弃。请用 {@link getServerConversations} 代替。
 *
 * 获取会话列表以及会话中的最新一条消息。
 *
 * ```typescript
 * connection.getConversationlist()
 * ```
 */
function getConversationlist(
	this: any,
	params?: {
		/** The current page number, starting from 1. */
		/** 当前页码，从 1 开始。 */
		pageNum?: number;
		/** The number of conversations that you expect to get on each page. The value cannot exceed 20. */
		/** 每页返回的会话数，最大值是 20。 */
		pageSize?: number;
		success?: (res: AsyncResult<IndexTypes.ConversationInfo>) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<IndexTypes.ConversationInfo>> {
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}

	const { orgName, appName, accessToken } = this.context;

	const isPaging = !!(
		params &&
		typeof params.pageNum === 'number' &&
		typeof params.pageSize === 'number' &&
		params.pageNum > 0 &&
		params.pageSize > 0
	);
	const url = `${this.apiUrl}/${orgName}/${appName}/users/${
		this.user
	}/user_channels${isPaging ? '/page' : ''}`;

	const opts = {
		url: url,
		type: 'GET',
		dataType: 'json',
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
		success: params && params?.success,
		error: params && params?.error,
	};
	if (isPaging) {
		(opts as any).data = {
			pageNum: params.pageNum,
			pageSize: params.pageSize,
		};
	}
	logger.debug('Call getConversationlist');
	return ajax.call(this, opts, OperationName.GET_SESSION_LIST).then((res) => {
		return formatLastMessage.call(this, res);
	});
}
function formatLastMessage(this: Connection, res: any) {
	const channelInfos = res.data.channel_infos;
	channelInfos?.forEach((item: any) => {
		if (item.meta && JSON.stringify(item.meta) !== '{}') {
			item.meta.payload = JSON.parse(item.meta.payload);
			item.lastMessage = formatMessage.call(this, item.meta, {
				formatCustomExts: false,
			});
		} else {
			item.lastMessage = item.meta;
		}
		delete item.meta;
	});
	return res;
}

