// 获取token的过期时间
function getTokenExpireTimestamp(this: any, token: string): Promise<any> {
	if (typeof token !== 'string' || token === '') {
		throw Error('Invalid parameter: "token"');
	}
	const { orgName, appName } = this.context;
	const opts = {
		url: `${this.apiUrl}/${orgName}/${appName}/sdk/users/${this.user}/token/expires`,
		type: 'GET',
		dataType: 'json',
		headers: {
			Authorization: 'Bearer ' + token,
			'Content-Type': 'application/json',
		},
	};
	logger.debug('Call getTokenExpireTimestamp', token);
	return ajax.call(this, opts, OperationName.SDK_INTERNAL);
}

