const removeUserFromBlocklist = removeFromBlackList;

/**
 * Recalls a message.
 *
 * ```typescript
 * connection.recallMessage({mid: 'messageId', to: 'user1', type: 'singleChat'})
 * ```
 */

/**
 * 撤回消息。
 *
 * ```typescript
 * connection.recallMessage({mid: 'messageId', to: 'user1', type: 'singleChat'})
 * ```
 */
function recallMessage(
	this: Connection,
	option: {
		/** The ID of the message to be recalled. */
		/** 需要撤回的消息 ID。 */
		mid: string;
		/** The recipient of the message. */
		/** 消息的接收方。 */
		to: UserId;
		/**
		 * The chat type for SDK (deprecated):
		 * - chat: one-to-one chat;
		 * - groupchat: group chat;
		 * - chatroom: chat room.
		 */
		/**
		 * 会话类型（SDK deprecated）:
		 * - `chat`：单聊；
		 * - `groupchat`：群组聊天；
		 * - `chatroom`：聊天室聊天。
		 */
		type?: 'chat' | 'groupchat' | 'chatroom'; // v3.0
		/**
		 * The chat type for SDK:
		 * - `singleChat`: one-to-one chat;
		 * - `groupChat`: group chat;
		 * - `chatRoom`: chat room.
		 */
		/**
		 * 会话类型：
		 * - `singleChat`：单聊；
		 * - `groupChat`：群组聊天；
		 * - `chatRoom`：聊天室聊天。
		 */
		chatType?: 'singleChat' | 'groupChat' | 'chatRoom'; // v4.0
		/** Whether the message is in the thread. */
		/** 是否是子区内的消息。 */
		isChatThread?: boolean;
		/** Custom extension fields. */
		/** 自定义扩展字端。 */
		ext?: string;
		success?: (res: IndexTypes.SendMsgResult) => void;
		fail?: (error: ErrorEvent) => void;
	}
): Promise<IndexTypes.SendMsgResult> {
	const metaExt = option?.ext || '';
	if (typeof metaExt !== 'string') {
		throw Error('"Invalid parameter": "ext"', metaExt);
	}
	const id = this.getUniqueId();
	const params = { id, to: option.to };
	this._msgHash[id] = { ...params };

	let chatType = '';
	if (typeof option.chatType !== 'undefined') {
		chatType = option.chatType;
	} else if (typeof option.type !== 'undefined') {
		chatType = option.type === 'chat' ? 'singleChat' : option.type;
	}
	const messageOption = {
		id: id,
		type: 'recall',
		chatType: chatType,
		ackId: option.mid,
		to: option.to,
		isChatThread: option.isChatThread || false,
		metaExt,
		success: async (res: any) => {
			try {
				if (chatType === 'singleChat' || chatType === 'groupChat') {
					await this._localCache
						?.getInstance()
						?.removeMsgByServerMsgId(option.mid);
					const res = await this._localCache
						?.getInstance()
						?.getConversationLastMessage(option.to, chatType);

					await this._localCache
						?.getInstance()
						?.updateLocalConversation(
							getSessionId({
								conversationId: option.to,
								conversationType: chatType,
							}),
							{
								lastMessageId: res?.serverMsgId,
							}
						);
				}
				option?.success?.(res);
			} catch (error) {
				option?.success?.(res);
			}
		},
		fail: option.fail,
	};
	logger.debug('Call recallMessage', option);
	return this.mSync.send(messageOption, this);
}

/**
 * Modifies a message on the server.
 *
 * This method can only modify a text message in one-to-one chats or group chats, but not in chat rooms.
 *
 * Upon a message modification, the callback `onModifiedMessage` will be received by the message recipient(s) and in multi-device login scenarios.
 *
 * ```typescript
 *
 * const textMessage = WebIM.message.create({
 *   type: "txt",
 *   msg: "message content",
 *   to: "username",
 *   chatType: "singleChat",
 * });
 *
 * connection.modifyMessage({ messageId: 'messageId', message: textMessage })
 * ```
 */

/**
 * 修改服务端的消息内容。
 *
 * 调用该方法只能修改单聊和群聊中的文本消息，不能修改聊天室消息。
 *
 * 多设备和该消息的接收方可通过 `onModifiedMessage` 回调监听消息是否有变更。
 *
 * ```typescript
 *
 * const textMessage = WebIM.message.create({
 *   type: "txt",
 *   msg: "message content",
 *   to: "username",
 *   chatType: "singleChat",
 * });
 *
 * connection.modifyMessage({ messageId: 'messageId', message: textMessage })
 * ```
 */

function modifyMessage(
	this: any,
	option: {
		/** The ID of the message to modify.*/
		/** 需要修改的消息 ID。 */
		messageId: string;
		/** The modified message.*/
		/** 内容修改后的消息体。 */
		modifiedMessage: ModifiedMsg;
	}
): Promise<IndexTypes.ModifyMsgResult> {
	const { messageId, modifiedMessage } = option || {};
	logger.debug('Call modifyMessage', messageId, modifiedMessage);
	if (messageId === '') {
		throw Error('Invalid parameter: "messageId"');
	}
	return this.mSync.send({
		editMessageId: messageId,
		...modifiedMessage,
	});
}

