/**
 * Sets whether to pin a conversation.
 *
 * ```typescript
 * connection.pinConversation({conversationId:'conversationId',conversationType: 'singleChat', isPinned: boolean})
 * ```
 */

/**
 * 设置是否置顶会话。
 *
 * ```typescript
 * connection.pinConversation({conversationId:'conversationId', conversationType: 'singleChat', isPinned: boolean})
 * ```
 */
async function pinConversation(
	this: any,
	params: {
		/** The conversation ID. */
		/** 会话 ID。 */
		conversationId: string;
		/** The conversation type. */
		/** 会话类型。 */
		conversationType: 'singleChat' | 'groupChat';
		/** Whether to pin the conversation:
		 * - `true`: Yes.
		 * - `false`: No. The conversation is unpinned.
		 */
		/** 是否置顶会话：
		 * -`true`：置顶会话。
		 * - `false`：取消置顶会话。
		 */
		isPinned: boolean;
	}
): Promise<AsyncResult<IndexTypes.PinConversation>> {
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	const { conversationId, conversationType, isPinned } = params;
	const type = conversationType === 'singleChat' ? 'chat' : 'groupChat';
	if (typeof conversationId !== 'string' || conversationId === '') {
		throw Error(`Invalid parameter conversationId: ${conversationId}`);
	} else if (!['singleChat', 'groupChat'].includes(conversationType)) {
		throw Error(`Invalid parameter conversationType: ${conversationType}`);
	} else if (typeof isPinned !== 'boolean') {
		throw Error(`Invalid parameter isPinned: ${isPinned}`);
	}
	const { orgName, appName, accessToken, jid } = this.context;
	const requestData = {
		type,
		to: conversationId,
	};
	const paramsUrl = isPinned ? '' : `type=${type}&to=${conversationId}&`;
	const url = `${this.apiUrl}/${orgName}/${appName}/sdk/user/${this.user}/user_channel/top?${paramsUrl}resource=${jid.clientResource}`;
	const opts: any = {
		url: url,
		type: isPinned ? 'POST' : 'DELETE',
		dataType: 'json',
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
	};
	if (isPinned) {
		opts.data = JSON.stringify(requestData);
	}
	logger.debug('Call pinConversation', params);
	return ajax.call(this, opts, OperationName.PIN_CONVERSATION).then((res) => {
		//处理服务端数据
		const { type, data } = res;
		const resData = {
			isPinned: data.is_top || false,
			pinnedTime: data.is_top ? data.update_top_status_time : 0,
		};
		return {
			type,
			data: resData,
		};
	});
}

