async function markConversation(
	this: Connection,
	params: {
		/** The conversation list that needs to be marked. */
		/** 需要添加标记的会话数组。*/
		conversations: Array<
			Pick<
				IndexTypes.ConversationItem,
				'conversationId' | 'conversationType'
			>
		>;
		/** Conversation mark. */
		/** 会话标记。*/
		mark: IndexTypes.MarkType;
		/** Add or remove mark. */
		/** 添加或移除标记。*/
		isMarked: boolean;
	}
): Promise<void> {
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	logger.debug('Call markConversation', params);
	const { conversations = [], mark, isMarked } = params || {};
	if (!Array.isArray(conversations)) {
		throw Error('Invalid parameter conversations');
	}
	const targets = conversations.map((conversation) => {
		if (
			!conversation.conversationId ||
			!['singleChat', 'groupChat'].includes(conversation.conversationType)
		) {
			throw Error('Invalid parameter conversations');
		}
		return {
			to: conversation.conversationId,
			type: REST_CHAT_TYPE_MAP[conversation.conversationType],
		};
	});

	if (typeof isMarked !== 'boolean') {
		throw Error(`Invalid parameter isMarked: ${isMarked}`);
	}

	if (typeof mark !== 'number') {
		throw Error(`Invalid parameter mark: ${mark}`);
	}

	const { orgName, appName, accessToken, userId, jid } = this.context;
	const requestData = {
		mark: IndexTypes.MarkType[mark],
		targets,
	};
	const opts = {
		url: `${this.apiUrl}/${orgName}/${appName}/sdk/user/${userId}/user_channels/mark?resource=${jid.clientResource}`,
		dataType: 'json',
		data: JSON.stringify(requestData),
		type: isMarked ? 'Post' : 'Delete',
		headers: {
			Authorization: 'Bearer ' + accessToken,
		},
	};
	await ajax.call(this, opts, OperationName.MARK_CONVERSATION).then((res) => {
		const ignoreList = res.data?.ignore || [];
		// 判断是否有ignore会话,有则打印log
		if (ignoreList) {
			if (Array.isArray(ignoreList) && ignoreList.length > 0) {
				logger.debug(
					'markConversation has ignored conversations',
					ignoreList.map((ignoreItem) => ({
						conversationId: ignoreItem.to,
						conversationType:
							CHAT_TYPE_MAP[ignoreItem.type as ChatType],
					}))
				);
			}
		}
	});
}
