export type NewMessageParamters =
	| ReadParameters
	| DeliveryParameters
	| ChannelParameters
	| TextParameters
	| CmdParameters
	| CustomParameters
	| LocationParameters
	| ImgParameters
	| AudioParameters
	| VideoParameters
	| CombineParameters
	| FileParameters;

export type MessageSetParameters =
	| ReadMsgSetParameters
	| DeliveryMsgSetParameters
	| ChannelMsgSetParameters
	| TextMsgSetParameters
	| CmdMsgSetParameters
	| CustomMsgSetParameters
	| LocationMsgSetParameters
	| ImgMsgSetParameters
	| AudioMsgSetParameters
	| VideoMsgSetParameters
	| FileMsgSetParameters;

export type CreateMsgType =
	| CreateTextMsgParameters
	| CreateImgMsgParameters
	| CreateCmdMsgParameters
	| CreateFileMsgParameters
	| CreateVideoMsgParameters
	| CreateCustomMsgParameters
	| CreateLocationMsgParameters
	| CreateChannelMsgParameters
	| CreateDeliveryMsgParameters
	| CreateReadMsgParameters
	| CreateCombineMsgParameters
	| CreateAudioMsgParameters;

export type MessageBody =
	| ReadMsgBody
	| DeliveryMsgBody
	| ChannelMsgBody
	| TextMsgBody
	| CmdMsgBody
	| CustomMsgBody
	| LocationMsgBody
	| ImgMsgBody
	| AudioMsgBody
	| VideoMsgBody
	| CombineMsgBody
	| FileMsgBody;

function isImg(params: CreateMsgType): params is CreateImgMsgParameters {
	return params.type === 'img' && !('version' in params);
}
function isTxt(params: CreateMsgType): params is CreateTextMsgParameters {
	return params.type === 'txt' && !('version' in params);
}
function isCmd(params: CreateMsgType): params is CreateCmdMsgParameters {
	return params.type === 'cmd' && !('version' in params);
}
function isFile(params: CreateMsgType): params is CreateFileMsgParameters {
	return params.type === 'file' && !('version' in params);
}
function isAudio(params: CreateMsgType): params is CreateAudioMsgParameters {
	return params.type === 'audio' && !('version' in params);
}
function isVideo(params: CreateMsgType): params is CreateVideoMsgParameters {
	return params.type === 'video' && !('version' in params);
}
function isCustom(params: CreateMsgType): params is CreateCustomMsgParameters {
	return params.type === 'custom' && !('version' in params);
}
function isLocation(
	params: CreateMsgType
): params is CreateLocationMsgParameters {
	return params.type === 'loc' && !('version' in params);
}
function isChannel(
	params: CreateMsgType
): params is CreateChannelMsgParameters {
	return params.type === 'channel' && !('version' in params);
}
function isDelivery(
	params: CreateMsgType
): params is CreateDeliveryMsgParameters {
	return params.type === 'delivery' && !('version' in params);
}
function isRead(params: CreateMsgType): params is CreateReadMsgParameters {
	return params.type === 'read' && !('version' in params);
}
function isCombine(
	params: CreateMsgType
): params is CreateCombineMsgParameters {
	return params.type === 'combine' && !('version' in params);
}
class Message {
	id: string;
	body: any;
	type: MessageType;
	constructor(type: MessageType, id?: string) {
		this.type = type;
		this.id = id || utils.getUniqueId();
		return Message.createOldMsg({
			type: type,
			id: this.id,
		}) as unknown as Message;
	}
	/** @hidden */
	static createOldMsg(options: NewMessageParamters): any {
		switch (options.type) {
			case 'read':
				return new Read({ type: 'read', id: options.id });
			case 'delivery':
				return new Delivery({ type: 'delivery', id: options.id });
			case 'channel':
				return new Channel({ type: 'channel', id: options.id });
			case 'txt':
				return new Text({ type: 'txt', id: options.id });
			case 'cmd':
				return new Cmd({ type: 'cmd', id: options.id });
			case 'custom':
				return new Custom({ type: 'custom', id: options.id });
			case 'loc':
				return new Location({ type: 'loc', id: options.id });
			case 'img':
				return new Img({ type: 'img', id: options.id });
			case 'audio':
				return new Audio({ type: 'audio', id: options.id });
			case 'video':
				return new Video({ type: 'video', id: options.id });
			case 'file':
				return new FileMsg({ type: 'file', id: options.id });
			default:
				break;
			//return { id: options.id, type: options.type } as unknown as MessageClasses
		}
	}
	/** Creates a message. */
	/** 创建消息。*/
	static create(options: CreateMsgType): MessageBody {
		if (isTxt(options)) {
			return Text.create(options);
		} else if (isImg(options)) {
			return Img.create(options);
		} else if (isCmd(options)) {
			return Cmd.create(options);
		} else if (isFile(options)) {
			return FileMsg.create(options);
		} else if (isAudio(options)) {
			return Audio.create(options);
		} else if (isVideo(options)) {
			return Video.create(options);
		} else if (isCustom(options)) {
			return Custom.create(options);
		} else if (isLocation(options)) {
			return Location.create(options);
		} else if (isChannel(options)) {
			return Channel.create(options);
		} else if (isDelivery(options)) {
			return Delivery.create(options);
		} else if (isRead(options)) {
			return Read.create(options);
		} else if (isCombine(options)) {
			return Combine.create(options);
		}
		return {} as MessageBody;
	}
	/** When sending attachment messages, use this method to obtain the file path. */
	/** 发送附件消息时，使用这个方法获取文件路径。 */
	static getFileUrl: (fileInputId: string | HTMLInputElement) => FileObj =
		utils.getFileUrl;
	/** Download file. */
	/** 下载文件。 */
	static download: (params: DownloadParams) => void = utils.download;
	/** Convert the downloaded object into an object URL. */
	/** 将下载下来的对象转换成 object URL。 */
	static parseDownloadResponse = utils.parseDownloadResponse;

	/**
	 * @hidden
	 * Set the message body.
	 */

	/**
	 * @hidden
	 * 设置消息体。
	 */
	set(options: MessageSetParameters): void {
		// do nothing
	}
}

export { Message };
