/**
 *
 * The log module of the SDK.
 * @module Logger
 */
import { traceForIE, isIE } from './util';
import { utils, ajax } from '../utils';
const emptyFun = function emptyFun() {
	// do nothing
};
const _loggersByName: { [key: string]: any } = {};

export type DefaultLevel =
	| 'TRACE'
	| 'DEBUG'
	| 'INFO'
	| 'WARN'
	| 'ERROR'
	| 'SILENT';
export type LevelNum = 0 | 1 | 2 | 3 | 4 | 5;
export type Level = DefaultLevel | LevelNum;

export enum Levels {
	'TRACE' = 0,
	'DEBUG' = 1,
	'INFO' = 2,
	'WARN' = 3,
	'ERROR' = 4,
	'SILENT' = 5,
}
export interface Config {
	/** Whether to enable log caching. -`true`: Enable; - (Default)`false`: Do not enable. */
	/** 是否启用缓存日志。-`true`：开启；-（默认）`false`：关闭。*/
	useCache: boolean;
	/** The maximum number of bytes for cached logs. */
	/** 缓存日志最大字节数。 */
	maxCache: number;
	/** The log color. */
	/** 输出日志颜色。 */
	color?: string;
	/** The output log background color. */
	/** 输出日志背景颜色。 */
	background?: string;
}

export class Logger {
	/** The logger name. */
	/** logger 名字。 */
	name: string;
	/** The current log level. */
	/** 当前日志等级。 */
	currentLevel: LevelNum;
	/** @hidden */
	useCookiePersist: boolean;
	/** The log registration key in local storage. */
	/** 在 local storage 中存储日志登记的 key。 */
	storageLogLevelKey: string;
	/** The log levels. */
	/** 可选的日志等级。 */
	levels;
	/** @hidden */
	logMethods: string[];
	/** @hidden */
	methodFactory: any;
	/** The cached logs. */
	/** 缓存的日志。 */
	logs: string[];
	/** The logger configuration.  */
	/** logger 配置。 */
	config: Config;
	/** The number of bytes of cached logs. */
	/** 缓存的日志字节长度。 */
	logBytes: number;
	/** Whether output logs in the console. */
	/** 是否在控制台输出Log。*/
	consoleLogVisibility: boolean;
	/** The logs callback function. */
	/** 日志回调函数。 */
	onLog?: (data: {
		/** The time when logs are printed. */
		/** 打印日志时间。 */
		time: string;
		/** The log level. */
		/** 日志等级。*/
		level: string;
		/** The log content. */
		/** 日志内容。 */
		logs: any[];
	}) => void;
	// eslint-disable-next-line no-undef
	[key: string]: any;
	/** @hidden */
	report?: boolean; // 是否上报日志
	/** @hidden */
	reportLogs: string[]; // 上报日志
	reportInterval?: number; // 上报日志间隔
	constructor(name?: string, defaultLevel?: Level, factory?: any) {
		this.name = name || 'defaultLogger';
		this.currentLevel = 0;
		this.useCookiePersist = false; // 使用cookie 存储 storageLogLevelKey
		this.storageLogLevelKey = 'loglevel';
		this.levels = Levels;
		this.consoleLogVisibility = true;
		this.logMethods = ['trace', 'debug', 'info', 'warn', 'error'];
		this.methodFactory = factory || this.defaultMethodFactory;
		this.report = false;
		// Initialize with the right level
		let initialLevel = this._getPersistedLevel();
		if (initialLevel === null || initialLevel === undefined) {
			initialLevel = defaultLevel === null ? 'WARN' : defaultLevel;
		}

		this.logs = [];
		this.reportLogs = [];
		this.reportInterval = 1000 * 60 * 5; // 5分钟上报一次
		this.timer = null;
		this.config = {
			useCache: false,
			maxCache: 3 * 1024 * 1024,
			color: '',
			background: '',
		};

		this.logBytes = 0;
		this.setLevel(initialLevel, false, '');
	}

	async reportLog() {
		if (this.reportLogs?.length === 0) {
			return;
		}

		const MAX_LENGTH = 2 * 1024 * 1024;
		let logData = this.reportLogs.join('\n') + '\n';

		let logDataLength = logData.length;
		const logContents: string[] = [];

		while (logDataLength > MAX_LENGTH) {
			logContents.push(logData.substring(0, MAX_LENGTH));
			logDataLength = logDataLength - MAX_LENGTH;
			logData = logData.substring(MAX_LENGTH);
		}

		logContents.push(logData);
		for (const logContent of logContents) {
			this.reportLogs = [];
			if (!this.connection?.context?.accessToken) {
				return;
			}
			try {
				const response = await this.reportData(logContent);
				if (response?.status !== 'ok') {
					this.reportLogs.unshift(logContent);
				}
			} catch (e) {
				this.reportLogs.unshift(logContent);
			}

			// 两次上传间隔太近，服务端生成日志会出问题
			await utils.delay(3000);
		}
	}

	private reportData(logContent: string) {
		if (!this.connection) {
			this.error('report log error', 'connection is null');
			return;
		}
		const context = this.connection.context || {};
		const { orgName, appName, accessToken, userId } = context;
		if (!orgName || !appName) {
			return;
		}
		const opts = {
			url: `${this.connection?.apiUrl}/${orgName}/${appName}/sdk/users/${userId}/client/logs`,
			type: 'POST',
			data: JSON.stringify({
				resource:
					this.connection.clientResource || `random_${Date.now()}`,
				logContent: logContent,
			}),
			dataType: 'json',
			headers: {
				Authorization: 'Bearer ' + accessToken,
				'Content-Type': 'application/json',
			},
		};

		return ajax
			.call(this.connection, opts)
			.then((res) => {
				this.log('report log success', res);
				return res;
			})
			.catch((err) => {
				this.error('report log error', err);
			});
	}

	_regularlyReportLogs() {
		this.timer && clearInterval(this.timer);
		(this.reportInterval ?? 0) < 1000 * 60 &&
			(this.reportInterval = 1000 * 60);
		this.timer = setInterval(() => {
			this.reportLog();
		}, this.reportInterval || 1000 * 60 * 5);
	}
	async _stopReportLogs() {
		try {
			await this.reportLog();
		} catch (e) {
			this.error('report log error when stopping upload');
		}

		this.reportLogs = [];
		clearInterval(this.timer);
	}
	/** set Logger config. */
	/** 设置Logger配置。 */
	setConfig(
		/** The logger configuration.  */
		/** logger 配置。 */
		config: Config
	): void {
		this.config = config;
	}

	/** Gets the current log level. */
	/** 获取当前日志等级。 */
	getLevel(): LevelNum {
		return this.currentLevel;
	}

	/** Sets the log level. The value range is 0-5. */
	/** 设置日志等级，可选范围：0-5。*/
	setLevel(level: Level, persist?: boolean, name?: string): void {
		if (typeof level === 'string') {
			level = Levels[level];
		}
		if (typeof level === 'undefined') {
			// return console.error('level', level)
			level = 0;
		}
		if (
			typeof level === 'number' &&
			level >= 0 &&
			level <= this.levels.SILENT
		) {
			this.currentLevel = level;
			if (persist !== false) {
				this._persistLevel(level);
			}
			this.replaceLoggingMethods(level, name || '');
			if (typeof console === 'undefined' && level < Levels.SILENT) {
				throw Error('No console available for logging');
			}
		} else {
			throw Error('log.setLevel() called with invalid level: ' + level);
		}
	}

	/** @hidden */
	setDefaultLevel(level: Level) {
		if (!this._getPersistedLevel()) {
			this.setLevel(level, false, '');
		}
	}

	/** Enables the output of all levels of logs. */
	/** 输出全部等级的日志。 */
	enableAll(
		/** Whether to persist the level of logs. - (Default)`true`: Yes, so you will get the logs of the remembered level next time; - `false`: No.*/
		/** 是否记住日志等级。-（默认）`true`：记住后下次可以直接输出记住的等级的日志； - `false`：不记住。 */
		persist?: boolean
	): void {
		this.setLevel(this.levels.TRACE, (persist = true), '');
	}

	/** Disables the output of all levels of logs. */
	/** 禁止全部等级的日志输出。 */
	disableAll(
		/** Whether to persist the level of logs. - (Default)`true`: Yes; - `false`: No.*/
		/** 是否记住日志等级。-（默认）`true`：记住； - `false`：不记住。 */
		persist?: boolean
	): void {
		this.setLevel(this.levels.SILENT, (persist = true), '');
	}

	/** Gets cached logs. */
	/** 获取缓存日志。 */
	getLogs(): string[] {
		return this.logs;
	}
	/** Downloads logs. */
	/** 下载日志文件。 */
	download(): void {
		if (typeof window !== 'undefined' && typeof document !== 'undefined') {
			const logData = this.getLogs().join('\n');
			const blob = new Blob([logData], {
				type: 'text/plain;charset=UTF-8',
			});
			const url = window.URL.createObjectURL(blob);
			// var postdata = new FormData();
			const link = document.createElement('a');
			link.style.display = 'none';
			link.href = url;
			link.setAttribute('download', 'sdklog');
			document.body.appendChild(link);
			link.click();
		}
	}

	/** Sets whether to output logs to the console. */
	/** 设置日志是否输出到控制台。 */
	setConsoleLogVisibility(visible: boolean) {
		this.consoleLogVisibility = visible;
	}

	// Cross-browser bind equivalent that works at least back to IE6
	/** @hidden */
	_bindMethod(obj: any, methodName: string, useCache: boolean) {
		const method = obj[methodName];
		const time = this.getTime();
		if (useCache) {
			return this._cacheLog;
		} else if (typeof method.bind === 'function') {
			return (...arg: any) => {
				const time = this.getTime();
				this.consoleLogVisibility &&
					method.call(
						obj,
						`${time} IM SDK [${
							methodName === 'log' ? 'debug' : methodName
						}]: `,
						...arg
					);
				this.onLog &&
					this.onLog({
						time,
						level: methodName === 'log' ? 'debug' : methodName,
						logs: arg,
					});
				this._cacheReportLogs(...arg);
			};
		} else {
			try {
				return Function.prototype.bind.call(
					method,
					obj,
					`${time} IM SDK [${
						methodName === 'log' ? 'debug' : methodName
					}]: `
				);
			} catch (e) {
				// Missing bind shim or IE8 + Modernizr, fallback to wrapping
				return function () {
					return Function.prototype.apply.apply(method, [
						obj,
						// eslint-disable-next-line prefer-rest-params
						arguments,
					]);
				};
			}
		}
	}
	/** @hidden */
	getTime() {
		const date = new Date();
		return date.toTimeString().split(' ')[0] + ':' + date.getMilliseconds();
	}
	/** @hidden */
	_cacheLog(...rest: any[]) {
		const time = new Date().toLocaleString() + ': ';
		let stringLog = '';
		rest.forEach((item) => {
			if (typeof item === 'object') {
				stringLog += JSON.stringify(item) + ' ';
			} else {
				stringLog += item + ' ';
			}
		});
		this._cacheLogCall(time + stringLog);
		this._cacheReportLogs(...rest);
		// console.log( time, ...rest);
	}
	/** @hidden */
	_cacheLogCall(log: any) {
		const logB = log.length;
		const futureB = this.logBytes + logB;
		const maxCache = this.config.maxCache;

		if (logB >= maxCache) {
			return;
		}

		if (futureB < maxCache) {
			this.logBytes += logB;
		} else {
			const midB = futureB - maxCache;
			let clearOutB = 0;
			while (clearOutB < midB) {
				const clearOutLog = this.logs.shift();
				if (typeof clearOutLog !== 'undefined') {
					clearOutB += clearOutLog.length;
				}
			}
		}
		this.logs.push(log);
	}
	/** @hidden */
	_cacheReportLogs(...rest: any[]) {
		if (this.report) {
			const time = new Date().toLocaleString() + ': ';
			let stringLog = '';
			rest.forEach((item) => {
				if (typeof item === 'object') {
					stringLog += JSON.stringify(item) + ' ';
				} else {
					stringLog += item + ' ';
				}
			});

			this.reportLogs.push(time + stringLog);
		}
	}

	// get local stored level
	/** @hidden */
	_getPersistedLevel() {
		let storedLevel;
		if (typeof window === 'undefined') {
			return 5;
			// return console.warn('this lib can run in browser only!')
		}

		storedLevel =
			window &&
			window.localStorage &&
			window.localStorage[this.storageLogLevelKey];

		if (storedLevel === 'undefined') {
			const cookie = window.document.cookie;
			const location = cookie.indexOf(
				encodeURIComponent(this.storageLogLevelKey)
			);
			if (location !== -1) {
				storedLevel = (
					/^([^;]+)/.exec(cookie.slice(location)) as any[]
				)[1];
			}
		}

		return storedLevel || 5;
	}

	// Store level persistently in browser
	/** @hidden */
	_persistLevel(levelNum: number) {
		const levelName = this.logMethods[levelNum] || 'SILENT';
		if (typeof window === 'undefined') {
			return; // console.warn('this lib can run in browser only!')
		}
		if (window.localStorage) {
			try {
				window.localStorage[this.storageLogLevelKey] = levelName;
			} catch (e) {
				console.log(e);
			}
		}

		if (this.useCookiePersist) {
			window.document.cookie =
				encodeURIComponent(this.storageLogLevelKey) +
				'=' +
				levelName +
				';';
		}
	}

	/** @hidden */
	replaceLoggingMethods(level: LevelNum, loggerName: string) {
		for (let i = 0; i < this.logMethods.length; i++) {
			const methodName = this.logMethods[i];
			this[methodName] =
				i < level
					? (...arg: any[]) => {
							this.report && this._cacheReportLogs(...arg);
					  }
					: this.methodFactory(methodName, level, loggerName);
		}

		// Define log.log as an alias for log.debug
		(this as any).log = (this as any).debug;
	}

	// By default, we use closely bound real methods wherever possible, and
	// otherwise we wait for a console to appear, and then try again.
	/** @hidden */
	defaultMethodFactory(
		methodName: string,
		level: LevelNum,
		loggerName: string
	) {
		return (
			this.realMethod(methodName) ||
			this.enableLoggingWhenConsoleArrives.apply(this, [
				methodName,
				level,
				loggerName,
			])
		);
	}

	// Build the best logging method possible for this env
	// Wherever possible we want to bind, not wrap, to preserve stack traces
	/** @hidden */
	realMethod(methodName: string) {
		if (methodName === 'debug') {
			methodName = 'log';
		}

		if (typeof console === 'undefined') {
			return false; // No method possible, for now - fixed later by enableLoggingWhenConsoleArrives
		} else if (methodName === 'trace' && isIE) {
			return traceForIE;
		} else if (typeof (console as any)[methodName] !== 'undefined') {
			return this._bindMethod(console, methodName, this.config.useCache);
		} else if (typeof console.log !== 'undefined') {
			return this._bindMethod(console, 'log', this.config.useCache);
		} else {
			return emptyFun;
		}
	}

	// In old IE versions, the console isn't present until you first open it.
	// We build realMethod() replacements here that regenerate logging methods
	/** @hidden */
	enableLoggingWhenConsoleArrives(
		methodName: string,
		level: LevelNum,
		loggerName: string
	) {
		return function (this: Logger) {
			if (typeof console !== 'undefined') {
				this.replaceLoggingMethods.call(this, level, loggerName);
				// eslint-disable-next-line prefer-rest-params
				(this as any)[methodName].apply(this, arguments);
			}
		}.bind(this);
	}
}

const defaultLogger = new Logger();

defaultLogger.getLogger = function (name: string) {
	if (typeof name !== 'string' || name === '') {
		throw new TypeError('You must supply a name when creating a logger.');
	}
	return this;
	// let logger = _loggersByName[name] as any;
	// if (!logger) {
	// 	logger = _loggersByName[name] = new Logger(
	// 		name,
	// 		defaultLogger.getLevel(),
	// 		defaultLogger.methodFactory
	// 	);
	// }

	// return logger;
};

// Grab the current global log variable in case of overwrite
const _log = typeof window !== 'undefined' ? (window as any).log : undefined;

defaultLogger.noConflict = function () {
	if (
		typeof window !== 'undefined' &&
		(window as any).log === defaultLogger
	) {
		(window as any).log = _log;
	}

	return defaultLogger;
};

defaultLogger.getLoggers = function getLoggers() {
	return _loggersByName;
};

defaultLogger.initReport = function (param: {
	report: boolean;
	reportInterval: number;
	connection: any;
}) {
	const { report, reportInterval, connection } = param;
	defaultLogger.report = report;
	defaultLogger.reportInterval = reportInterval;
	defaultLogger.connection = connection;
	if (report) {
		defaultLogger._regularlyReportLogs();
	}
};

const logger = defaultLogger;
export { logger };
