/* eslint-disable @typescript-eslint/no-empty-function */
import { TRACE_CONFIG } from './const';
import { ReqData, EnvInfo, utils } from '../../../utils';
import { isMiniProgram } from '../../utils';
import { UPLOAD_TIMEOUT } from '../../../constant';

let CURRENT_ENV = {} as EnvInfo;

type EventBody = {
	eventId: number;
	body: {
		[key: string]: any;
	};
};

export const request = (
	token: string,
	params: {
		biz: string;
		appId: string;
		data: Array<EventBody>;
		debug?: boolean;
		onSuccess?: () => void;
	}
) => {
	try {
		const { biz, appId, data, debug, onSuccess } = params;
		if (!biz) {
			throw new Error('biz is not defined');
		}
		if (!CURRENT_ENV.global) {
			CURRENT_ENV = utils.getEnvInfo();
		}
		const platForm = CURRENT_ENV;
		// 如果不是小程序
		if (!isMiniProgram(platForm)) {
			const xhr = new XMLHttpRequest();
			xhr.onreadystatechange = () => {
				if (xhr.readyState === 2) {
					onSuccess?.();
				}
			};
			xhr.open('POST', debug ? TRACE_CONFIG.debugUrl : TRACE_CONFIG.url);

			xhr.setRequestHeader('Content-Type', 'application/json');
			xhr.setRequestHeader('token', token);
			xhr.setRequestHeader('appid', appId ?? TRACE_CONFIG.appid);
			xhr.setRequestHeader(
				'sendts',
				`${Math.floor(new Date().getTime() / 1000)}`
			);
			xhr.setRequestHeader('biz', biz);
			xhr.setRequestHeader('debug', `${debug}`);

			xhr.send(JSON.stringify(data));
		} else {
			const reqData: ReqData = {
				url: debug ? TRACE_CONFIG.debugUrl : TRACE_CONFIG.url,
				data: data,
				method: 'POST',
				timeout: UPLOAD_TIMEOUT,
				success: function () {
					onSuccess?.();
				},
				fail: function () {},
				complete: function () {},
			};
			const headers = {
				token: token,
				appid: appId ?? TRACE_CONFIG.appid,
				sendts: `${Math.floor(new Date().getTime() / 1000)}`,
				biz: biz,
				debug: `${debug}`,
			};
			if (platForm.platform === 'zfb' || platForm.platform === 'dd') {
				reqData.headers = headers;
			} else {
				reqData.header = headers;
			}
			if (platForm.platform === 'dd') {
				return platForm.global.httpRequest(reqData);
			}

			platForm.global.request(reqData);
		}
	} catch (err) {
		console.error(err);
	}
};
