import { ChatType, MessagePriority } from '../types/message';
import { utils } from '../utils/index';
export interface ChannelMsgSetParameters {
	/** The session type. */
	/** 会话类型。 */
	chatType: ChatType;
	/** The receipt. */
	/** 消息接收方。 */
	to: string;
	/** The sender, which can only be the current user and can not be changed. */
	/** 消息发送方，只能是当前用户，无法修改。*/
	from?: string;
}

export interface ChannelMsgBody extends ChannelMsgSetParameters {
	/** The message ID. */
	/** 消息 ID。 */
	id: string;
	/** Whether it's group chat. */
	/** 是否是群聊。 */
	group?: string;
	/** The message type. */
	/** 消息类型。 */
	type: 'channel';
	/** Time. */
	/** 时间。*/
	time: number;
	/** Whether the message is a thread message. */
	/** 消息是否为子区消息 */
	isChatThread?: boolean;
	/** Message priority. */
	/** 消息优先级 */
	priority?: MessagePriority;
	/** Whether the message is delivered only when the recipient(s) is/are online:
	 *  - `true`: The message is delivered only when the recipient(s) is/are online. If the recipient is offline, the message is discarded.
	 *  - (Default) `false`: The message is delivered when the recipient(s) is/are online. If the recipient(s) is/are offline, the message will not be delivered to them until they get online.
	 */
	/** 消息是否只投递给在线用户：
	 * - `true`: 只有消息接收方在线时才能投递成功。若接收方离线，则消息会被丢弃。
	 * - （默认）`false`: 如果用户在线，则直接投递；如果用户离线，消息会在用户上线时投递。
	 */
	deliverOnlineOnly?: boolean;
}
export interface ChannelParameters {
	/** The message type. */
	/** 消息类型。 */
	type: 'channel';
	/** The message ID. */
	/** 消息 ID。 */
	id: string;
}

export interface CreateChannelMsgParameters {
	/** The message type. */
	/** 消息类型。 */
	type: 'channel';
	/** The session type. */
	/** 会话类型。 */
	chatType: ChatType;
	/** The receipt. */
	/** 消息接收方。 */
	to: string;
	/** The sender, which can only be the current user and can not be changed. */
	/** 消息发送方，只能是当前用户，无法修改。*/
	from?: string;
	/** Whether the message is a thread message. */
	/** 消息是否为子区消息 */
	isChatThread?: boolean;
	/** Message priority. */
	/** 消息优先级 */
	priority?: MessagePriority;
	/** Whether the message is delivered only when the recipient(s) is/are online:
	 *  - `true`: The message is delivered only when the recipient(s) is/are online. If the recipient is offline, the message is discarded.
	 *  - (Default) `false`: The message is delivered when the recipient(s) is/are online. If the recipient(s) is/are offline, the message will not be delivered to them until they get online.
	 */
	/** 消息是否只投递给在线用户：
	 * - `true`: 只有消息接收方在线时才能投递成功。若接收方离线，则消息会被丢弃。
	 * - （默认）`false`: 如果用户在线，则直接投递；如果用户离线，消息会在用户上线时投递。
	 */
	deliverOnlineOnly?: boolean;
}

export class Channel {
	id: string;
	type: 'channel';
	body?: ChannelMsgBody;
	constructor(parameters: ChannelParameters) {
		const type = parameters.type;
		const id = parameters.id;
		this.id = id;
		this.type = type;
	}
	/**
	 * @hidden
	 */
	set(options: ChannelMsgSetParameters) {
		this.body = {
			id: this.id,
			chatType: options.chatType || 'singleChat',
			type: 'channel',
			to: options.to,
			from: options.from || '',
			time: Date.now(),
		};
	}
	/**
	 * @hidden
	 */
	setChatType(chatType: ChatType): boolean {
		if (this.body) {
			this.body.chatType = chatType;
			return true;
		}
		return false;
	}
	// 3.0 api, will Deprecate in version 4.0
	/**
	 * @hidden
	 */
	setGroup(group: string): boolean {
		if (this.body) {
			this.body.group = group;
			return true;
		}
		return false;
	}
	static create(options: CreateChannelMsgParameters): ChannelMsgBody {
		return {
			id: utils.getUniqueId(),
			type: 'channel',
			chatType: options.chatType || 'singleChat',
			to: options.to,
			from: options.from || '',
			time: Date.now(),
			isChatThread: options.isChatThread,
			priority: options.priority,
			deliverOnlineOnly: options.deliverOnlineOnly,
		};
	}
}

