// type RequestMethods = 'GET' | 'POST' | 'PUT' | 'DELETE' | '';
type RequestMethods = string | undefined;

type LastApi = 1 | 0;

enum NetworkType {
	'5G' = 7,
	'4G' = 7,
	'3G' = 7,
	'2G' = 7,
	'SLOW-2G' = 7,
	'WIFI' = 2,
	'LAN' = 1, // 局域网
	'DISCONNECTED' = 0,
	'NONE' = 0,
	'UNKNOWN' = -1,
	'WEBIM UNABLE TO GET' = -2,
}

interface ApiEventData {
	operateId?: string; // 操作id
	operationName?: string; // 操作名称
	uid?: string; // uid
	code?: number; // httpCode
	codeDesc?: string; // 失败原因
	requestMethod?: RequestMethods; // 请求方法 GET || POST
	isLastApi?: LastApi; // 1 表示是最后1个API, 0 为其他
	requestUrl?: string; // 请求接口地址
	requestName?: string; // 请求名称
	requestElapse?: number; // 请求耗时
	subrequestid?: string; // 接口重试次数
	isSuccess?: number; // 是否成功 0: 失败 1: 成功
	msgId?: string | number;
	accessChannel?: string;
	operationType?: string;
}
interface ApiEventOptions {
	isRetry?: boolean; // 是否是重试
	isEndApi?: boolean; // 是否API请求结束代表操作结束
	data: ApiEventData;
}

enum WSS_CODE {
	success = 200,
	failed = 500,
	disconnect = -1,
	closed = 401,
	notFound = 404,
	reachLimit = 429,
}

interface EmBaseOptions {
	org: string;
	appkey: string;
	os: number;
	sdkVersion: string;
	sdkProduct: SdkProductType.web; // 上报数据源
	deviceId: string; // 设备id
	sdkServiceId: string; // sdk初始化生成的sessionId
	did: string; // userAgent
}

interface InitOptions {
	org: string;
	appkey: string;
	deviceId: string;
	sdkVersion: string;
	isReport: boolean; // 是否上报数据
}

interface OperateOptions {
	operationName?: string; // 操作名称
	uid?: string; // uid
}

interface InitSdkOptions {
	did: string;
	os: number;
	sdkVersion: string;
	deviceId: string;
}

enum SdkProductType {
	web = 0,
	native = 1,
}

export type {
	ApiEventOptions,
	ApiEventData,
	EmBaseOptions,
	InitOptions,
	OperateOptions,
	InitSdkOptions,
};
export {
	OperationName,
	OperationType,
	RequestName,
	WSS_CODE,
	NetworkType,
	SdkProductType,
};
