/**
 * @hidden
 * @deprecated Use {@link createGroup} instead.
 * Creates a group instance.
 *
 * ```typescript
 * connection.createGroupNew({
 *  data: {
 *      groupname: 'groupname',
 *      desc: 'this is my group',
 *      members: ['user1', 'user2'],
 *      public: true,
 *      approval: false,
 *      allowinvites: true,
 *      inviteNeedConfirm: false
 *  }
 *  })
 * ```
 */

/**
 * @hidden
 * @deprecated 已废弃，请用 {@link createGroup} 代替。
 * 创建群组。
 *
 * ```typescript
 * connection.createGroupNew({
 * data: {
 *      groupname: 'groupname',
 *      desc: 'this is my group',
 *      members: ['user1', 'user2'],
 *      public: true,
 *      approval: false,
 *      allowinvites: true,
 *      inviteNeedConfirm: false
 *   }
 * })
 * ```
 */
function createGroupNew(
	this: Connection,
	params: {
		/** The group information. */
		/** 群组信息。*/
		data: {
			/** The group name. */
			/** 群组名称。*/
			groupname: string;
			/** The description of the group. */
			/** 群组描述。 */
			desc: string;
			/** The array of member IDs to add to the group. These users will be added directly into the group and receive "operation: 'directJoined'" in the callback of onGroupEvent. */
			/** 群成员 ID 数组，这些用户会被直接加入群组，在 onGroupEvent 回调里收到 operation:'directJoined'。 */
			members: UserId[];
			/** Whether it is a public group. -`true`: Yes; -`false`: No. Public group: the group that others can query by calling `listgroups`. */
			/** 是否为公开群，公开群：其他人可以调用 listGroups 查询到的群组。- `true`：公开群；- `false`：非公开群。*/
			public: boolean;
			/** Whether a user requires the approval from the group admin to join the group. -`true`: Yes; -`false`: No.*/
			/** 加群是否需要管理员审批。- `true`：需要；- `false`：不需要。 */
			approval: boolean;
			/** Whether to allow group members to invite others to the group. `true`: Allow; `false`: Do not allow.*/
			/** 是否允许群成员邀请其他人进群。`true`：允许；`false`：不允许。*/
			allowinvites: boolean;
			/**
			 * Whether the invitee needs to accept the invitation before joining the group.
			 * - `true`: The invitee's consent is required. The default value is `true`.
			 * - `false`: The invitee will be directly added to the group without confirmation.
			 */
			/**
			 * 受邀人加入群组前是否需接受入群邀请。
			 * - `true` ：需受邀人确认入群邀请。默认值为 `true`。
			 * - `false`：受邀人直接加入群组，无需确认入群邀请。
			 */
			inviteNeedConfirm: boolean;
			/** The group max users. */
			/** 群组最大成员数。*/
			maxusers: number;
			/** Group detail extensions which can be in the JSON format to contain more group information. */
			/** 群组详情扩展，可以采用 JSON 格式，以包含更多群信息。 */
			ext?: string;
		};
		success?: (res: AsyncResult<GroupTypes.CreateGroupResult>) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<GroupTypes.CreateGroupResult>> {
	if (!(params && params.data)) {
		throw Error('Invalid parameter');
	}
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken, jid } = this.context;
	const suc = (respData: any) => {
		params.success && params.success(respData);
	};
	const options = {
		url: `${this.apiUrl}/${orgName}/${appName}/chatgroups?resource=${jid.clientResource}`,
		dataType: 'json',
		type: 'POST',
		data: JSON.stringify({
			owner: this.user,
			groupname: params.data.groupname,
			desc: params.data.desc,
			members: params.data.members,
			public: params.data.public,
			approval: params.data.approval,
			allowinvites: params.data.allowinvites,
			invite_need_confirm: params.data.inviteNeedConfirm,
			maxusers: params.data.maxusers,
			custom: params.data.ext,
		}),
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
		success: suc,
		error: params.error,
	};
	logger.debug('Call createGroup:', params);
	return ajax.call(this, options, OperationName.CREATE_GROUP);
}

