/**
 * @hidden
 * @deprecated 已废弃，请用 {@link blockGroupMessages} 代替。
 * 屏蔽群组消息，只对移动端有效。
 * ```typescript
 * connection.blockGroup({groupId: 'groupId'})
 * ```
 */
function blockGroup(
	this: Connection,
	params: {
		/** The group ID. */
		/** 群组 ID。 */
		groupId: string;
		success?: (res: AsyncResult<GroupTypes.BlockGroupResult>) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<GroupTypes.BlockGroupResult>> {
	if (typeof params.groupId !== 'string' || params.groupId === '') {
		throw Error('Invalid parameter');
	}
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken, jid } = this.context;

	const groupId = 'notification_ignore_' + params.groupId;
	const data = {
		entities: [{ [groupId]: true }],
	};
	const options = {
		type: 'PUT',
		url: `${this.apiUrl}/${orgName}/${appName}/users/${this.user}?resource=${jid.clientResource}`,
		data: JSON.stringify(data),
		dataType: 'json',
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
		success: params.success,
		error: params.error,
	};
	logger.debug('Call blockGroupMessages', params);
	return ajax.call(this, options, OperationName.BLOCK_GROUP);
}

