/**
 * @hidden
 * @deprecated 已废弃。请用 {@link getPublicGroups} 替换。
 * 分页获取公开群。
 *
 * ```typescript
 * connection.listGroups({limit: 20, cursor: null})
 * ```
 */
function listGroups(
	this: Connection,
	params: {
		/** The number of records per page. */
		/** 每页返回的公开群组数。 */
		limit: number;
		/** The cursor that specifies where to start to get data. If there will be data on the next page, this method will return the value of this field to indicate the position to start to get data of the next page. If it is null, the data of the first page will be fetched. */
		/** 游标。如果数据还有下一页，该方法的返回值会包含此字段，传递此字段可获取下一页的数据，为 null 时获取第一页数据。 */
		cursor?: string;
		success?: (res: AsyncResult<GroupTypes.BaseGroupInfo[]>) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<GroupTypes.BaseGroupInfo[]>> {
	if (typeof params.limit !== 'number') {
		throw Error('Invalid parameter');
	}
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken } = this.context;
	const requestData = {
		limit: params.limit,
		cursor: params.cursor,
	};
	if (!params.cursor) {
		delete requestData.cursor;
	}
	const options = {
		url: this.apiUrl + '/' + orgName + '/' + appName + '/publicchatgroups',
		type: 'GET',
		dataType: 'json',
		data: requestData,
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
		success: params.success,
		error: params.error,
	};
	logger.debug('Call listGroups', params);
	return ajax.call(this, options, OperationName.LIST_GROUP);
}

