/**
 * Lists all the groups a user has joined.
 *
 * @note
 * If either `needAffiliations` or `needRole` is set `true`, when you get data with pagination, the current page number (pageNum) starts from 0 and you can get a maximum of 20 groups (pageSize) on each page and
 * the function return type is `Promise<AsyncResult<GroupTypes.GroupInfo[]>`
 *
 * If neither of the parameters is set, when you get data with pagination, the current page number (pageNum) starts from 1 and you can get a maximum of 500 groups (pageSize) on each page and and
 * the function return type is `Promise<AsyncResult<GroupTypes.BaseGroupInfo[]>`
 *
 * ```typescript
 * connection.getJoinedGroups({
 * 		pageNum: 1,
 * 		pageSize: 500,
 * 		needAffiliations: false,
 * 		needRole: false
 * })
 * ```
 */

/**
 * 获取用户加入的所有群组。
 *
 * @note
 * 若设置了 `needAffiliations`  或 `needRole` 参数为 `true`，分页获取时，当前页码 （pageNum）从 0 开始，每页获取的群组上限（pageSize）为 20 并且
 * 返回值为类型为 `Promise<AsyncResult<GroupTypes.GroupInfo[]>`
 *
 * 若不设置这两个参数，分页获取时，当前页码 （pageNum）从 1 开始，每页获取的群组上限（pageSize）为 500 并且
 * 返回值为类型为 `Promise<AsyncResult<GroupTypes.BaseGroupInfo[]>`
 *
 * ```typescript
 * connection.getJoinedGroups({
 * 		pageNum: 1,
 * 		pageSize: 500,
 * 		needAffiliations: false,
 * 		needRole: false
 * })
 * ```
 */
function getJoinedGroups(
	this: Connection,
	params: {
		/**
		 * If either `needAffiliations` or `needRole` is set, when you get data with pagination, the current page number (pageNum) starts from 0.
		 *
		 * If neither of the parameters is set, when you get data with pagination, the current page number (pageNum) starts from 1.
		 */
		/**
		 * 若设置了 `needAffiliations`  或 `needRole` 参数，当前页码 (pageNum)从 0 开始。
		 *
		 * 不设置这两个参数，分页获取时，当前页码 （pageNum）从 1 开始。
		 */
		pageNum: number;
		/**
		 * If either `needAffiliations` or `needRole` is set, when you get data with pagination, you can get a maximum of 20 groups (pageSize) on each page.
		 *
		 * If neither of the parameters is set, when you get data with pagination, you can get a maximum of 500 groups (pageSize) on each page.
		 */
		/**
		 * 若设置了 `needAffiliations`  或 `needRole` 参数，每页获取的群组上限（pageSize）为 20。
		 *
		 * 不设置这两个参数，分页获取时，每页获取的群组上限(pageSize)为 500。
		 */
		pageSize: number;
		/**
		 * Whether the number of group members is required.
		 * `true`: Yes;
		 * (Default)`false`: No.
		 */
		/**
		 * 是否需要群组成员数。
		 * `true`：是；
		 * （默认）`false`：否。
		 */
		needAffiliations?: boolean;
		/**
		 * Whether the role of the current user in the group is required.
		 * `true`: Yes;
		 * (Default)`false`: No.
		 */
		/**
		 * 是否需要当前用户在群组中的角色。
		 * `true`：是；
		 * （默认）`false`：否。
		 */
		needRole?: boolean;
		success?: (
			res: AsyncResult<
				GroupTypes.BaseGroupInfo[] | GroupTypes.GroupInfo[]
			>
		) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<GroupTypes.BaseGroupInfo[] | GroupTypes.GroupInfo[]>> {
	if (
		typeof params.pageNum !== 'number' ||
		typeof params.pageSize !== 'number'
	) {
		throw Error('Invalid parameter: "pageNum or pageSize"');
	}
	if (params.pageNum < 0 || params.pageSize < 0) {
		throw Error('"pageNum" should >= 0 and "pageSize" should >= 0');
	}
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken } = this.context;
	const requestUrl =
		params.needAffiliations || params.needRole
			? `/chatgroups/user/${this.user}?pagenum=${params.pageNum}&pagesize=${params.pageSize}&needAffiliations=${params.needAffiliations}&needRole=${params.needRole}`
			: `/users/${this.user}/joined_chatgroups?pagenum=${params.pageNum}&pagesize=${params.pageSize}`;
	const options = {
		url: this.apiUrl + '/' + orgName + '/' + appName + requestUrl,
		dataType: 'json',
		type: 'GET',
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
		success: params && params?.success,
		error: params && params?.error,
	};
	logger.debug('Call getGroup', params);
	return ajax
		.call(this, options, OperationName.GET_USER_GROUP)
		.then((res: any) => {
			const { uri, entities } = res;
			const resp: any = [];
			if (uri.includes('joined_chatgroups')) {
				return res;
			} else {
				entities.forEach((item: any) => {
					const resultData = {
						affiliationsCount: item.affiliations_count,
						groupName: item.name,
						groupId: item.groupId,
						role: item.permission,
						disabled: item.disabled,
						approval: item.membersonly,
						allowInvites: item.allowinvites,
						description: item.description,
						maxUsers: item.maxusers,
						public: item.public,
					};
					resp.push(resultData);
				});
				res.entities = resp;
				return res;
			}
		});
}

