interface extMsg {
	key: string;
	type: number;
	varintValue: any;
	stringValue: any;
	doubleValue: any;
	floatValue: any;
}

export enum MsyncEditMsgChatType {
	'chat:user' = 'singleChat',
	'chat:group' = 'groupChat',
	'chat:room' = 'chatRoom',
};

const ContentsType = {
	0: 'TEXT',
	1: 'IMAGE',
	2: 'VIDEO',
	3: 'LOCATION',
	4: 'VOICE',
	5: 'FILE',
	6: 'COMMAND',
	7: 'CUSTOM',
	8: 'COMBINE',
};

function decodeMsg(msg: extMsg[]) {
	const msgObj: any = {};
	for (let i = 0; i < msg.length; i++) {
		if (msg[i].type === 8) {
			msgObj[msg[i].key] = JSON.parse(msg[i].stringValue);
		} else if (msg[i].type === 7) {
			msgObj[msg[i].key] = msg[i].stringValue;
		} else if (msg[i].type === 6) {
			msgObj[msg[i].key] = msg[i].doubleValue;
		} else if (msg[i].type === 5) {
			msgObj[msg[i].key] = msg[i].floatValue;
		} else if (msg[i].type === 1) {
			const varintValue = msg[i].varintValue;
			const value = new Long(
				varintValue.low,
				varintValue.high,
				varintValue.unsigned
			).toString();
			msgObj[msg[i].key] = Number(value) !== 0;
		} else if (
			msg[i].type === 2 ||
			msg[i].type === 3 ||
			msg[i].type === 4
		) {
			const varintValue = msg[i].varintValue;
			const value = new Long(
				varintValue.low,
				varintValue.high,
				varintValue.unsigned
			).toString();
			msgObj[msg[i].key] = Number(value);
		}
	}
	return msgObj;
}

