	unMSyncSendMsgMap: Map<string, any>;
	/** @hidden */
	max_cache_length = 100;
	/** @hidden */
	static _getSock: () => any = () => {
		//
	};
	/**	@hidden */
	_localCache?: typeof LocalCache;
	/** @hidden */
	clientResource: string;
	/** @hidden */
	expiresIn: number;
	/** @hidden */
	expirationTime: number;

	/** @hidden */
	mSync: any;
	/** @hidden */
	_msgHash: { [key: string]: any };
	/** @hidden */
	_msgPromiseHash: { [key: string]: any };
	/** @hidden */
	mr_cache: { [key: string]: any };
	/** @hidden */
	_offlineMessagePullState: OfflineMessagePullState;
	/** @hidden */
	_offlineMessagePullQueue: Array<any>;
	/** @hidden */
	reconnecting: boolean;
	/** @hidden */
	dataReport: DataReport;
	eventHandler?: EventHandler;
	/** @hidden */
	onOpened?: () => void;
	/** @hidden */
	onPresence?: (msg: PresenceMsg) => void;
	/** @hidden */
	onTextMessage?: (msg: TextMsgBody) => void;
	/** @hidden */
	onPictureMessage?: (msg: ImgMsgBody) => void;
