	onOnline?: () => void;
	/** @hidden */
	onStatisticMessage?: (msg: any) => void; // ? TODO
	/** @hidden */
	onContactAgreed?: (msg: ContactMsgBody) => void;
	/** @hidden */
	onContactRefuse?: (msg: ContactMsgBody) => void;
	/** @hidden */
	onContactDeleted?: (msg: ContactMsgBody) => void;
	/** @hidden */
	onContactAdded?: (msg: ContactMsgBody) => void;
	/** @hidden */
	onContactInvited?: (msg: ContactMsgBody) => void;
	/** @hidden */
	onTokenWillExpire?: () => void;
	/** @hidden */
	onTokenExpired?: () => void;
	/** @hidden */
	onClosed?: () => void;
	/** @hidden */
	onPresenceStatusChange?: (msg: PresenceType[]) => void;
	/** @hidden */
	//eslint-disable-next-line no-undef
	[key: string]: any;
	_reportLogs = false;
	_reportInterval: number = REPORT_INTERVAL;
	_isLogging = false;
	_initWithAppId = false;
	constructor(options: ConnectionParameters) {
		this.lastHeartbeat = Date.now();
		this.isDebug = options.isDebug || false;
		this.isReport = false;
		this.uploadPartEnable = true;
		this.isHttpDNS =
			options.isHttpDNS === undefined ? isHttpDNS : options.isHttpDNS;
		this.heartBeatWait = options.heartBeatWait || HEARTBEAT_INTERVAL;
		this.autoReconnectNumMax =
			options.autoReconnectNumMax || RECONNECT_LIMIT;
		this.refreshDNSIntervals =
			this.autoReconnectNumMax < 5 ? this.autoReconnectNumMax : 5;
		this.delivery = options.delivery || false;
		this.disconnectedReason = undefined;
		this.loginInfoCustomExt = undefined;
		this.https = utils.isUseHttps(options.https);
		this.dnsArr = (process.env.DNS_CONFIG as unknown as string[] || ['https://rs.easemob.com']).filter(
			(item) => item.startsWith(this.https ? 'https' : '')
		);
		this.dnsIndex = 0;
		this.restHosts = [];
		this.restTotal = 0;
		this.restIndex = 0;
		this.hostIndex = 0;
		this.socketHost = [];
		this.hostTotal = 0;
		this.times = 1; // 重联次数
		this.autoReconnectNumTotal = 0;
		this.domain = 'easemob.com';
		this.dnsTotal = this.dnsArr.length;
		// this.appKey = options.appKey;
		// this.appId = options.appId;
		if ('appKey' in options) {
			this.appKey = options.appKey as string;
		}
		if ('appId' in options) {
			this.appId = options.appId;
			this._initWithAppId = true;
		}
		this.appName = '';
		this.orgName = '';
		this.token = '';
		this.grantType = '';
		this.apiUrl = options.apiUrl || '';
		this.url = options.url || '';
		// mysnc config
		this.version = process.env.SDK_VERSION || '4.11.0';
		this.deviceId = options.deviceId || 'webim';
		this.isFixedDeviceId = options.isFixedDeviceId ?? true;
		this.osType = 16;
		this.useOwnUploadFun = options.useOwnUploadFun || false;
		this.compressType = [0];
		this.encryptType = [0];
		this.clientResource = '';
		this.expiresIn = 0;
		this.expirationTime = 0;
		this.useReplacedMessageContents =
			options.useReplacedMessageContents || false;
		this.logOut = true;
		this.context = {
			jid: {
				appKey: '',
				clientResource: '',
				domain: 'easemob.com',
				name: '',
			},
			userId: '',
			appKey: '',
			appId: '',
			status: 0,
			restTokenData: '',
			appName: '',
			orgName: '',
			root: {},
			accessToken: '',
		};
		this._msgHash = {};
		this._msgPromiseHash = {};
		this._queues = [];
		// this.root = {}
		this._load_msg_cache = [];
		this.unMSyncSendMsgMap = new Map();
		this.mr_cache = {};
		this.reconnecting = false;
		this._offlineMessagePullState = OfflineMessagePullState.SYNC_INIT;
		this._offlineMessagePullQueue = [];
		// uniapp推送
		this.uniPushConfig = {};
		this.uniPush = null;
		this.isRegisterPush = false;
		this.pushCertificateName = '';

		if (this._initWithAppId) {
			this.appKey = '';
			if (typeof this.appId !== 'string' || !this.appId) {
				throw Error(`Invalid appId: ${this.appId}`);
			}
		} else {
			if (
				typeof this.appKey !== 'string' ||
				this.appKey.split('#').length !== 2
			) {
				throw Error(`Invalid appKey: ${this.appKey}`);
			}

			this.devInfos = this.appKey.split('#');
			this.orgName = this.devInfos[0];
			this.appName = this.devInfos[1];
		}

		// this.devInfos = this.appKey.split('#');
		// this.orgName = this.devInfos[0];
		// this.appName = this.devInfos[1];
		this.listen = listen.bind(this);
		this.mSync = this.usePlugin(mSync);
		this.eventHandler = this.usePlugin(
			EventHandler
		) as unknown as EventHandler;
		Object.assign(Connection.prototype, contactApis);
		Object.assign(Connection.prototype, groupApis);
		Object.assign(Connection.prototype, chatRoomApis);
		Object.assign(Connection.prototype, presenceApis);
		Object.assign(Connection.prototype, silentModeApis);
		Object.assign(Connection.prototype, threadApis);
		Object.assign(Connection.prototype, translationApis);
		// 初始化DataReport
		this.dataReport = new DataReport({
			appkey: this.appKey || 'default#appkey',
			org: this.orgName,
			sdkVersion: this.version,
			deviceId: this.deviceId,
			isReport: this.isReport,
		});

		// util.ajax = util.ajax.bind(this);
		let _ajaxUnconventionalErrorTimes = 0;
		// eslint-disable-next-line @typescript-eslint/no-this-alias
		const _self = this;
		Object.defineProperty(utils, 'ajaxUnconventionalErrorTimes', {
			set(value) {
				if (value === 0) {
					return;
				}
				logger.debug(`rest api request fail times: ${value}`);
				_ajaxUnconventionalErrorTimes = value;
				if (
					_ajaxUnconventionalErrorTimes % 5 === 0 &&
					_self.isHttpDNS
				) {
					logger.debug('refresh dns config when rest request fail.');
					retryRequestDNS.call(_self, () => {
						// empty
					});
				}
			},
			get() {
				return _ajaxUnconventionalErrorTimes;
			},
		});
		if (this.isFixedDeviceId) {
			const deviceInfo = utils.getLocalDeviceInfo();
			if (deviceInfo) {
				// if deviceInfo is not null, it means that the device ID has been generated before
				this.clientResource = JSON.parse(deviceInfo).deviceId;
			}
		}
		logger.debug(`init SDK: Conn ${this.version} ${platForm.platform}`);
	}
	/**
	 * @hidden
	 */
	usePlugin(this: Connection, plugin: any, name?: string) {
		// 集成 uni-app 推送插件
		if (name === 'push') {
			if (plugin.config && plugin.emPush) {
				this.uniPushConfig = plugin.config;
				this.uniPush = plugin.emPush;
			} else {
				logger.error(
					'use push plugin failed',
					'emPush',
					this.uniPush,
					'config',
					this.uniPushConfig
				);
			}
			return;
		}
		if (name) {
			this[name] = new plugin(this);
		} else {
			return new plugin(this);
		}
	}

