	/** Registers a user. */
	/** 注册用户。 */
	async registerUser(
		this: Connection,
		params: RegisterUserParameters
	): Promise<RegisterUserResult> {
		if (!params.username || !params.password) {
			throw Error('Invalid parameter');
		}

		try {
			// if (!this.orgName && !this.appName) {
			// 	const error = ErrorEvent.create({
			// 		type: Code.WEBIM_CONNCTION_APPKEY_NOT_ASSIGN_ERROR,
			// 		message: 'signup error',
			// 	});
			// 	throw error;
			// }
			const rpt = this.dataReport.geOperateFun({
				uid: params.username,
				operationName: OperationName.REGISTER,
			});

			if (this.isHttpDNS) {
				this.dnsIndex = 0;
				await retryRequestDNS.call(this, rpt);
			}

			const result = await retrySignup.call(this, params, { rpt });
			return result;
		} catch (e: any) {
			params.error && params.error(e);
			throw e;
		}
	}

	/** Logs in. */
	/** 登录。 */
	async open(parameters: LoginParameters): Promise<LoginResult> {
		logger.debug('open', parameters.user, 'isLogout:', this.logOut);
		if (this._isLogging) {
			const error = ErrorEvent.create({
				type: Code.WEBIM_CONNCTION_OPEN_ERROR,
				message: 'Currently logging in, please wait.',
			});
			throw error;
		}
		if (!this.logOut) {
			const error = ErrorEvent.create({
				type: Code.WEBIM_USER_ALREADY_LOGIN,
				message: 'The user has logged in.',
			});
			throw error;
		}
		this.retryConnectTimes = 0;
		this._isLogging = true;
		try {
			if (platForm.platform === 'web') {
				const browser = utils.detectBrowser();
				logger.debug('browser', browser);
			}

			const error = _validateOpenParams.call(this, parameters);
			if (error) {
				this.onError?.(error);
				throw error;
			}

			if (parameters.accessToken) {
				this.token = parameters.accessToken;
			}

			const params = {
				...parameters,
			};

			const rpt = this.dataReport.geOperateFun({
				uid: parameters.user,
				operationName: OperationName.LOGIN,
			});
			this.dnsIndex = 0;
			if (this.isHttpDNS) {
				await retryRequestDNS.call(this, rpt);
			}
			logger.initReport({
				report: this._reportLogs,
				reportInterval: this._reportInterval,
				connection: this,
			});

			const result = await retryLogin.call(this, params, { rpt });

			await retryConnectMSync.call(this, rpt);

			logger.debug('grantType', this.grantType);
			utils.listenNetwork(online.bind(this), offline.bind(this));
			utils.listenBrowserVisibility(
				() => {
					logger.debug('visibility: true');
				},
				() => {
					logger.debug('visibility: false');
				}
			);
			return result;
		} catch (e: any) {
			this._isLogging = false;
			parameters.error && parameters.error(e);
			logger.error('login failed', e);
			throw e;
		} finally {
			logger.reportLog();
		}
	}

	/** Checks the connection status. - `true`: Connected; - (Default)`false`: Disconnected.  */
	/** 监测连接状态。-`true`：已连接；- `false`：未连接。*/
	isOpened(): boolean {
		return (
			(this.sock && this.sock.readyState === SOCKJS_STATUS.OPEN) || false
		);
	}

	/** Closes the connection. */
	/** 关闭连接。 */
	close(): void {
		logger.debug('call close');
		logger._stopReportLogs();
		this.logOut = true;
		this.disconnectedReason = undefined;
		this.reconnecting = false;
		this.context.status = Code.STATUS_CLOSING;
		this.sock && this.sock.close();
		this.unMSyncSendMsgMap?.clear();
		this.stopHeartBeat();
		this.rejectMessage();
		this.context.status = Code.STATUS_CLOSED;
		this._load_msg_cache = [];
		this._queues = [];
		this._msgHash = {};
		this.mr_cache = {};
		this.token = '';
		this.context.accessToken = '';
		this.isRegisterPush = false;
		this.clearTokenTimeout();
		this?._localCache?.getInstance()?.close();
		this.connectionTimer && clearTimeout(this.connectionTimer);
		this.provisionTimer && clearTimeout(this.provisionTimer);
		this.probingTimer && clearTimeout(this.probingTimer);
	}

	/** Download and parse the merged message. */
	/** 下载并解析合并消息。 */
	downloadAndParseCombineMessage(
		options: DownloadCombineMessagesParams
	): Promise<MessagesType[]> {
		const { url, secret } = options;
		return new Promise((resolve, reject) => {
			const platForm = utils.getEnvInfo();
			const isMiniProgram =
				platForm.platform !== 'web' &&
				platForm.platform !== 'node' &&
				platForm.platform !== 'quick_app' &&
				platForm.global?.canIUse?.('getFileSystemManager');

			const loadSuccess = (res: any) => {
				const handleReadFail = (error: any) => {
					reject({
						type: Code.PARSE_FILE_ERROR,
						message: 'Read file failed',
						data: error,
					});
				};

				const handleReadSuccess = async (data: any) => {
					const combinedMsg = isMiniProgram
						? new Uint8Array(data.data)
						: new Uint8Array(data.target.result);

					let start = 0,
						totalLength = 0,
						length = 2; //文件头两个字节
					const headerBytes = combinedMsg.subarray(
						start,
						start + length
					);
					const startStr = utils.Uint8ArrayToString(headerBytes);
					logger.debug('file header:', startStr);
					totalLength += length;

					let checkResult = 0x00;

					for (let i = 2; i < combinedMsg.length - 1; i++) {
						if (i % 2 === 1) {
							checkResult ^= combinedMsg[i];
						}
					}

					const fileCheckResult = combinedMsg.subarray(
						combinedMsg.length - 1,
						combinedMsg.length
					);

					logger.debug(
						'checkResult:',
						checkResult,
						fileCheckResult[0] === checkResult
					);
					if (fileCheckResult[0] !== checkResult) {
						return reject({
							type: Code.PARSE_FILE_ERROR,
							message: 'File verification failed',
						});
					}

					if (startStr === 'cm') {
						//文件头校验通过
						try {
							const msgList: MessagesType[] = [];
							while (totalLength < combinedMsg.length - 1) {
								start = start + length;
								length = 4; //四个字节来表示消息长度
								totalLength += length;
								const Uint8ArrayLength = combinedMsg.subarray(
									start,
									start + length
								);
								start = start + length;
								length = Uint8ArrayLength.reduce(
									(acc, val, index) =>
										acc +
										(val <<
											(8 *
												(Uint8ArrayLength.length -
													index -
													1))),
									0
								); //计算出消息的长度
								totalLength += length;
								const msg = combinedMsg.subarray(
									start,
									start + length
								);
								let MetaMessage =
									this.root.lookup('easemob.pb.Meta');
								MetaMessage = MetaMessage.decode(msg);
								const message: MessagesType =
									await handleChatMsg.call(
										// eslint-disable-next-line @typescript-eslint/ban-ts-comment
										// @ts-ignore
										this,
										MetaMessage,
										0,
										true,
										true //是否按照SDK V4.0格式化消息，默认true
									);
								msgList.push(message);
							}
							return resolve(msgList);
						} catch (err: any) {
							//文件解析失败，如何提示？
							reject({
								type: Code.PARSE_FILE_ERROR,
								message: 'Parse file failed',
								data: err,
							});
						}
					} else {
						return reject({
							type: Code.PARSE_FILE_ERROR,
							message: 'File verification failed',
						});
					}
				};

				if (isMiniProgram) {
					const fs = platForm.global.getFileSystemManager();
					const path = res.tempFilePath;

					fs.readFile({
						filePath: path,
						success: handleReadSuccess,
						fail: handleReadFail,
					});
				} else {
					// browser
					if (res instanceof Blob) {
						const reader = new FileReader();
						reader.readAsArrayBuffer(res);
						reader.onerror = handleReadFail;
						reader.onload = handleReadSuccess;
					}
				}
			};
			const loadError = (res: any) => {
				reject({
					type: Code.WEBIM_DOWNLOADFILE_ERROR,
					message: 'Download failed, please try again',
					data: res,
				});
			};

			if (isMiniProgram) {
				platForm.global.downloadFile({
					url,
					success: loadSuccess,
					fail: loadError,
				});
			} else {
				utils.download.call(this, {
					url,
					headers: {
						Accept: 'application/json',
					},
					onFileDownloadComplete: loadSuccess,
					onFileDownloadError: loadError,
					secret,
					accessToken: this.context.accessToken,
				});
			}
		});
	}

