import { Connection } from '../engineCore/connection';
import { Code } from '../status';
import ErrorEvent from '../error/error';
import { logger } from '../logger';

function handleStatisticsMsg(this: Connection, meta: any) {
	const messageBodyMessage = this.root.lookup('easemob.pb.StatisticsBody');
	const thirdMessage = messageBodyMessage.decode(meta.payload);
	// let msgId = new Long(meta.id.low, meta.id.high, meta.id.unsigned).toString();
	let errorObj: ErrorEvent;
	const { operation, reason } = thirdMessage;
	switch (operation) {
		case 0:
			this.onStatisticMessage && this.onStatisticMessage(thirdMessage);
			this.eventHandler?.dispatch('onStatisticMessage', thirdMessage);
			break;
		case 1:
			errorObj = ErrorEvent.create({
				type: Code.WEBIM_CONNCTION_USER_REMOVED,
				message: 'user has been removed',
			});
			this.disconnectedReason = errorObj;
			this.logOut = true;
			this.onError && this.onError(errorObj);
			this.eventHandler?.dispatch('onError', errorObj);
			break;
		case 2:
			errorObj = ErrorEvent.create({
				type: Code.WEBIM_CONNCTION_USER_LOGIN_ANOTHER_DEVICE,
				message: 'the user is already logged on another device',
			});
			if (reason) {
				errorObj.data = {
					loginInfoCustomExt: thirdMessage.reason,
				};
			}
			this.disconnectedReason = errorObj;
			this.logOut = true;
			this.onError && this.onError(errorObj);
			this.eventHandler?.dispatch('onError', errorObj);
			break;
		case 3:
			errorObj = ErrorEvent.create({
				type: Code.WEBIM_CONNCTION_USER_KICKED_BY_CHANGE_PASSWORD,
				message: 'the user was kicked by changing password',
			});
			this.disconnectedReason = errorObj;
			this.logOut = true;
			this.onError && this.onError(errorObj);
			this.eventHandler?.dispatch('onError', errorObj);
			break;
		case 4:
			errorObj = ErrorEvent.create({
				type: Code.WEBIM_CONNCTION_USER_KICKED_BY_OTHER_DEVICE,
				message: 'the user was kicked by other device',
			});
			this.disconnectedReason = errorObj;
			this.logOut = true;
			this.onError && this.onError(errorObj);
			this.eventHandler?.dispatch('onError', errorObj);
			break;
		default:
			logger.error('handleStatisticsMsg:', thirdMessage);
			break;
	}
}

export default handleStatisticsMsg;
