import { Jid, UserId } from './common';
import {
	ExcludeAckMessageBody,
	MessageBody,
	MessagesType,
	ModifiedMsg,
} from './message';

interface UpdateOwnUserInfoParams {
	/** The nickname. */
	/** 昵称。 */
	nickname?: string;
	/** The avatar URL. */
	/** 头像地址。 */
	avatarurl?: string;
	/** The email address. */
	/** 邮箱。 */
	mail?: string;
	/** The phone number. */
	/** 电话。 */
	phone?: string;
	/** Gender. You can define it with the following type: string, number, boolean. */
	/** 性别。你可以自定义，支持 String，Number，Boolean。 */
	gender?: string | number | boolean;
	/** Signature. */
	/** 签名。 */
	sign?: string;
	/** Birthday. */
	/** 生日。 */
	birth?: string;
	/** Extension. You can define it with the following type: string, number, boolean. */
	/** 扩展。你可以自定义。 */
	ext?: string;
}

/** Configurable fields. */
/** 可设置字段。 */
type ConfigurableKey =
	| 'nickname'
	| 'avatarurl'
	| 'mail'
	| 'phone'
	| 'gender'
	| 'sign'
	| 'birth'
	| 'ext';

interface RosterData {
	name: string;
	subscription: 'both';
	jid: Jid;
}

// 返回值

interface BaseUserInfo {
	/** Whether the current user is enabled. - `true`: Yes; - `false`: No.*/
	/** 当前用户是否是启用状态。 */
	activated: boolean;
	/** The timestamp when the current user is created. */
	/** 当前用户创建时间。 */
	created: number;
	/** The time when the user information is last modified. */
	/** 最新一次修改用户信息的时间。*/
	modified: number;
	/** The display name in the message push notification. */
	/** 用于推送的用户昵称。 */
	nickname: string;
	/** The user type. */
	/** 用户类型。 */
	type: string;
	/** The user ID. */
	/** 用户 ID。 */
	username: string;
	/** The user uuid on the server. */
	/** 用户在服务器上的唯一 ID。 */
	uuid: string;
}

export interface PushInfo {
	/** The device ID, used to identify a device, which can be customized.*/
	/** 设备 ID，可以自己定义，一般用来标识一个设备。 */
	device_id: string;
	/** The push token, which can be defined by yourself, is generally used to identify the same device. */
	/** 推送 token。 */
	device_token: string;
	/** The Push service appId, senderID for FCM, "appId+#+AppKey" for Vivo */
	/** 推送服务的 appId，对于 FCM 是 senderId，对于 VIVO 是 “appId+#+AppKey”。 */
	notifier_name: string;
}

interface UploadTokenResult extends BaseUserInfo {
	/** The push information. */
	/** 推送信息。 */
	pushInfo: PushInfo[];
}

interface SessionInfo {
	/** The conversation ID. */
	/** 会话 ID。 */
	channel_id: string;
	/** The content of the last message.*/
	/** 最后一条消息内容。 */
	meta: {
		/** The message sender. */
		/** 消息发送方。 */
		from: string;
		/** The message ID. */
		/** 消息 ID。 */
		id: string;
		/** The message content. */
		/** 消息内容。 */
		payload: string;
		/** The time when the message is received. */
		/** 收到消息时间。 */
		timestamp: number;
		/** The message recipient. */
		/** 消息接收方。 */
		to: string;
	};
	/** The number of unread messages. */
	/** 未读消息数。 */
	unread_num: number;
}

interface SessionsInfo {
	/** The conversation list. */
	/** 会话列表。 */
	channel_infos: SessionInfo[];
}

interface ConversationList {
	/** The conversation ID. */
	/** 会话 ID。 */
	channel_id: string;
	/** Overview of the latest news. */
	/** 最新一条消息概况。 */
	lastMessage: MessageBody | Record<string, never>;
	/** The number of unread messages. */
	/** 未读消息数。 */
	unread_num: number;
}

interface ConversationInfo {
	/** The conversation list. */
	/** 会话列表。 */
	channel_infos: ConversationList[];
}

interface DeleteSessionResult {
	/** The result of request. */
	/** 请求结果。 */
	result: 'ok';
}

interface SendMsgResult {
	/** The local ID of the message. */
	/** 消息本地 ID。 */
	localMsgId: string;
	/** The message ID on the server. */
	/** 消息在服务器端的 ID。 */
	serverMsgId: string;
	/** Message. */
	/** 消息。 */
	message?: ExcludeAckMessageBody;
}

interface ModifyMsgResult extends SendMsgResult {
	/** The modified message. */
	/** 修改的消息。 */
	message: ModifiedMsg;
}

interface HistoryMessages {
	/** The starting message ID for the next query. If the number of messages returned by the SDK is smaller than the requested number, the cursor will be `undefined`. */
	/** 下次查询的起始消息 ID。若 SDK 实际返回的消息条数小于请求获取的数量，cursor 的值为 `undefined`。*/
	cursor?: string;
	/** The historical messages. */
	/** 历史消息。 */
	messages: MessagesType[];
	/** Whether it is the last page of data.
	 *  - `true`: Yes;
	 *  - `false`: No.
	 * If the number of data entries is smaller than the message count set in the request, the data is of the last page and `true` is returned; otherwise, `false` is returned.
	 */
	/** 返回的数据是否为最后一页数据。
	 *  - `true`：是；
	 *  - `false`：否。
	 * 当返回的数据条数小于请求中设置的消息条数时，返回 `true`，否则为 `false`。
	 */
	isLast: boolean;
}

interface ServerConversations {
	/** The conversation list. */
	/** 会话列表。 */
	conversations: ConversationItem[];
	/** The position from which to start getting data for the next query. If the number of returned data entries is smaller than that specified in the request, the cursor is an empty string (''), which indicates that the current page is the last page; otherwise, the SDK returns the specific cursor position which indicates where to start getting data for the next query.*/
	/** 下次查询数据的游标位置。若 SDK 返回的数据条数小于请求中设置的数目，`cursor` 的值为空字符串（''），表示当前为最后一页数据。否则，SDK 返回具体的游标位置，指定开始获取数据的位置。*/
	cursor: string;
}

interface ConversationItem {
	/** The conversation ID. */
	/** 会话 ID。 */
	conversationId: string;
	/** The conversation type. */
	/** 会话类型。 */
	conversationType: 'singleChat' | 'groupChat';
	/** Whether the conversation is pinned. `true`:pinned; `false`: unpinned. */
	/** 是否置顶。true：置顶；false：不置顶。 */
	isPinned: boolean;
	/** The UNIX timestamp when the conversation is pinned. The unit is millisecond. This value is `0` when conversation is not pinned. */
	/** 会话置顶的 UNIX 时间戳，单位为毫秒。未置顶时此值为 `0`。 */
	pinnedTime: number;
	/** Overview of the latest message. */
	/** 最新一条消息概况。*/
	lastMessage: MessageBody | Record<string, never> | null;
	/** The number of unread messages. */
	/** 未读消息数。 */
	unReadCount: number;
	/** The conversation marks. */
	/** 会话标记。 */
	marks?: Array<MarkType>;
}

interface PinConversation {
	/** Whether the conversation is pinned. `true`: pinned; `false`: unpinned.*/
	/** 是否置顶。`true`：置顶；`false`：不置顶。 */
	isPinned: boolean;
	/** The UNIX timestamp when the conversation is pinned. The unit is millisecond. This value is `0` when the conversation is not pinned. */
	/** 会话置顶的 UNIX 时间戳，单位为毫秒。未置顶时值为 `0`。 */
	pinnedTime: number;
}

interface ContactItem {
	/** The user ID. */
	/** 用户 ID。 */
	userId: UserId;
	/** The user remark. */
	/** 用户备注。 */
	remark: string;
}

interface CursorContactsResult {
	/** The position from which to start getting data for the next query. If the number of returned data entries is smaller than that specified in the request, the cursor is an empty string (''), which indicates that the current page is the last page; otherwise, the SDK returns the specific cursor position which indicates where to start getting data for the next query.*/
	/** 下次查询数据的游标位置。若 SDK 返回的数据条数小于请求中设置的数目，`cursor` 的值为空字符串（''），表示当前为最后一页数据。否则，SDK 返回具体的游标位置，指定开始获取数据的位置。*/
	cursor?: string;
	/** The contact list. */
	/** 联系人列表。 */
	contacts: ContactItem[];
}
interface OperateResult {
	/** The userIds of the operation successfully. */
	/** 操作成功的userIds。 */
	userIds: UserId[];
}

/** The conversation mark types. The mapping between each type of conversation mark and their actual meanings is maintained by the developer. */
/** 会话标记枚举类型, 各会话标记类型与实际意义之间的映射由开发者维护。 */
enum MarkType {
	mark_0,
	mark_1,
	mark_2,
	mark_3,
	mark_4,
	mark_5,
	mark_6,
	mark_7,
	mark_8,
	mark_9,
	mark_10,
	mark_11,
	mark_12,
	mark_13,
	mark_14,
	mark_15,
	mark_16,
	mark_17,
	mark_18,
	mark_19,
}

interface PinnedMessageInfo {
	/** The pinned message. */
	/** 置顶的消息. */
	message: MessagesType;
	/** When the message is pinned. The unit is millisecond.  */
	/** 消息置顶时间，单位为毫秒。 */
	pinTime: number;
	/** The user ID of the operator that pins the message. */
	/** 操作人的用户 ID. */
	operatorId: UserId;
}
interface CursorPinnedMessagesResult {
	/** The position from which to start getting data for the next query. If the number of returned data entries is smaller than that specified in the request, the cursor is an empty string (''), which indicates that the current page is the last page; otherwise, the SDK returns the specific cursor position which indicates where to start getting data for the next query.*/
	/** 下次查询数据的游标位置。若 SDK 返回的数据条数小于请求中设置的数目，`cursor` 的值为空字符串（''），表示当前为最后一页数据。否则，SDK 返回具体的游标位置，指定下次查询时开始获取数据的位置。*/
	cursor: string;
	/** The list of pinned messages. */
	/** 置顶消息列表. */
	list: PinnedMessageInfo[];
}

export { MarkType };

export type {
	UploadTokenResult,
	SessionInfo,
	SessionsInfo,
	ConversationList,
	BaseUserInfo,
	DeleteSessionResult,
	SendMsgResult,
	ConversationInfo,
	ServerConversations,
	ConversationItem,
	PinConversation,
	OperateResult,
	RosterData,
	UpdateOwnUserInfoParams,
	ConfigurableKey,
	HistoryMessages,
	ModifyMsgResult,
	ContactItem,
	CursorContactsResult,
	PinnedMessageInfo,
	CursorPinnedMessagesResult,
};
