async function leaveChatThread(
	this: Connection,
	params: {
		/** The ID of the message thread that the current user wants to leave. */
		/** 要退出的子区的 ID。*/
		chatThreadId: string;
	}
): Promise<void> {
	if (typeof params.chatThreadId !== 'string' || params.chatThreadId === '') {
		throw Error(`Invalid parameter chatThreadId: ${params.chatThreadId}`);
	}
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken, jid } = this.context;
	const options = {
		url: `${this.apiUrl}/${orgName}/${appName}/thread/${params.chatThreadId}/user/${this.user}/quit?resource=${jid.clientResource}`,
		type: 'DELETE',
		dataType: 'json',
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
	};
	await ajax.call(this, options);
	return;
}

