/**
 * Gets members in the message thread with pagination.
 *
 * ```typescript
 * connection.getChatThreadMembers({chatThreadId: 'chatThreadId',pageSize:20,cursor:'cursor'})
 * ```
 */

/**
 * 分页获取子区成员。
 *
 * ```typescript
 * connection.getChatThreadMembers({chatThreadId: 'chatThreadId ',pageSize:20,cursor:'cursor'})
 * ```
 */
function getChatThreadMembers(
	this: Connection,
	params: {
		/** The message thread ID. */
		/** 子区 ID。*/
		chatThreadId: string;
		/** The number of members that you expect to get on each page. The value range is [1,50] and the default value is `20`. */
		/** 每页期望返回的成员数。取值范围为 [1,50]，默认为 `20`。 */
		pageSize?: number;
		/** The position from which to start getting data. At the first method call, if you set `cursor` to an empty string, the SDK will get data in the chronological order of when members join the message thread.*/
		/** 开始获取数据的游标位置。首次调用方法时传空字符串，按成员加入子区时间的正序获取数据。 */
		cursor?: string;
	}
): Promise<AsyncResult<ThreadTypes.ChatThreadMembers>> {
	if (typeof params.chatThreadId !== 'string' || params.chatThreadId === '') {
		throw Error(`Invalid parameter chatThreadId: ${params.chatThreadId}`);
	}
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken } = this.context;
	const requestData = {
		limit: params.pageSize || 20,
		cursor: params.cursor || '',
	};
	const options = {
		url: `${this.apiUrl}/${orgName}/${appName}/thread/${params.chatThreadId}/users`,
		type: 'GET',
		dataType: 'json',
		data: requestData,
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
	};
	return ajax.call(this, options);
}

