/**
 * Uses the pagination to get the message threads that the current user has joined.
 *
 * ```typescript
 * connection.getJoinedChatThreads({parentId: 'parentId',cursor: 'cursor',pageSize: 50})
 * ```
 */

/**
 * 分页获取当前用户加入的子区列表。
 *
 * ```typescript
 * connection.getJoinedChatThreads({parentId: 'parentId',cursor: 'cursor',pageSize: 50})
 * ```
 */
function getJoinedChatThreads(
	this: Connection,
	params: {
		/** The group ID to which the message thread belongs. If this parameter is set, the SDK returns the message threads that the current user has joined in a specified group.*/
		/** 子区所属群组的 ID。 若该设置了该参数，表示获取在指定群组中加入的子区列表。*/
		parentId?: string;
		/** The number of message threads that you expect to get on each page. The value range is [1,50] and the default value is `20`. */
		/** 每页期望返回的子区数。取值范围为 [1,50]，默认为 `20`。 */
		pageSize?: number;
		/** The position from which to start getting data. At the first method call, if you set `cursor` to an empty string, the SDK will get data in the reverse chronological order of when the user joins the message threads.*/
		/** 开始获取数据的游标位置。首次调用方法时传空字符串，按用户加入子区时间的倒序获取数据。 */
		cursor?: string;
	}
): Promise<AsyncResult<ThreadTypes.ChatThreadDetail[]>> {
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken } = this.context;
	const requestData = {
		limit: params.pageSize || 20,
		cursor: params.cursor || '',
	};
	const optionUrl = params.parentId
		? `${this.apiUrl}/${orgName}/${appName}/threads/chatgroups/${params.parentId}/user/${this.user}`
		: `${this.apiUrl}/${orgName}/${appName}/threads/user/${this.user}`;
	const options = {
		url: optionUrl,
		type: 'GET',
		dataType: 'json',
		data: requestData,
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
	};
	return ajax.call(this, options).then((res) => {
		const entities = res.entities;
		entities?.forEach((item: any) => {
			item.parentId = item.groupId;
			item.messageId = item.msgId;
			delete item.groupId;
			delete item.msgId;
		});
		return res;
	});
}

