/**
 * Tools provided by the SDK
 * @module Utils
 */
import { Code } from '../status';
import { Connection } from '../engineCore/connection';
import handleError from '../error/ajaxError';
import { OperationName } from '../dataReport/types';
import { NET_WORK_ERROR_CODE } from '../dataReport/const';
import { logger } from '../logger';
import { SERVER_SLICE_INFO, SLICE_SIZE } from '../uploadFile/const';
import SliceUploadFile from '../uploadFile';
import simpleUpload from '../uploadFile/simpleUpload';
import { UniPushType } from '../engineCore/mSync';
import { REQUEST_TIMEOUT } from '../constant';
export const EMPTYFN = () => void {};

interface AsyncResult<T = any> {
	/** Status of the request. */
	/** 请求状态码。 */
	type: Code;
	/** Data returned by successful request. */
	/** 请求成功返回的数据。 */
	data?: T;
	/** message. */
	/** 描述信息。 */
	message?: string;
	[key: string]: any;
}

export interface KVString {
	[key: string]: string;
}
interface AjaxOptions {
	url: string;
	dataType?: string;
	type?: string;
	data?: any;
	headers?: KVString;
	responseType?: 'arraybuffer' | 'blob' | 'document' | 'json' | 'text';
	mimeType?: string;
	isreport?: string;
	// success: (res: any) => AsyncRes;
	// error: (res: any) => AsyncRes;
	success?: (res: any) => void;
	error?: (res: any) => void;
}
/** @hidden */
export function ajax(
	this: any,
	options: AjaxOptions,
	operateName?: OperationName
): Promise<AsyncResult<any>> {
	let startTime: number;
	const envInfo = getEnvInfo();

	if (envInfo.platform === PLATFORM.WEB) {
		const p = new Promise((resolve, reject) => {
			const dataType = options.dataType || 'text';
			const suc = options.success || EMPTYFN;
			const error = options.error || EMPTYFN;
			const xhr = new XMLHttpRequest();
			xhr.timeout = REQUEST_TIMEOUT;
			let isTimeout = false;
			xhr.ontimeout = function () {
				logger.warn('request timeout');
				isTimeout = true;
				const errorEvent = {
					type: Code.REQUEST_TIMEOUT,
					message: 'Request Timeout',
					errorType: 'timeout_error',
					xhr: xhr,
				};
				error(errorEvent);
				reject(errorEvent);
			};

			xhr.onerror = function () {
				reject({
					type: Code.REQUEST_UNKNOWN,
					message: 'Request Unknow Error',
					errorType: 'onerror',
					xhr: xhr,
				});
			};

			xhr.onabort = function () {
				reject({
					type: Code.REQUEST_ABORT,
					message: 'Request Abort',
					errorType: 'onabort',
					xhr: xhr,
				});
			};

			xhr.onreadystatechange = function () {
				if (xhr.readyState === 4) {
					const endTime = new Date().getTime();
					const elapsedTime = endTime - startTime;
					const status = xhr.status || 0;
					const extraInfo = {
						elapse: elapsedTime,
						httpCode: status,
					};
					if (status === 200) {
						utils.ajaxUnconventionalErrorTimes = 0;
						try {
							switch (dataType) {
								case 'text':
									// @ts-ignore
									// if (options.version === '3.0') {
									suc(xhr.responseText); // 兼容回调函数的方式
									// @ts-ignore
									resolve(xhr.responseText);
									return;
								case 'json':
									const res = JSON.parse(xhr.responseText);
									res.extraInfo = extraInfo;
									// @ts-ignore
									// if (options.version === '3.0') {
									suc(res); // 兼容回调函数的方式
									// @ts-ignore
									resolve(res);
									return;
								// }
								case 'xml':
									if (
										xhr.responseXML &&
										xhr.responseXML.documentElement
									) {
										// @ts-ignore
										// if (options.version === '3.0') {
										suc(xhr.responseXML.documentElement);
										// @ts-ignore
										resolve(
											xhr.responseXML.documentElement
										);
										return;
										// }
									} else {
										error({
											type: Code.WEBIM_CONNCTION_AJAX_ERROR,
											data: xhr.responseText,
											message:
												'XHR.responseXML is null or XHR.responseXML.documentElement is null',
										});
										reject({
											type: Code.WEBIM_CONNCTION_AJAX_ERROR,
											data: xhr.responseText,
											message:
												'XHR.responseXML is null or XHR.responseXML.documentElement is null',
										});
									}
									return;
								default:
									error({
										type: Code.WEBIM_CONNCTION_AJAX_ERROR,
										data: xhr.responseText,
										message: 'Invalid dataType',
									});
									reject({
										type: Code.WEBIM_CONNCTION_AJAX_ERROR,
										data: xhr.responseText,
										message: 'Invalid dataType',
									});
							}
							// @ts-ignore
							// if (options.version === '3.0') {
							resolve(xhr.response || xhr.responseText);
							// @ts-ignore
							suc(xhr.response || xhr.responseText, xhr);
							return;
							// resolve({ type: 0, data: xhr.response || xhr.responseText })
						} catch (e) {
							reject(e);
						}
						return;
					} else if (status === 0) {
						// status === 0 时，可能是网络超时，等待ontimeout先处理
						setTimeout(() => {
							logger.debug('request timeout:', isTimeout);
							!isTimeout &&
								handleError(xhr, reject, error, elapsedTime);
						}, 0);
					} else {
						const generalErrorCode = [
							400, 401, 403, 404, 429, 500, 503,
						];
						if (!generalErrorCode.includes(status)) {
							logger.debug(
								'rest api request fail status:',
								status
							);
							utils.ajaxUnconventionalErrorTimes++;
						}
						handleError(xhr, reject, error, elapsedTime);
					}
				}
				if (xhr.readyState === 0) {
					error({
						type: Code.WEBIM_CONNCTION_AJAX_ERROR,
						data: xhr.responseText,
						message: 'Request not initialized',
					});
					reject({
						type: Code.WEBIM_CONNCTION_AJAX_ERROR,
						data: xhr.responseText,
						message: 'Request not initialized',
					});
				}
			};

			if (options.responseType) {
				if (xhr.responseType) {
					xhr.responseType = options.responseType;
				}
			}
			if (options.mimeType) {
				xhr.overrideMimeType(options.mimeType);
			}

			const type = options.type || 'POST';
			let data = options.data || null;
			let tempData = '';
			if (type.toLowerCase() === 'get' && data) {
				for (const o in data) {
					// eslint-disable-next-line no-prototype-builtins
					if (data.hasOwnProperty(o)) {
						tempData += o + '=' + data[o] + '&';
					}
				}
				tempData = tempData ? tempData.slice(0, -1) : tempData;
				options.url +=
					(options.url.indexOf('?') > 0 ? '&' : '?') +
					(tempData ? tempData + '&' : tempData) +
					'_v=' +
					new Date().getTime();
				data = null;
				tempData = '';
			}
			startTime = new Date().getTime();
			xhr.open(type, options.url);

			const headers = options.headers || {};
			if (!headers['Content-Type']) {
				headers['Content-Type'] = 'application/json';
			}
			for (const key in headers) {
				if (headers.hasOwnProperty(key)) {
					xhr.setRequestHeader(key, headers[key]);
				}
			}
			xhr.send(data);
		})
			.then((res: any) => {
				// 存在操作名称并且不是sdk内部逻辑则上报
				if (
					this.dataReport &&
					operateName &&
					operateName !== OperationName.SDK_INTERNAL
				) {
					const rpt = this.dataReport.geOperateFun({
						operationName: operateName,
					});
					rpt({
						isEndApi: true,
						data: {
							isSuccess: 1,
							requestUrl: options.url,
							requestName: operateName,
							requestMethod: options.type,
							...getExtraData(res.extraInfo),
						},
					});
				}
				if (operateName === OperationName.SDK_INTERNAL) {
					res.extraInfo.url = options.url;
				}
				// sdk内部逻辑单独上报，不删除res.extraInfo
				if (operateName !== OperationName.SDK_INTERNAL) {
					delete res.extraInfo;
				}
				if (toRawType(res) === 'Object') {
					return {
						...(res as object),
						type: Code.REQUEST_SUCCESS,
					};
				} else {
					return {
						data: res,
						type: Code.REQUEST_SUCCESS,
					};
				}
			})
			.catch((e) => {
				// 存在操作名称并且不是sdk内部逻辑则上报
				if (
					this.dataReport &&
					operateName &&
					operateName !== OperationName.SDK_INTERNAL
				) {
					const rpt = this.dataReport.geOperateFun({
						operationName: operateName,
					});
					rpt({
						isEndApi: true,
						data: {
							isSuccess: 0,
							requestUrl: options.url,
							requestName: operateName,
							requestMethod: options.type,
							...getExtraData(e.extraInfo),
						},
					});
				}
				if (operateName === OperationName.SDK_INTERNAL) {
					if (e.extraInfo) {
						e.extraInfo.url = options.url;
					} else {
						const endTime = new Date().getTime();
						const elapsedTime = endTime - startTime;
						const status = e.xhr?.status ?? 0;
						const extraInfo = {
							elapse: elapsedTime,
							httpCode: status,
							url: options.url,
						};
						e.extraInfo = extraInfo;
					}
				}
				// sdk内部逻辑单独上报，不删除e.extraInfo
				if (operateName !== OperationName.SDK_INTERNAL) {
					delete e.extraInfo;
				}
				throw e;
			});
		return p;
	} else {
		return wxRequest.call(this, options, operateName);
	}
}

/** @hidden */
function toRawType(value: any) {
	const _toString = Object.prototype.toString;
	return _toString.call(value).slice(8, -1);
}

interface ReqData extends AjaxOptions {
	header?: { [key: string]: any };
	timeout: number;
	method: string;
	complete: () => any;
	fail: (e: any) => any;
}
/** @hidden */
function wxRequest(
	this: any,
	options: AjaxOptions,
	operateName?: OperationName
): Promise<any> {
	return new Promise((resole, reject) => {
		const suc = options.success || EMPTYFN;
		const error = options.error || EMPTYFN;
		const type = options.type || 'POST';
		let data = options.data || null;
		let tempData = '';
		const startTime = new Date().getTime();
		const platForm = utils.getEnvInfo();

		if (type.toLowerCase() === 'get' && data) {
			for (const o in data) {
				if (data.hasOwnProperty(o)) {
					tempData += o + '=' + data[o] + '&';
				}
			}
			tempData = tempData ? tempData.slice(0, -1) : tempData;
			options.url +=
				(options.url.indexOf('?') > 0 ? '&' : '?') +
				(tempData ? tempData + '&' : tempData) +
				'_v=' +
				new Date().getTime();
			data = null;
			tempData = '';
		}

		const reqData: ReqData = {
			url: options.url,
			data: options.data,
			method: type,
			headers: {},
			timeout: REQUEST_TIMEOUT,
			success: function (res: any) {
				const endTime = new Date().getTime();
				const elapsedTime = endTime - startTime;
				const extraInfo = {
					elapse: elapsedTime,
					httpCode: Number(
						res.statusCode?.toString() || res.status?.toString()
					),
					errDesc: res?.data?.error_description || '',
				};
				if (
					res.statusCode?.toString() === '200' ||
					res.status?.toString() === '200'
				) {
					res.data.extraInfo = extraInfo;
					const dt = res.data;
					suc(dt);
					resole(dt);
				} else {
					res.extraInfo = extraInfo;
					const dt = res;
					error(dt);
					reject(dt);
					logger.debug(operateName, 'reject reason: ', dt);
				}
			},
			complete() {
				// console.log("wx.request.complete", arguments);
			},
			fail(e: any) {
				const endTime = new Date().getTime();
				const elapsedTime = endTime - startTime;
				const extraInfo = {
					elapse: elapsedTime,
					httpCode: NET_WORK_ERROR_CODE,
					errDesc: 'request:fail',
				};
				e.extraInfo = extraInfo;
				// format error desc
				e.data = {
					error: 'request:fail',
					error_description: 'request:fail',
				};
				if (e.errMsg === 'request:fail timeout') {
					// uni 微信小程序上报超时错误
					reject({
						type: Code.REQUEST_TIMEOUT,
						message: 'Request Timeout',
						extraInfo,
					});
					error({
						type: Code.REQUEST_TIMEOUT,
						message: 'Request Timeout',
						extraInfo,
					});
					return;
				}
				error(e);
				reject(e);
				logger.error(operateName, 'fail reason:', e);
			},
		};
		if (platForm.platform === 'zfb' || platForm.platform === 'dd') {
			reqData.headers = options.headers;
		} else {
			reqData.header = options.headers;
		}
		if (platForm.platform === 'dd') {
			// @ts-ignore
			return dd.httpRequest(reqData);
		}
		platForm.global.request(reqData);
	})
		.then((res: any) => {
			// 存在操作名称并且不是sdk内部逻辑则上报
			if (
				this.dataReport &&
				operateName &&
				operateName !== OperationName.SDK_INTERNAL
			) {
				const rpt = this.dataReport.geOperateFun({
					operationName: operateName,
				});
				rpt({
					isEndApi: true,
					data: {
						isSuccess: 1,
						requestUrl: options.url,
						requestName: operateName,
						requestMethod: options.type,
						...getExtraData(res.extraInfo),
					},
				});
			}
			// sdk内部逻辑单独上报，不删除res.extraInfo
			if (operateName !== OperationName.SDK_INTERNAL) {
				delete res.extraInfo;
			}
			if (toRawType(res) === 'Object') {
				return {
					...(res as object),
					type: Code.REQUEST_SUCCESS,
				};
			} else {
				return {
					data: res,
					type: Code.REQUEST_SUCCESS,
				};
			}
		})
		.catch((e) => {
			// 存在操作名称并且不是sdk内部逻辑则上报
			if (
				this.dataReport &&
				operateName &&
				operateName !== OperationName.SDK_INTERNAL
			) {
				const rpt = this.dataReport.geOperateFun({
					operationName: operateName,
				});
				rpt({
					isEndApi: true,
					data: {
						isSuccess: 0,
						requestUrl: options.url,
						requestName: operateName,
						requestMethod: options.type,
						...getExtraData(e.extraInfo),
					},
				});
			}
			// sdk内部逻辑单独上报，不删除e.extraInfo
			if (operateName !== OperationName.SDK_INTERNAL) {
				delete e.extraInfo;
			}
			const response = JSON.stringify(e.data || {});

			const formattedError = {
				...e,
				status: e.statusCode || e.status || 0,
				response: response,
				responseText: response,
			};

			handleError(
				formattedError,
				(newError) => {
					throw {
						...e,
						message: newError.message,
						type: newError.type,
					};
				},
				EMPTYFN,
				0
			);
		});
}
/**
 * @description get unique message id
 */
export function getUniqueId(this: Utils): string {
	//*******
	if (this.autoIncrement as number) {
		this.autoIncrement++;
	} else {
		this.autoIncrement = 1;
	}
	const cdate = new Date();
	const offdate = new Date(2010, 1, 1);
	const offset = cdate.getTime() - offdate.getTime();
	const hexd = offset + this.autoIncrement;
	return hexd.toString();
}

export function once(fn: any, context?: object) {
	let res: any;
	return function (...args: any[]) {
		if (!fn) return res;
		res = context ? fn.apply(context, args) : fn(...args);
		fn = null;
	};
}

/**
 * @description listen network change
 */
export const listenNetwork = once(_listenNetwork);
export function _listenNetwork(
	onlineCallback: () => void,
	offlineCallback: () => void
): void {
	const envInfo = getEnvInfo();
	if (envInfo.platform !== PLATFORM.WEB) {
		const { global } = envInfo;
		const netWorkWacher = (res: any) => {
			if (res.isConnected) {
				onlineCallback();
			} else {
				offlineCallback();
			}
		};
		global.offNetworkStatusChange &&
			global.offNetworkStatusChange(netWorkWacher);
		global.onNetworkStatusChange &&
			global.onNetworkStatusChange(netWorkWacher);
	} else {
		if (typeof addEventListener !== 'undefined') {
			window.addEventListener('online', onlineCallback);
			window.addEventListener('offline', offlineCallback);
		}
	}
}

export const listenBrowserVisibility = once(_listenBrowserVisibility);
function _listenBrowserVisibility(onShow: () => void, onHide: () => void) {
	// 监听页面可见性变化
	const envInfo = getEnvInfo();
	if (envInfo.platform === PLATFORM.WEB) {
		document &&
			document.addEventListener(
				'visibilitychange',
				() => {
					if (document.hidden) {
						onHide?.();
					} else {
						onShow?.();
					}
				},
				false
			);
	} else {
		const { global } = envInfo;
		global.onAppShow && global.onAppShow?.(onShow);
		global.onAppHide && global.onAppHide?.(onHide);
	}
}

enum PLATFORM {
	WEB = 'web',
	WX = 'wx',
	QQ = 'qq',
	ZFB = 'zfb',
	DD = 'dd',
	TT = 'tt',
	BAIDU = 'baidu',
	QUICK_APP = 'quick_app',
	UNI = 'uni',
	NODE = 'node',
}
interface EnvInfo {
	platform: PLATFORM;
	global: any;
}
/** @hidden */
function getEnvInfo(): EnvInfo {
	// @ts-ignore
	if (typeof swan !== 'undefined' && hasMiniBaseEvent(swan)) {
		return {
			platform: PLATFORM.BAIDU,
			// @ts-ignore
			global: swan,
		};
		// @ts-ignore
	} else if (typeof tt !== 'undefined' && hasMiniBaseEvent(tt)) {
		return {
			platform: PLATFORM.TT,
			// @ts-ignore
			global: tt,
		};
		// @ts-ignore This judgment condition should be in front of 'zfb'
	} else if (typeof dd !== 'undefined' && hasMiniBaseEvent(dd)) {
		return {
			platform: PLATFORM.DD,
			// @ts-ignore
			global: dd,
		};
		// @ts-ignore
	} else if (typeof my !== 'undefined' && hasMiniBaseEvent(my)) {
		return {
			platform: PLATFORM.ZFB,
			// @ts-ignore
			global: my,
		};
		// @ts-ignore
	} else if (typeof wx !== 'undefined' && hasMiniBaseEvent(wx)) {
		return {
			platform: PLATFORM.WX,
			// @ts-ignore
			global: wx,
		};
		// @ts-ignore
	} else if (typeof qq !== 'undefined' && hasMiniBaseEvent(qq)) {
		return {
			platform: PLATFORM.QQ,
			// @ts-ignore
			global: qq,
		};
		// @ts-ignore
	} else if (typeof uni !== 'undefined' && hasMiniBaseEvent(uni)) {
		return {
			platform: PLATFORM.UNI,
			// @ts-ignore
			global: uni,
		};
	} else if (typeof window !== 'undefined' && window.WebSocket) {
		return {
			platform: PLATFORM.WEB,
			// @ts-ignore
			global: window,
		};
	} else {
		return {
			platform: PLATFORM.NODE,
			// @ts-ignore
			global: global || {},
		};
	}
}

/** @hidden */
function hasMiniBaseEvent(miniGlobal: any) {
	const baseMiniEventNames = ['canIUse', 'getSystemInfo'];
	for (let i = 0, max = baseMiniEventNames.length; i < max; i++) {
		const baseEventName = baseMiniEventNames[i];
		if (!miniGlobal[baseEventName]) {
			return false;
		}
	}
	return true;
}

export interface uri {
	url: string;
	filename: string;
	filetype: string;
	data: File | null;
}

/**
 * get file url
 */
function getFileUrl(fileInputId: string | HTMLInputElement): FileObj {
	const uri: uri = {
		url: '',
		filename: '',
		filetype: '',
		data: {} as File,
	};
	const fileObj =
		typeof fileInputId === 'string'
			? (document.getElementById(fileInputId) as HTMLInputElement)
			: fileInputId;
	if (window.URL.createObjectURL) {
		if (!fileObj.files) {
			throw Error('this is not HTMLInputElement');
		}
		const fileItems = fileObj.files;
		if (fileItems.length > 0) {
			const u = fileItems.item(0);
			uri.data = u;
			uri.url = window.URL.createObjectURL(u as unknown as MediaSource);
			uri.filename = u?.name || '';
		}
	} else {
		// IE
		if (typeof fileInputId !== 'string') {
			throw Error('in IE fileInputId must be string');
		}
		const inputEl = document.getElementById(
			fileInputId
		) as HTMLInputElement;
		const u = inputEl.value;
		uri.url = u;
		const pos1 = u.lastIndexOf('/');
		const pos2 = u.lastIndexOf('\\');
		const pos = Math.max(pos1, pos2);
		if (pos < 0) uri.filename = u;
		else uri.filename = u.substring(pos + 1);
	}
	const index = uri.filename.lastIndexOf('.');
	if (index !== -1) {
		uri.filetype = uri.filename.substring(index + 1).toLowerCase();
	}
	return uri as FileObj;
}

export interface UploadFile {
	onFileUploadProgress?: (msg: any) => void;
	onFileUploadComplete?: (msg: any) => void;
	onFileUploadError?: (msg: any) => void;
	onFileUploadCanceled?: () => void;
	accessToken: string;
	appKey: string;
	apiUrl: string;
	uploadUrl?: string;
	file: any;
	thumbnailWidth?: number;
	thumbnailHeight?: number;
}

/**
 * @description upload file
 */
export function uploadFile(
	this: Connection,
	options: UploadFile,
	operateName?: OperationName
): void {
	const acc = options.accessToken;
	const appKey = options.appKey;
	const fileSize = options?.file?.data?.size;
	let devInfos: string[] = [];
	let orgName = '';
	let appName = '';
	if (!acc) {
		options.onFileUploadError &&
			options.onFileUploadError({
				type: Code.WEBIM_UPLOADFILE_NO_LOGIN,
				message: 'AccessToken cannot be empty',
			});
		return;
	}
	if (appKey) {
		devInfos = appKey.split('#');
		orgName = devInfos[0];
		appName = devInfos[1];
	}
	if (!orgName && !appName) {
		options.onFileUploadError &&
			options.onFileUploadError({
				type: Code.WEBIM_UPLOADFILE_ERROR,
				message: this._initWithAppId
					? 'appId illegal'
					: 'AppKey illegal',
			});
		return;
	}
	if (fileSize <= 0) {
		options.onFileUploadError &&
			options.onFileUploadError({
				type: Code.WEBIM_UPLOADFILE_ERROR,
				message: 'fileSize must be greater than 0',
			});
		return;
	}
	// Chatroom or group upload share file, use simple upload.
	if (options.uploadUrl) {
		simpleUpload.call(this, { ...options, orgName, appName, operateName });
		return;
	}

	const sliceSize = SERVER_SLICE_INFO.size || SLICE_SIZE;
	// File size > slice sze, use slice upload.
	if (this.uploadPartEnable && fileSize > sliceSize * 1.5) {
		new SliceUploadFile(this, {
			file: options?.file?.data,
			onFileUploadProgress: options.onFileUploadProgress || EMPTYFN,
			onFileUploadComplete: options.onFileUploadComplete || EMPTYFN,
			onFileUploadError: options.onFileUploadError || EMPTYFN,
			onFileUploadCanceled: options.onFileUploadCanceled || EMPTYFN,
			onInitFail: () => {
				// Fail in slice upload init, use simple Upload
				simpleUpload.call(this, {
					...options,
					orgName,
					appName,
					operateName,
				});
			},
			thumbnailInfo: {
				width: options.thumbnailWidth,
				height: options.thumbnailHeight,
			},
		});
		return;
	}
	// Default simple upload
	simpleUpload.call(this, { ...options, orgName, appName, operateName });
}

export interface DownloadParams {
	url: string;
	onFileDownloadComplete?: (data: any) => void;
	onFileDownloadError?: (error: any) => void;
	id?: string;
	secret?: string;
	headers?: { [key: string]: any };
	[key: string]: any;
}

/**
 * @description download file
 */
export function download(
	this: any,
	options: DownloadParams,
	operateName?: OperationName
): void {
	options.onFileDownloadComplete = options.onFileDownloadComplete || EMPTYFN;
	options.onFileDownloadError = options.onFileDownloadError || EMPTYFN;
	const startTime = new Date().getTime();
	const xhr = new XMLHttpRequest();
	// eslint-disable-next-line @typescript-eslint/no-this-alias
	const that = this;
	const onError = function (e: any) {
		const elapse = new Date().getTime() - startTime;
		// 存在操作名称则上报
		if (
			that.dataReport &&
			operateName &&
			operateName === OperationName.DOWN_GROUP_FILE
		) {
			const rpt = that.dataReport.geOperateFun({
				operationName: operateName,
			});
			rpt({
				isEndApi: true,
				data: {
					isSuccess: 0,
					requestMethod: 'POST',
					requestName: operateName,
					requestElapse: elapse,
					requestUrl: options?.url,
					code: xhr?.status || 0,
					codeDesc: 'download file error',
				},
			});
		}
		options.onFileDownloadError &&
			options.onFileDownloadError({
				type: Code.WEBIM_DOWNLOADFILE_ERROR,
				id: options.id,
				xhr: xhr,
			});
	};

	xhr.addEventListener(
		'load',
		function (e) {
			const elapse = new Date().getTime() - startTime;
			if (
				that.dataReport &&
				operateName &&
				operateName === OperationName.DOWN_GROUP_FILE
			) {
				const rpt = that.dataReport.geOperateFun({
					operationName: operateName,
				});
				rpt({
					isEndApi: true,
					data: {
						isSuccess: xhr.status === 200 ? 1 : 0,
						requestMethod: 'POST',
						requestName: operateName,
						requestElapse: elapse,
						requestUrl: options?.url,
						code: xhr.status,
						codeDesc:
							xhr.status === 200 ? '' : 'download file error',
					},
				});
			}
			options.onFileDownloadComplete &&
				options.onFileDownloadComplete(xhr.response);
		},
		false
	);
	xhr.addEventListener('error', onError, false);

	const method = options.method || 'GET';
	const resType = options.responseType || 'blob';
	const mimeType = options.mimeType || 'text/plain; charset=x-user-defined';
	xhr.open(method, options.url);
	if (typeof Blob !== 'undefined') {
		xhr.responseType = resType;
	} else {
		xhr.overrideMimeType(mimeType);
	}
	const innerHeaer: { [key: string]: any } = {
		'X-Requested-With': 'XMLHttpRequest',
		Accept: 'application/octet-stream',
		'share-secret': options.secret,
		Authorization: 'Bearer ' + this?.context?.accessToken,
	};
	const headers = options.headers || {};
	for (const key in headers) {
		innerHeaer[key] = headers[key];
	}
	for (const key in innerHeaer) {
		if (innerHeaer[key]) {
			xhr.setRequestHeader(key, innerHeaer[key]);
		}
	}
	// fix: ie8 status error
	// window.XDomainRequest && (xhr.readyState = 2)
	xhr.send(null);
}

