/** @hidden */
export function Uint8ArrayToString(fileData: any) {
	let dataString = '';
	for (let i = 0; i < fileData.length; i++) {
		dataString += String.fromCharCode(fileData[i]);
	}
	return dataString;
}
/**
 * change the url domain address to `this.apiUrl`
 * @param url attach url
 * @returns url
 */
function formatAttachUrl(this: Connection, url: string) {
	if (url && typeof url === 'string') {
		return `${this.apiUrl}${url.substr(url.indexOf('/', 9))}`;
	}
	return '';
}

type Fns = (...args: any) => any;

/** @hidden */
function flow(this: Utils, fns: Fns[]) {
	const len = fns.length;
	// 检查所有参数是否为函数
	let index = len;
	while (index--) {
		if (typeof fns[index] !== 'function') {
			throw new TypeError('Expected a function');
		}
	}

	return function (this: Utils, ...args: any[]) {
		let index = 0;
		let reslut = len ? fns[index].apply(this, args) : args[0];
		while (++index < len) {
			reslut = fns[index].call(this, reslut);
		}
		return reslut;
	};
}

/**
 *
 * @param response the download response
 * @returns the
 */
function parseDownloadResponse(this: Connection, response: any): string {
	if (!(window && window.URL)) {
		throw Error('parseDownloadResponse can be used in broswer only');
	}
	return (response &&
		response.type &&
		response.type === 'application/json') ||
		0 > Object.prototype.toString.call(response).indexOf('Blob')
		? this.url + '?token='
		: window.URL.createObjectURL(response);
}

function parseNotify(payload: number[]) {
	let encoded = '';
	for (let i = 0; i < payload.length; i++) {
		encoded += '%' + payload[i].toString(16);
	}
	// const data = String.fromCharCode(...payload);
	return JSON.parse(decodeURIComponent(encoded));
}

interface ExtraInfo {
	elapse?: number;
	httpCode?: number;
	errDesc?: string;
}

interface ApiRptInfo {
	requestElapse: number;
	code: number;
	codeDesc: string;
}

function getExtraData(extraInfo: ExtraInfo = {}): ApiRptInfo {
	const { elapse = 0, httpCode = 0, errDesc = '' } = extraInfo;
	return {
		requestElapse: elapse,
		code: httpCode,
		codeDesc: errDesc,
	};
}
const LOCAL_DEVICE_INFO = 'localDeviceInfo';

function getLocalData(key: string): string {
	const envInfo = utils.getEnvInfo();
	const { platform } = envInfo;
	let value = '';
	if (platform === PLATFORM.NODE || platform === PLATFORM.QUICK_APP) {
		value = '';
	}
	try {
		if (platform === PLATFORM.WEB) {
			value = localStorage.getItem(key) || '';
		} else if (
			platform === PLATFORM.WX ||
			platform === PLATFORM.QQ ||
			platform === PLATFORM.TT ||
			platform === PLATFORM.BAIDU ||
			platform === PLATFORM.UNI
		) {
			value = envInfo.global.getStorageSync(key) || '';
		} else if (platform === PLATFORM.ZFB || platform === PLATFORM.DD) {
			value =
				envInfo.global.getStorageSync({
					key: key,
				})?.data || '';
		}
	} catch (error) {
		logger.debug(`get local ${key} failed: `, error);
	}
	logger.debug(`${key} `, value);
	return value;
}

function setLocalData(key: string, value: string) {
	const envInfo = utils.getEnvInfo();
	const { platform } = envInfo;
	if (platform === PLATFORM.NODE || platform === PLATFORM.QUICK_APP) {
		return;
	}
	if (platform === PLATFORM.WEB) {
		try {
			localStorage.setItem(key, value);
		} catch (error) {
			logger.error(`set local ${key} failed: `, error);
		}
	} else {
		// async setting storage
		envInfo.global.setStorage({
			key: key,
			data: value,
			success: (res: any) => {
				logger.debug(`set local ${key} success: `, res);
			},
			fail: (error: any) => {
				logger.error(`set local ${key} failed: `, error);
			},
		});
	}
}

function getLocalDeviceInfo(): string {
	return getLocalData(LOCAL_DEVICE_INFO);
}

function setLocalDeviceInfo(devInfoString: string) {
	setLocalData(LOCAL_DEVICE_INFO, devInfoString);
}

function getDevicePlatform(envInfo: EnvInfo): string {
	if (
		envInfo.platform === PLATFORM.WX &&
		//@ts-ignore
		typeof uni !== 'undefined' &&
		//@ts-ignore
		hasMiniBaseEvent(uni)
	) {
		return PLATFORM.UNI;
	}
	return envInfo.platform;
}
