const detectBrowser = () => {
	if (typeof navigator === 'undefined') {
		return 'unknown';
	}
	const userAgent = navigator.userAgent;

	// 检查微信内置浏览器
	if (/MicroMessenger/i.test(userAgent)) {
		return 'WeChat';
	}

	// 检查QQ浏览器
	if (/QQBrowser/i.test(userAgent)) {
		return 'QQ';
	}

	// 检查Chrome浏览器，注意要排除Edge和Opera
	if (
		/Chrome/i.test(userAgent) &&
		!/Edg/i.test(userAgent) &&
		!/OPR/i.test(userAgent)
	) {
		return 'Chrome';
	}

	// 检查Safari浏览器
	if (
		/Safari/i.test(userAgent) &&
		!/Chrome/i.test(userAgent) &&
		!/CriOS/i.test(userAgent)
	) {
		return 'Safari';
	}

	// 检查Firefox浏览器
	if (/Firefox/i.test(userAgent)) {
		return 'Firefox';
	}

	// 检查IE浏览器
	if (/MSIE/i.test(userAgent) || /Trident/i.test(userAgent)) {
		return 'IE';
	}

	// 检查Edge浏览器
	if (/Edg/i.test(userAgent)) {
		return 'Edge';
	}

	return 'unknown';
};

function delay(ms: number) {
	return new Promise((resolve) => setTimeout(resolve, ms));
}

// promise retry
function retryPromise<T>(
	promiseFunction: () => Promise<T>,
	// 失败后的重试次数,默认重试3次
	retries?: number,
	// 重试间隔时间，默认间隔1秒
	delay?: number
): Promise<T> {
	return new Promise((resolve, reject) => {
		const attempt = (retryCount: number) => {
			promiseFunction()
				.then(resolve)
				.catch((error) => {
					if (retryCount > 0) {
						setTimeout(() => {
							attempt(retryCount - 1);
						}, delay || 1000);
					} else {
						reject(error);
					}
				});
		};
		attempt(retries || 3);
	});
}

function isUseHttps(optionsHttps?: boolean): boolean {
	// If https is explicitly set in options, use that value
	if (typeof optionsHttps === 'boolean') {
		return optionsHttps;
	}

	// Check if we're in a browser environment and use HTTPS if the current protocol is HTTPS
	const isBrowser = typeof window !== 'undefined';
	return isBrowser ? window.location?.protocol === 'https:' : true;
}

/**
 * 计算重试延迟时间(秒)
 */
function getRetryDelay(attempts: number): number {
	if (attempts <= 5) {
		return Number((Math.random() + 1).toFixed(2)); // 1-2秒
	}
	if (attempts > 5 && attempts < 20) {
		return Math.floor(Math.random() * 4) + 3; // 3-6秒
	}
	return Math.floor(Math.random() * 6) + 5; // 5-10秒
}

