import { ConfigParams } from './types';
import { request } from './utils';

type EventItem = {
	eventId: number;
	biz: string;
	appId: string;
	[key: string]: any;
};

const STOCK = 1000;

export class EventQueue {
	private eventQueue = [] as EventItem[];
	private timer?: any;
	private config: ConfigParams;
	private stock = STOCK; // 每分钟限制上报数量 1000

	constructor(config: ConfigParams) {
		this.config = config;
		this.governor();
	}

	add(params: EventItem) {
		if (this.stock <= 0) {
			console.warn(`Event Report limit ${STOCK} per minute`);
			return;
		}
		this.eventQueue.push(params);
		this.consume();
		this.stock -= 1;
	}

	consume() {
		if (this.eventQueue.length === 0) {
			return;
		}
		if (this.timer && this.eventQueue.length <= 10) {
			clearTimeout(this.timer);
		}
		this.timer = setTimeout(() => {
			const batchEvents = this.eventQueue.splice(0, 10);
			const reportEvents = batchEvents.filter(
				(item) => item.appId === batchEvents[0].appId
			);
			const filterEvents = batchEvents.filter(
				(item) => item.appId !== batchEvents[0].appId
			);
			this.eventQueue.unshift(...filterEvents);
			this.batchSend(reportEvents);
		}, 1000);
	}

	private governor() {
		const timer = setInterval(() => {
			this.stock = STOCK;
		}, 60 * 1000);
		if (typeof addEventListener !== 'undefined') {
			addEventListener('beforeunload', () => {
				clearInterval(timer);
			});
		}
	}

	private batchSend(params: EventItem[] = []) {
		if (params.length === 0) {
			return;
		}
		try {
			const jsons = params.map((item) => {
				const { biz, appId, eventId, ...restParams } = item;
				return {
					eventId: Number(eventId),
					body: restParams,
				};
			});

			const dt = jsons;
			request(this.config.token, {
				biz: params[0]?.biz,
				appId: params[0]?.appId,
				data: dt,
				debug: this.config.debug,
				onSuccess: this.consume.bind(this),
			});
		} catch (err) {
			console.error(err);
		}
	}
}
