import { PLATFORM, EnvInfo, utils } from '../utils';
import { WEB_IM_OS } from './const';
import {
	EmBaseOptions,
	InitOptions,
	NetworkType,
	SdkProductType,
	OperationType,
} from './types';

const getOperationType = (name: any): string => {
	const idx = Number(OperationType[name]);
	if (idx === OperationType.USER_LOGIN) return 'MANUALLOGIN';
	if (idx === OperationType.MSYNC_SENDMESSAGE) return 'SENDMESSAGE';
	if (
		idx > OperationType.UNKNOWOPERATION &&
		idx < OperationType.REST_OPERATE
	) {
		return 'REST';
	}
	if (idx > OperationType.REST_OPERATE && idx < OperationType.MSYNC_OPERATE) {
		return 'MESSAGE';
	}
	if (
		idx > OperationType.MSYNC_OPERATE &&
		idx < OperationType.ROSTER_OPERATE
	) {
		return 'ROSTER';
	}
	if (
		idx > OperationType.ROSTER_OPERATE &&
		idx < OperationType.USER_OPERATE
	) {
		return 'USER';
	}
	if (idx > OperationType.USER_OPERATE && idx < OperationType.GROUP_OPERATE) {
		return 'GROUP';
	}
	if (
		idx > OperationType.GROUP_OPERATE &&
		idx < OperationType.CHATROOM_OPERATE
	) {
		return 'CHATROOM';
	}
	return 'OPERATION';
};

const getTimestamp = (): number => {
	return new Date().getTime();
};

const isMiniProgram = (envInfo: EnvInfo) => {
	return [
		PLATFORM.BAIDU,
		PLATFORM.WX,
		PLATFORM.DD,
		PLATFORM.ZFB,
		PLATFORM.TT,
		PLATFORM.QUICK_APP,
		PLATFORM.UNI,
	].includes(envInfo.platform);
};

const getNetworkType = (envInfo: EnvInfo): Promise<number | string> => {
	return new Promise((resolve) => {
		if (!envInfo.platform) {
			envInfo = utils.getEnvInfo();
		}
		let netType: number | string = '';
		if (envInfo.platform === PLATFORM.WEB) {
			// @ts-ignore
			const connection: any = navigator!.connection;
			if (connection?.type) {
				connection?.type === 'wifi';
				netType = NetworkType.WIFI;
			} else {
				if (connection?.effectiveType) {
					netType =
						NetworkType[
							connection.effectiveType.toLocaleUpperCase()
						];
				}
			}
			resolve(netType);
		} else if (envInfo.platform === PLATFORM.NODE) {
			netType = NetworkType.UNKNOWN;
			resolve(netType);
		} else {
			envInfo.global?.getNetworkType({
				success: (res: { networkType: any }) => {
					netType = NetworkType[res.networkType.toLocaleUpperCase()];
					resolve(netType);
				},
			});
		}
	});
};

// 获取两个值之间的随机整数
const getRandomInt = (min: number, max: number) => {
	min = Math.ceil(min);
	max = Math.floor(max);
	return Math.floor(Math.random() * (max - min)) + min; //不含最大值，含最小值
};

// 获取1-99999随机数
const getRandom = () => {
	return getRandomInt(1, 99999);
};

const getBaseOptions = (opt: InitOptions, envInfo: EnvInfo): EmBaseOptions => {
	if (!envInfo.platform) {
		envInfo = utils.getEnvInfo();
	}
	const { org, appkey, deviceId, sdkVersion } = opt;
	const did =
		typeof navigator !== 'undefined'
			? navigator?.userAgent
			: `${envInfo.platform}_mini_program`;
	const sdkServiceId = `sid_${utils.getUniqueId()}_${getRandom()}`;
	return {
		org,
		appkey,
		deviceId,
		sdkServiceId,
		did,
		sdkVersion,
		os: WEB_IM_OS,
		sdkProduct: SdkProductType.web,
	};
};

export {
	getOperationType,
	getTimestamp,
	getNetworkType,
	getBaseOptions,
	getRandom,
	isMiniProgram,
	getRandomInt,
};
