import { Code } from '../status';
import { Connection } from '../engineCore/connection';
import { UploadFile, EMPTYFN } from '../utils/index';
import { OperationName } from '../dataReport/types';
import { UPLOAD_TIMEOUT } from '../constant';
function simpleUpload(
	this: Connection,
	options: UploadFile & {
		orgName: string;
		appName: string;
		operateName?: OperationName;
	}
) {
	const startTime = new Date().getTime();
	const apiUrl = options.apiUrl;
	const { orgName, appName, operateName, accessToken } = options;
	const uploadUrl =
		options.uploadUrl || `${apiUrl}/${orgName}/${appName}/chatfiles`;
	const onUploadError = (errInfo: { type: number; data: any }) => {
		const elapse = new Date().getTime() - startTime;
		// 存在操作名称则上报
		if (
			this.dataReport &&
			operateName &&
			[
				OperationName.UPLOAD_MSG_ATTACH,
				OperationName.UPLOAD_CHATROOM_FILE,
				OperationName.UPLOAD_GROUP_FILE,
			].includes(operateName)
		) {
			const rpt = this.dataReport.geOperateFun({
				operationName: operateName,
			});
			rpt({
				isEndApi: true,
				data: {
					isSuccess: 0,
					requestMethod: 'POST',
					requestName: operateName,
					requestElapse: elapse,
					requestUrl: uploadUrl,
					code: xhr?.status || 0,
					codeDesc: 'upload file error',
				},
			});
		}
		options.onFileUploadError && options.onFileUploadError(errInfo);
	};

	const onUploadComplete = (json: any) => {
		const elapse = new Date().getTime() - startTime;
		// 存在操作名称则上报
		if (
			this.dataReport &&
			operateName &&
			[
				OperationName.UPLOAD_MSG_ATTACH,
				OperationName.UPLOAD_CHATROOM_FILE,
				OperationName.UPLOAD_GROUP_FILE,
			].includes(operateName)
		) {
			const rpt = this.dataReport.geOperateFun({
				operationName: operateName,
			});
			rpt({
				isEndApi: true,
				data: {
					isSuccess: json?.error ? 0 : 1,
					requestMethod: 'POST',
					requestName: operateName,
					requestElapse: elapse,
					requestUrl: uploadUrl,
					code: xhr.status,
					codeDesc: json?.error_description || '',
				},
			});
		}
		options.onFileUploadComplete && options.onFileUploadComplete(json);
	};

	function onError(e: any) {
		onUploadError({
			type: Code.WEBIM_UPLOADFILE_ERROR,
			data: xhr,
		});
	}

	const xhr = new XMLHttpRequest();
	if (xhr.upload) {
		xhr.upload.addEventListener?.(
			'progress',
			options.onFileUploadProgress || EMPTYFN,
			false
		);
	}
	xhr.addEventListener(
		'abort',
		options.onFileUploadCanceled || EMPTYFN,
		false
	);
	xhr.addEventListener('error', onError, false);

	xhr.addEventListener(
		'load',
		function (e) {
			try {
				const json = JSON.parse(xhr.responseText);
				if (xhr.status === 400) {
					onUploadError({
						type: Code.WEBIM_UPLOADFILE_ERROR,
						data: json,
					});
					return false;
				}
				try {
					onUploadComplete(json);
				} catch (e) {
					onUploadError({
						type: Code.WEBIM_CONNCTION_CALLBACK_INNER_ERROR,
						data: e,
					});
				}
			} catch (e) {
				onUploadError({
					type: Code.WEBIM_UPLOADFILE_ERROR,
					data: xhr.responseText,
				});
			}
		},
		false
	);
	xhr.addEventListener('timeout', onError, false);
	xhr.timeout = UPLOAD_TIMEOUT;
	xhr.open('POST', uploadUrl);
	xhr.setRequestHeader('restrict-access', 'true');
	xhr.setRequestHeader('Accept', '*/*'); // Android QQ browser has some problem with this attribute.
	xhr.setRequestHeader('Authorization', 'Bearer ' + accessToken);
	const formData = new FormData();
	formData.append('file', options.file.data);
	if (options.thumbnailWidth) {
		formData.append('thumbnail-width', options.thumbnailWidth + '');
	}
	if (options.thumbnailHeight) {
		formData.append('thumbnail-height', options.thumbnailHeight + '');
	}
	xhr.send(formData);
}

export default simpleUpload;
