import { checkStatus } from './utils';
import { Code } from '../status';
import ErrorEvent from '../error/error';
import { ajax } from '../utils';
import * as IndexTypes from '../types/indexApi';
import { AsyncResult } from '../types/common';
import { logger } from '../logger';
import { OperationName } from '../dataReport/types';
import { utils } from '../utils/index';

function uploadPushTokenToServer(
	this: any,
	params: {
		/** The device ID that identifies the device. Custom device IDs are allowed. */
		/** 设备 ID，用于标识设备。用户可自定义设备 ID。 */
		deviceId: string;
		/** The push token, which identifies a device during message push. Custom push tokens are allowed.*/
		/** 推送 token，用于标识相同的设备。用户可自定义该推送 token。 */
		deviceToken: string;
		/** The app ID for the push service, which is the senderID for Firebase Cloud Messaging (FCM) and "appId+#+AppKey" for the vivo push service. */
		/** 推送服务的 App ID，对于 Firebase Cloud Messaging (FCM) 是 senderId, 对于 VIVO 推送服务是 “appId+#+AppKey”。 */
		notifierName: string;
		success?: (res: AsyncResult<IndexTypes.UploadTokenResult>) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<IndexTypes.UploadTokenResult>> {
	if (typeof params.deviceId !== 'string' || params.deviceId === '') {
		throw Error('Invalid parameter: "deviceId"');
	}
	const { error } = checkStatus.call(this);
	if (error) {
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken } = this.context;
	const opts = {
		url: `${this.apiUrl}/${orgName}/${appName}/users/${this.user}`,
		type: 'PUT',
		data: JSON.stringify({
			device_id: params.deviceId,
			device_token: params.deviceToken,
			notifier_name: params.notifierName,
		}),
		dataType: 'json',
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
		success: params.success,
		error: params.error,
	};
	logger.debug('Call uploadPushTokenToServer', params);
	const func = ajax.bind(this, opts, OperationName.UPLOAD_PUSH_TOKEN);
	return utils.retryPromise(func).then((res) => {
		const data: IndexTypes.UploadTokenResult = res.entities[0] || {};
		return {
			type: res.type,
			data: data,
		};
	});
}

export { uploadPushTokenToServer };
