import { Code } from '../status';
import { Connection } from '../engineCore/connection';
import { logger } from '../logger';
import { ajax } from '../utils';
import { AsyncResult } from '../types/common';
import { OperationName } from '../dataReport/types';
import { UPLOAD_TIMEOUT } from '../constant';

interface SliceUploadFileInfo {
	/** file max size . */
	/** 文件最大限制 。 */
	fileMaxSize: number;
	/** part min size . */
	/** 分块最小限制 。*/
	partMinSize: number;
	/** file uuid . */
	/** 文件uuid 。*/
	uuid: string;
}

interface UploadPartParam {
	/** file uuid . */
	/** 文件uuid 。*/
	uuid: string;
	/** part number . */
	/** 分块序号 。*/
	partNumber: string;
	/** part . */
	/** 分块数据 。*/
	part: Blob;
	/** progress callback . */
	/** 进度回调 。*/
	onProgress: (e: ProgressEvent<XMLHttpRequestEventTarget>) => void;
}

/**
 *  初始化分块上传
 */
function multipartInit(
	this: Connection
): Promise<AsyncResult<SliceUploadFileInfo>> {
	const { orgName, appName, accessToken } = this.context;
	const url = `${this.apiUrl}/${orgName}/${appName}/sdk/chatfiles/part-upload`;
	const opts = {
		url: url,
		dataType: 'json',
		type: 'POST',
		headers: {
			Authorization: 'Bearer ' + accessToken,
		},
	};
	logger.debug('Call multipartInit');
	return ajax.call(this, opts, OperationName.SDK_INTERNAL).then((res) => {
		const {
			file_upper_limit: fileMaxSize,
			part_lower_limit: partMinSize,
			uuid,
		} = res.entities[0];
		return {
			data: {
				fileMaxSize,
				partMinSize,
				uuid,
			},
			extraInfo: res.extraInfo,
			type: res.type,
		};
	});
}

/**
 * 上传分片
 */
function multipartUpload(
	this: Connection,
	param: UploadPartParam
): Promise<Record<string, any>> {
	return new Promise((resolve, reject) => {
		const startTime = new Date().getTime();
		const { orgName, appName, accessToken } = this.context;
		const { uuid, partNumber, part, onProgress } = param;
		const url = `${this.apiUrl}/${orgName}/${appName}/sdk/chatfiles/part-upload/${uuid}`;
		const xhr = new XMLHttpRequest();
		logger.debug('Call multipartUpload');
		if (xhr.upload) {
			xhr.upload.addEventListener?.(
				'progress',
				(e) => {
					onProgress?.(e);
				},
				false
			);
		}
		xhr.addEventListener(
			'abort',
			() => {
				reject({
					type: Code.REQUEST_ABORT,
					message: 'Request Abort',
					errorType: 'onabort',
					xhr: xhr,
					extraInfo: {
						elapse: new Date().getTime() - startTime,
						httpCode: xhr.status || -1,
						errDesc: 'Request Abort',
						url,
					},
				});
			},
			false
		);
		xhr.addEventListener(
			'error',
			() => {
				reject({
					type: Code.WEBIM_UPLOADFILE_ERROR,
					data: xhr,
					extraInfo: {
						elapse: new Date().getTime() - startTime,
						httpCode: xhr.status || -1,
						errDesc: 'request error',
						url,
					},
				});
			},
			false
		);

		xhr.addEventListener(
			'load',
			function () {
				try {
					const json = JSON.parse(xhr.responseText);
					if (xhr.status !== 200) {
						reject({
							type: Code.WEBIM_UPLOADFILE_ERROR,
							data: json,
							extraInfo: {
								elapse: new Date().getTime() - startTime,
								httpCode: xhr.status || -1,
								errDesc: 'part upload failed',
								url,
							},
						});
						return false;
					}
					try {
						resolve({
							...json,
							extraInfo: {
								elapse: new Date().getTime() - startTime,
								httpCode: xhr.status || -1,
								url,
							},
						});
					} catch (e) {
						reject({
							type: Code.WEBIM_CONNCTION_CALLBACK_INNER_ERROR,
							data: e,
							extraInfo: {
								elapse: new Date().getTime() - startTime,
								httpCode: xhr.status || -1,
								errDesc: 'part upload failed',
								url,
							},
						});
					}
				} catch (e) {
					reject({
						type: Code.WEBIM_UPLOADFILE_ERROR,
						data: xhr.responseText,
						extraInfo: {
							elapse: new Date().getTime() - startTime,
							httpCode: xhr.status || -1,
							errDesc: 'part upload failed',
							url,
						},
					});
				}
			},
			false
		);
		xhr.addEventListener(
			'timeout',
			() => {
				reject({
					type: Code.REQUEST_TIMEOUT,
					message: 'request timeout',
					extraInfo: {
						elapse: new Date().getTime() - startTime,
						httpCode: xhr.status || -1,
						errDesc: 'request timeout',
						url,
					},
				});
			},
			false
		);
		const formData = new FormData();
		formData.append('part_file', part);
		formData.append('part_number', partNumber);
		xhr.timeout = UPLOAD_TIMEOUT;
		xhr.open('PUT', url);
		xhr.setRequestHeader('restrict-access', 'true');
		xhr.setRequestHeader('Accept', '*/*');
		xhr.setRequestHeader('Authorization', 'Bearer ' + accessToken);
		xhr.send(formData);
	});
}
/**
 * 完成分块文件上传
 */
function multipartComplete(
	this: Connection,
	param: {
		uuid: string;
		thumbnailWidth?: number;
		thumbnailHeight?: number;
	}
): Promise<AsyncResult<any>> {
	const { uuid, thumbnailHeight, thumbnailWidth } = param;
	const { orgName, appName, accessToken } = this.context;
	let url = `${this.apiUrl}/${orgName}/${appName}/sdk/chatfiles/part-upload/${uuid}?restrict-access=true`;
	if (thumbnailWidth) {
		url += `&thumbnail-width=${thumbnailWidth}`;
	}
	if (thumbnailHeight) {
		url += `&thumbnail-height=${thumbnailHeight}`;
	}
	const opts = {
		url: url,
		dataType: 'json',
		type: 'POST',
		headers: {
			Authorization: 'Bearer ' + accessToken,
		},
	};
	logger.debug('Call multipartComplete');
	return ajax.call(this, opts, OperationName.SDK_INTERNAL);
}

/**
 * 终止分块上传
 */

function multipartAbort(
	this: Connection,
	param: {
		uuid: string;
	}
): Promise<AsyncResult<any>> {
	const { uuid } = param;
	const { orgName, appName, accessToken } = this.context;
	const url = `${this.apiUrl}/${orgName}/${appName}/sdk/chatfiles/part-upload/${uuid}`;
	const opts = {
		url: url,
		dataType: 'json',
		type: 'DELETE',
		headers: {
			Authorization: 'Bearer ' + accessToken,
		},
	};
	logger.debug('Call multipartAbort');
	return ajax.call(this, opts, OperationName.SDK_INTERNAL);
}

export { multipartInit, multipartUpload, multipartComplete, multipartAbort };
