/**
 * The error codes in the SDK.
 * Defines error codes commonly used in the SDK.
 * @module StatusCode
 */

enum Code {
	/** No error. */
	/** 无错误。 */
	REQUEST_SUCCESS = 0, // 请求成功
	/** The server response times out. */
	/** 请求服务超时。 */
	REQUEST_TIMEOUT = -1, // 请求超时
	/** A general error.  */
	/** 默认未区分类型的错误。 */
	REQUEST_UNKNOWN = -2, // 请求未知错误
	/** The parameter is invalid. */
	/** 参数错误。 */
	REQUEST_PARAMETER_ERROR = -3, // 请求参数错误
	/** The request is canceled. */
	/** 取消请求。*/
	REQUEST_ABORT = -4, // 取消请求
	/** @hidden */
	WEBIM_CONNCTION_USER_NOT_ASSIGN_ERROR = 0, // 用户 ID 为空
	/** Login failed.  */
	/** 登录失败。 */
	WEBIM_CONNCTION_OPEN_ERROR = 1, // 如：用户不存在或密码错误。
	/** The user authentication fails. The reasons are as follows. The SDK is not initiated. The user is not login. */
	/** 鉴权失败，如：SDK 未初始化或用户未登录就调用 API 进行其他操作。 */
	WEBIM_CONNCTION_AUTH_ERROR = 2, // 如：SDK 未初始化或用户未登录就调用 API 进行其他操作。
	/** @hidden */
	WEBIM_CONNCTION_OPEN_USERGRID_ERROR = 3, // unused
	/** @hidden */
	WEBIM_CONNCTION_ATTACH_ERROR = 4, // unused
	/** @hidden */
	WEBIM_CONNCTION_ATTACH_USERGRID_ERROR = 5, // unused
	/** @hidden */
	WEBIM_CONNCTION_REOPEN_ERROR = 6, // unused
	/** @hidden */
	WEBIM_CONNCTION_SERVER_CLOSE_ERROR = 7, //7 = client-side network offline (netE= RR_INTERNET_DISCONNECTED) unused
	/** @hidden */
	WEBIM_CONNCTION_SERVER_ERROR = 8, //8 = offline by multi login  unused
	/** @hidden */
	WEBIM_CONNCTION_IQ_ERROR = 9, // unused
	/** @hidden */
	WEBIM_CONNCTION_PING_ERROR = 10, // unused
	/** @hidden */
	WEBIM_CONNCTION_NOTIFYVERSION_ERROR = 11, // unused
	/** Fails to get the token. */
	/** 获取 Chat token 失败。 */
	WEBIM_CONNCTION_GETROSTER_ERROR = 12,
	/** @hidden */
	WEBIM_CONNCTION_CROSSDOMAIN_ERROR = 13, // unused
	/** @hidden */
	WEBIM_CONNCTION_LISTENING_OUTOF_MAXRETRIES = 14, // unused
	/** @hidden */
	WEBIM_CONNCTION_RECEIVEMSG_CONTENTERROR = 15, // unused
	/** Websocket is disconnected */
	/** WebSocket 断开连接。 */
	WEBIM_CONNCTION_DISCONNECTED = 16, // 16 = server-side close the websocket connection
	/** A general error.  */
	/** 默认未区分类型的错误。（用 -2 替换） */
	WEBIM_CONNCTION_AJAX_ERROR = 17, // ajax error
	/** @hidden */
	WEBIM_CONNCTION_JOINROOM_ERROR = 18, // unused
	/** @hidden */
	WEBIM_CONNCTION_GETROOM_ERROR = 19, // unused
	/** @hidden */
	WEBIM_CONNCTION_GETROOMINFO_ERROR = 20, // unused
	/** @hidden */
	WEBIM_CONNCTION_GETROOMMEMBER_ERROR = 21, // unused
	/** @hidden */
	WEBIM_CONNCTION_GETROOMOCCUPANTS_ERROR = 22, // unused
	/** @hidden */
	WEBIM_CONNCTION_LOAD_CHATROOM_ERROR = 23, // 3.0 获取聊天室失败，拉取历史消息失败（后面去掉）
	/** @hidden */
	WEBIM_CONNCTION_NOT_SUPPORT_CHATROOM_ERROR = 24, // unused
	/** @hidden */
	WEBIM_CONNCTION_JOINCHATROOM_ERROR = 25, // unused
	/** @hidden */
	WEBIM_CONNCTION_QUITCHATROOM_ERROR = 26, // unused
	/** The App Key is invalid. */
	/** 未设置 App Key。 */
	WEBIM_CONNCTION_APPKEY_NOT_ASSIGN_ERROR = 27,
	/** The token is invalid. */
	/** token无效。 */
	WEBIM_CONNCTION_TOKEN_NOT_ASSIGN_ERROR = 28,
	/** @hidden */
	WEBIM_CONNCTION_SESSIONID_NOT_ASSIGN_ERROR = 29, // unused
	/** @hidden */
	WEBIM_CONNCTION_RID_NOT_ASSIGN_ERROR = 30, // unused
	/** The callback inner error code while the message is successfully sent. */
	/** 消息发送成功的回调函数内部错误。 */
	WEBIM_CONNCTION_CALLBACK_INNER_ERROR = 31, //31 = 处理下行消息出错 try/catch抛出异常
	/** The current user is offline. */
	/** 当前用户未登录。*/
	WEBIM_CONNCTION_CLIENT_OFFLINE = 32, //32 = client offline
	/** @hidden */
	WEBIM_CONNCTION_CLIENT_LOGOUT = 33, // unused 33 = client logout
	/** @hidden */
	WEBIM_CONNCTION_CLIENT_TOO_MUCH_ERROR = 34, // unused 34 = Over amount of the tabs a user opened in the same browser
	/** @hidden */
	WEBIM_CONNECTION_ACCEPT_INVITATION_FROM_GROUP = 35, // unused
	/** @hidden */
	WEBIM_CONNECTION_DECLINE_INVITATION_FROM_GROUP = 36, // unused
	/** @hidden */
	WEBIM_CONNECTION_ACCEPT_JOIN_GROUP = 37, // unused
	/** @hidden */
	WEBIM_CONNECTION_DECLINE_JOIN_GROUP = 38, // unused
	/** The user is not logged in.*/
	/** 未登录状态。 */
	WEBIM_CONNECTION_CLOSED = 39,
	/** The user authentication fails. */
	/** 用户鉴权失败。 */
	WEBIM_CONNECTION_ERROR = 40,

	// 50 - 100 加通用的错误码

	/** The upper limit is reached. */
	/** 达到上限。  */
	MAX_LIMIT = 50, // 达到上限，如： reaction 数量已达到限制； 翻译用量达到上限；
	/** The message is not found. */
	/** 没查到消息。 */
	MESSAGE_NOT_FOUND = 51, // 没查到消息，如： 没有查询到要举报的消息；
	/** Unauthorized operation. */
	/** 没有操作权限。 */
	NO_PERMISSION = 52, //用户对当前操作没有权限
	/** Unsupported Operation. */
	/** 不支持的操作。 */
	OPERATION_UNSUPPORTED = 53, //不支持的操作
	/** An operation that is not allowed. */
	/** 不允许的操作。 */
	OPERATION_NOT_ALLOWED = 54, //不允许此次操作
	/** Token expired. */
	/** Token过期。*/
	WEBIM_TOKEN_EXPIRED = 56,
	/** Server serving disabled. */
	/** 服务已禁用。*/
	WEBIM_SERVER_SERVING_DISABLED = 57,
	/** @hidden */
	WEBIM_UPLOADFILE_BROWSER_ERROR = 100, // unused
	/** The uploading of the file failed. */
	/** 上传文件失败。 */
	WEBIM_UPLOADFILE_ERROR = 101,
	/** The current user is not logged in when uploading the file. */
	/** 未登录上传文件。 */
	WEBIM_UPLOADFILE_NO_LOGIN = 102,
	/** @hidden */
	WEBIM_UPLOADFILE_NO_FILE = 103, // unused
	/** File-downloading failed. */
	/** 下载文件失败。 */
	WEBIM_DOWNLOADFILE_ERROR = 200,
	/** @hidden */
	WEBIM_DOWNLOADFILE_NO_LOGIN = 201, // unused
	/** @hidden */
	WEBIM_DOWNLOADFILE_BROWSER_ERROR = 202, // unused
	/** Parse file failed. */
	/** 解析文件失败。 */
	PARSE_FILE_ERROR = 203,
	/** User does not found. */
	/** 用户不存在。 */
	USER_NOT_FOUND = 204, // 用户不存在，如创建群拉人时不存在的用户报错
	/** The message parameter is invalid. */
	/** 消息参数错误。 */
	MESSAGE_PARAMETER_ERROR = 205,
	/** The user is logged in on another device. */
	/** 用户在其他设备登录。 */
	WEBIM_CONNCTION_USER_LOGIN_ANOTHER_DEVICE = 206,
	/** The user was removed. */
	/** 用户已经被注销。 */
	WEBIM_CONNCTION_USER_REMOVED = 207,
	/** The user is already logged in. */
	/** 用户已经登录。 */
	WEBIM_USER_ALREADY_LOGIN = 208,
	/** The password is renewed. */
	/** 用户密码更新。 */
	WEBIM_CONNCTION_USER_KICKED_BY_CHANGE_PASSWORD = 216,
	/** The user was kicked off from another device. */
	/** 用户被踢下线。 */
	WEBIM_CONNCTION_USER_KICKED_BY_OTHER_DEVICE = 217,
	/** Global muted. */
	/** 被全局禁言。 */
	USER_MUTED_BY_ADMIN = 219, // 被全局禁言

	/**
	 * The user is not on your contact list, and you cannot send messages to him or her.
	 * Note: You can send messages to strangers by default. This error occurs only when you enable the function of allowing to send messages only to your contacts.
	 */

	/**
	 * 该用户不是当前用户的好友，不能发送消息。
	 * 注意：默认是可以给陌生人发送消息的，需要开启只有好友才能发送消息的功能，才会报此错。
	 */

	USER_NOT_FRIEND = 221, // 不是好友发消息失败, 设置好友备注的时候，如果不是好友，会报此错误

	// 300 - 317 未启用
	/** @hidden */
	WEBIM_MESSAGE_REC_TEXT = 300,
	/** @hidden */
	WEBIM_MESSAGE_REC_TEXT_ERROR = 301,
	/** @hidden */
	WEBIM_MESSAGE_REC_EMOTION = 302,
	/** @hidden */
	WEBIM_MESSAGE_REC_PHOTO = 303,
	SERVER_GET_DNSLIST_FAILED = 304, //获取DNS失败
	/** @hidden */
	WEBIM_MESSAGE_REC_AUDIO_FILE = 305,
	/** @hidden */
	WEBIM_MESSAGE_REC_VEDIO = 306,
	/** @hidden */
	WEBIM_MESSAGE_REC_VEDIO_FILE = 307,
	/** @hidden */
	WEBIM_MESSAGE_REC_FILE = 308,
	/** @hidden */
	WEBIM_MESSAGE_SED_TEXT = 309,
	/** @hidden */
	WEBIM_MESSAGE_SED_EMOTION = 310,
	/** @hidden */
	WEBIM_MESSAGE_SED_PHOTO = 311,
	/** @hidden */
	WEBIM_MESSAGE_SED_AUDIO = 312,
	/** @hidden */
	WEBIM_MESSAGE_SED_AUDIO_FILE = 313,
	/** @hidden */
	WEBIM_MESSAGE_SED_VEDIO = 314,
	/** @hidden */
	WEBIM_MESSAGE_SED_VEDIO_FILE = 315,
	/** @hidden */
	WEBIM_MESSAGE_SED_FILE = 316,
	/** @hidden */
	WEBIM_MESSAGE_SED_ERROR = 317,

	// 400 - 406 未启用
	/** @hidden */
	STATUS_INIT = 400,
	/** @hidden */
	STATUS_DOLOGIN_USERGRID = 401,
	/** @hidden */
	STATUS_DOLOGIN_IM = 402,
	/** @hidden */
	STATUS_OPENED = 403,
	/** @hidden */
	STATUS_CLOSING = 404,
	/** @hidden */
	STATUS_CLOSED = 405,
	/** @hidden */
	STATUS_ERROR = 406,

	/** The server is busy. */
	/** 服务器忙碌。 */
	SERVER_BUSY = 500, // 服务忙, 限流
	/** The message content contains illegal or sensitive words. */
	/** 消息内容包含非法或敏感词。 */
	MESSAGE_INCLUDE_ILLEGAL_CONTENT = 501, //消息内容包含非法或敏感词
	/** The message was blocked. */
	/** 消息被拦截。 */
	MESSAGE_EXTERNAL_LOGIC_BLOCKED = 502, // 消息被拦截 开通反垃圾服务后，消息被拦截报此错误。
	/** Unknown error. */
	/** 消息发送失败未知错误。 */
	SERVER_UNKNOWN_ERROR = 503, //未知异常
	/** The message recall has exceeded the time limit.*/
	/** 消息撤回超时。 */
	MESSAGE_RECALL_TIME_LIMIT = 504, // 消息撤回超时
	/** The service is not enabled. */
	/** 服务未开启。 */
	SERVICE_NOT_ENABLED = 505, // 服务未开通
	/** The message fails to be delivered because the user is not on the allow list.*/
	/** 用户未在白名单中 */
	SERVICE_NOT_ALLOW_MESSAGING = 506, // 群组或聊天室开启全员禁言时，若用户未在白名单中发送消息时提示该错误。
	/** The current user is muted. */
	/** 当前用户被禁言。 */
	SERVICE_NOT_ALLOW_MESSAGING_MUTE = 507, // @since 4.0  被禁言  在群组或者聊天室中被禁言后发消息报此错误。
	/** The message is blocked by the Moderation service. */
	/** 消息被 Moderation 服务拦截。 */
	MESSAGE_MODERATION_BLOCKED = 508, // moderation 拦截
	/** Group chat ID current limiting. */
	/** 群聊 id 限流 */
	MESSAGE_CURRENT_LIMITING = 509, // 群聊 id 限流
	/** The network is disconnected, causing message sending failure. */
	/** 网络断开连接导致消息发送失败。 */
	MESSAGE_WEBSOCKET_DISCONNECTED = 510,
	/** You have exceeded the maximum allowed size of a message body.*/
	/** 消息体大小超过限制。 */
	MESSAGE_SIZE_LIMIT = 511,
	/** The message sending times out. */
	/** 消息发送超时。 */
	MESSAGE_SEND_TIMEOUT = 512,

	/** The group is not found. */
	/** 群组不存在。 */
	GROUP_NOT_EXIST = 605, // 群组不存在
	/** The user being operated is not in the group. */
	/** 禁言等操作的人员不在群组中。 */
	GROUP_NOT_JOINED = 602, // 禁言等操作的人员不在群组中
	/** The number of members in the group reaches the limit. */
	/** 群组人数达到上限。 */
	GROUP_MEMBERS_FULL = 606, // 群组人数达到上限
	/** Permission denied. */
	/** 用户无权限。 */
	PERMISSION_DENIED = 603, //用户无权限 例如如果用户被封禁，发送消息时会提示该错误。
	/** Internal error. */
	/** 消息回调函数内部错误。 */
	WEBIM_LOAD_MSG_ERROR = 604, //消息发送失败下行
	/** The current user is already in the group. */
	/** 当前用户已在该群组中。 */
	GROUP_ALREADY_JOINED = 601, //|已在该群组中|调用 加入群组的 API 时如果已经在该群组中则提示该错误|
	/** The maximum number of group members exceeds the limit during group creation. */
	/** 创建群组时群成员最大数量超出限制。 */
	GROUP_MEMBERS_LIMIT = 607, // 创建群组，群成员数量超出了，群组设置的最大数量
	/** Group disabled */
	/** 群组被禁用 */
	/** @hidden */
	GROUP_IS_DISABLED = 608, // 群组被禁用
	/** Failed to set the custom attributes of a group member. */
	/** 群成员属性设置失败。 */
	GROUP_MEMBER_ATTRIBUTES_SET_FAILED = 609, // 群成员属性更新失败

	/** Invalid token or App Key. */
	/** 没有 token 或 appkey。 */
	REST_PARAMS_STATUS = 700, // 700 参数校验
	/** The user being operated is not in the chatroom. */
	/** 被操作的人员不在聊天室。 */
	CHATROOM_NOT_JOINED = 702, // 操作的人不在聊天室
	/** The number of chatroom members reaches the limit.*/
	/** 聊天室人数达到上限。 */
	CHATROOM_MEMBERS_FULL = 704, // 聊天室人数达到上限
	/** The chatroom is not found. */
	/** 聊天室不存在。 */
	CHATROOM_NOT_EXIST = 705, // 聊天室不存在
	/** A local database operation failed. */
	/** 本地数据库操作失败。*/
	LOCAL_DB_OPERATION_FAILED = 800, //数据库操作失败
	/** Websocket error. */
	/** Websocket 发送消息错误。 */
	SDK_RUNTIME_ERROR = 999, // sock.send 报错
	/** The parameter length exceeds the limit when posting custom presence status. */
	/** 发布自定义在线状态时，参数长度超出限制。 */
	PRESENCE_PARAM_EXCEED = 1100, // 调用 Presence 相关方法时参数长度超出限制。
	/** The Reaction already exists. */
	/** Reaction 重复添加。 */
	REACTION_ALREADY_ADDED = 1101, // 已经添加过reaction又重复添加
	/** A Reaction is being created by multiple users at the same time. */
	/** 创建 Reaction 时，其他人正在创建。 */
	REACTION_CREATING = 1102, // 正在创建 reaction
	/** The user does not have the permission for the Reaction operation. For example, the user who does not add the reaction attempts to delete it, or the user that is neither the sender nor recipient of the one-to-one message attempts to add the Reaction. */
	/** 添加 reaction 时，当前用户不是这条消息的发送者或接收者。 */
	REACTION_OPERATION_IS_ILLEGAL = 1103, // 当前用户不是这条消息的发送者或接收者
	/** Invalid language code. */
	/** 传入的语言 code 不合法。 */
	TRANSLATION_NOT_VALID = 1200, // 翻译的语言 code 不支持
	/** The translated text is too long. */
	/** 翻译的文本过长。 */
	TRANSLATION_TEXT_TOO_LONG = 1201, // 翻译的文本过长
	/** Failed to obtain the translation service. */
	/** 获取第三方翻译服务失败。 */
	TRANSLATION_FAILED = 1204, // 第三方翻译服务失败
	/** The chatThread is not found. */
	/** chatThread 不存在。 */
	THREAD_NOT_EXIST = 1300, //chatThread没有找到
	/** Chat thread already exists. */
	/** chatThread 已经存在。 */
	THREAD_ALREADY_EXIST = 1301, //消息id已经创建了thread
	/** The message to modify does not exist. */
	/** 修改的消息不存在。 */
	MODIFY_MESSAGE_NOT_EXIST = 1302, // 被编辑的消息不存在
	/** The format of the modified messaged is incorrect. */
	/** 消息修改格式错误。 */
	MODIFY_MESSAGE_FORMAT_ERROR = 1303, // 编辑消息格式错误
	/** The message modification failed. */
	/** 消息修改失败。*/
	MODIFY_MESSAGE_FAILED = 1304, // 编辑消息失败
	/** The current conversation not exist . */
	/** 操作的用户会话不存在。 */
	CONVERSATION_NOT_EXIST = 1400, //操作的用户会话不存在
}
export { Code };
