import { SendMsgResult } from '../types/engineCore';
import { ChatType, MessagePriority, ModifiedMsgInfo } from '../types/message';
import { utils } from '../utils/index';
export interface ImgParameters {
	/** The message type. */
	/** 消息类型。 */
	type: 'img';
	/** The message ID. */
	/** 消息 ID。 */
	id: string;
}

export interface FileObj {
	url: string;
	filename: string;
	filetype: string;
	data: File;
}

export interface ImgMsgSetParameters {
	/** Session type. */
	/** 会话类型。 */
	chatType: ChatType;
	/** The recipient. */
	/** 消息接收方。 */
	to: string;
	/** The file object. */
	/** 文件对象。 */
	file?: FileObj;
	/** The image width. */
	/** 图片宽度。 */
	width?: number;
	/** The image height. */
	/** 图片高度。 */
	height?: number;
	/** The input ID of the file to be uploaded. */
	/** 上传文件的 input ID。 */
	fileInputId?: string;
	/** The sender, which can only be the current user and can not be changed. */
	/** 消息发送方，只能是当前用户，无法修改。*/
	from?: string;
	/** @deprecated Whether the session type is chat room. */
	/** @deprecated 会话类型是否是聊天室。 */
	roomType?: boolean;
	/** Whether it's group chat. */
	/** 是否是群聊。 */
	group?: string;
	/** The callback for message sending success. */
	/** 消息发送成功的回调。 */
	success?: (data: SendMsgResult) => void;
	/** The callback for a message sending failure. */
	/** 消息发送失败的回调。 */
	fail?: () => void;
	/** The message extension. */
	/** 消息扩展。 */
	ext?: { [key: string]: any };
	/** The file URL. If the file is uploaded, you can directly use the URL. */
	/** 文件 URL。如果文件已经上传，可直接使用 URL。 */
	url?: string;
	/** The callback of a file upload error. */
	/** 文件上传错误的回调。 */
	onFileUploadError?: (error: any) => void;
	/** The callback of file upload completion. */
	/** 文件上传完成的回调。 */
	onFileUploadComplete?: (data: any) => void;
	/** The callback of the file upload progress. */
	/** 文件上传进度的回调。 */
	onFileUploadProgress?: (data: any) => void;
	/** The message body. */
	/** 消息体。 */
	body?: {
		/** The file URL. */
		/** 文件 URL。 */
		url: string;
		/** The file type. */
		/** 文件类型。 */
		type: string;
		/** The file name. */
		/** 文件名。 */
		filename: string;
	};
	/** Whether read receipts are required during a group session. */
	/** 群组会话时是否需返回已读回执。*/
	msgConfig?: { allowGroupAck: boolean };
	/** Whether the message is a thread message. */
	/** 消息是否为子区消息 */
	isChatThread?: boolean;
}

export interface ImgMsgBody extends CreateImgMsgParameters {
	/** The message ID. */
	/** 消息 ID。 */
	id: string;
	/** Time. */
	/** 时间。 */
	time: number;
	/** The key required to download the file. */
	/** 下载文件所需要的密钥。*/
	secret?: string;
	/** The thumbnail URL. */
	/** 缩略图地址。 */
	thumb?: string;
	/** The key required to download the thumbnail. */
	/** 下载缩略图所需要的密钥。 */
	thumb_secret?: string;
	/** @deprecated Whether the session type is chat room. */
	/** @deprecated 会话类型是否是聊天室。 */
	roomType?: boolean;
	/** @deprecated Whether the session type is group. */
	/** @deprecated 会话类型是否是群组。 */
	group?: string;
	/** Whether read receipts are required during a group session. */
	/** 群组会话时是否需返回已读回执。*/
	msgConfig?: { allowGroupAck: boolean };
	/** Whether global notify message or not. */
	/** 判断是否是全局广播消息。*/
	broadcast?: boolean;
	/** Whether the message content is replaced. This property is valid only when `useReplacedMessageContent` is set to `true` during initialization. */
	/** 消息内容是否已被替换。该属性仅在初始化时开启 `useReplacedMessageContent` 后有效。*/
	isContentReplaced?: boolean;
	/** Message modified info. */
	/** 消息修改信息。 */
	modifiedInfo?: ModifiedMsgInfo;
	/** The callback for message sending success. */
	/** 消息发送成功的回调。 */
	success?: (data: SendMsgResult) => void;
	/** The callback for a message sending failure. */
	/** 消息发送失败的回调。 */
	fail?: () => void;
}

export interface CreateImgMsgParameters {
	/** The message type. */
	/** 消息类型。 */
	type: 'img';
	/** Session type. */
	/** 会话类型。 */
	chatType: ChatType;
	/** The file object. */
	/** 文件对象。 */
	file?: FileObj;
	/** The file URL. If the file is uploaded, you can directly use the URL. */
	/** 文件 URL。如果文件已经上传，可直接使用 URL。 */
	url?: string;
	/** The image width. */
	/** 图片宽度。 */
	width?: number;
	/** The image height. */
	/** 图片高度。 */
	height?: number;
	/** The image file length. */
	/** 图片文件大小。 */
	file_length?: number;
	/** The recipient. */
	/** 消息接收方。 */
	to: string;
	/** The sender, which can only be the current user and can not be changed. */
	/** 消息发送方，只能是当前用户，无法修改。*/
	from?: string;
	/** The callback of a file upload error. */
	/** 文件上传错误的回调。 */
	onFileUploadError?: (error: any) => void;
	/** The callback of file upload completion. */
	/** 文件上传完成的回调。 */
	onFileUploadComplete?: (data: any) => void;
	/** The callback of the file upload progress. */
	/** 文件上传进度的回调。 */
	onFileUploadProgress?: (data: any) => void;
	/** The message extension. */
	/** 消息扩展。 */
	ext?: { [key: string]: any };
	/** The message body. */
	/** 消息体。 */
	body?: {
		/** The file URL. */
		/** 文件 URL。 */
		url: string;
		/** The file type. */
		/** 文件类型。 */
		type: string;
		/** The file name. */
		/** 文件名。 */
		filename: string;
	};
	/** Whether read receipts are required during a group session. */
	/** 群组会话时是否需返回已读回执。*/
	msgConfig?: { allowGroupAck: boolean };
	/** Whether the message is a thread message. */
	/** 消息是否为子区消息 */
	isChatThread?: boolean;
	/** Message priority. */
	/** 消息优先级 */
	priority?: MessagePriority;
	/** Whether the message is delivered only when the recipient(s) is/are online:
	 *  - `true`: The message is delivered only when the recipient(s) is/are online. If the recipient is offline, the message is discarded.
	 *  - (Default) `false`: The message is delivered when the recipient(s) is/are online. If the recipient(s) is/are offline, the message will not be delivered to them until they get online.
	 */
	/** 消息是否只投递给在线用户：
	 * - `true`: 只有消息接收方在线时才能投递成功。若接收方离线，则消息会被丢弃。
	 * - （默认）`false`: 如果用户在线，则直接投递；如果用户离线，消息会在用户上线时投递。
	 */
	deliverOnlineOnly?: boolean;
	/** The list of message recipients. */
	/** 消息接收方列表。 */
	receiverList?: string[];
	/** The thumbnail width. */
	/** 缩略图宽度。 */
	thumbnailWidth?: number;
	/** The thumbnail height. */
	/** 缩略图高度。 */
	thumbnailHeight?: number;
}

export class Img {
	id: string;
	type: 'img';
	body?: ImgMsgBody;
	constructor(parameters: ImgParameters) {
		const type = parameters.type;
		const id = parameters.id || utils.getUniqueId();
		this.id = id;
		this.type = type;
	}
	/**
	 * @hidden
	 */
	set(options: ImgMsgSetParameters) {
		options.file =
			options.file ||
			((options.fileInputId &&
				utils.getFileUrl(options.fileInputId)) as FileObj);
		this.body = {
			id: this.id,
			chatType: options.chatType || 'singleChat',
			type: 'img',
			file: options.file,
			width: options.width,
			height: options.height,
			to: options.to,
			from: options.from || '',
			roomType: options.roomType,
			success: options.success,
			fail: options.fail,
			ext: options.ext,
			time: Date.now(),
			onFileUploadError: options.onFileUploadError,
			onFileUploadComplete: options.onFileUploadComplete,
			onFileUploadProgress: options.onFileUploadProgress,
			body: options.body,
			isChatThread: options.isChatThread,
		};
	}
	/**
	 * @hidden
	 */
	setChatType(chatType: ChatType): boolean {
		if (this.body) {
			this.body.chatType = chatType;
			return true;
		}
		return false;
	}
	// 3.0 api, will Deprecate in version 4.0
	/**
	 * @hidden
	 */
	setGroup(group: string): boolean {
		if (this.body) {
			this.body.group = group;
			return true;
		}
		return false;
	}
	static create(options: CreateImgMsgParameters): ImgMsgBody {
		return {
			id: utils.getUniqueId(),
			chatType: options.chatType,
			type: 'img',
			url: options.url,
			width: options.width,
			height: options.height,
			file: options.file,
			to: options.to,
			from: options.from || '',
			ext: options.ext,
			time: Date.now(),
			msgConfig: options.msgConfig,
			file_length: options.file_length,
			onFileUploadError: options.onFileUploadError,
			onFileUploadComplete: options.onFileUploadComplete,
			onFileUploadProgress: options.onFileUploadProgress,
			isChatThread: options.isChatThread,
			priority: options.priority,
			deliverOnlineOnly: options.deliverOnlineOnly,
			receiverList: options.receiverList,
			thumbnailWidth: options.thumbnailWidth,
			thumbnailHeight: options.thumbnailHeight,
		};
	}
}

