import { Connection } from '../engineCore/connection';
import { utils } from '../utils';
import { PresenceType, StatusDetailsType } from '../types/presenceApi';
import { ReactionMessage, Reaction } from '../types/reactionApi';
import {
	ThreadNotifyServerMsg,
	ThreadChangeInfo,
	onChatThreadChangeType,
} from '../types/threadApi';
import {
	RoamingDeleteMultiDeviceInfo,
	ConversationChangedInfo,
	ConversationChangedInfoOperation,
	MessagePinOperation,
	NotificationConMultiDeviceInfo,
	NotificationUserMultiDeviceInfo,
} from '../types/engineCore';
import { logger } from '../logger';
import { UserId } from '../types/common';
import { formatMessage } from '../apis/formatLastMessage';
import { MarkType } from '../types/indexApi';
import { CHAT_TYPE_MAP } from '../apis/formatLastMessage';
import { MessagePinEvent } from '../types/eventHandler';

interface ItemType {
	uid: string;
	last_time: number;
	expiry: number;
	ext: string;
	status: object;
}

type PresenceMsgToFix = any;

interface NotifyMessage {
	type: string;
	data:
		| ReactionServerMsg[]
		| ThreadNotifyServerMsg
		| DeleteRoamingMessage
		| ConversationServerMsg;
	values?: NotificationMessage[] | [];
}

interface ReactionServerMsg {
	channel_type: 'chat' | 'groupChat';
	messageId: string;
	from: UserId;
	to: UserId;
	reactions: Reaction[];
	ts: number;
}

interface DeleteRoamingMessage {
	appKey: string;
	chatType: 'chat' | 'groupchat';
	from: string;
	messageRoamingType: 'deleteMsgId' | 'deleteTime';
	msgIdList?: string[];
	operator: string;
	resource: string;
	to: string;
	deleteTime?: number;
	lastMsgId?: string;
}

interface ConversationServerMsg {
	op:
		| 'top'
		| 'not_top'
		| 'del'
		| 'mark'
		| 'mark_delete'
		| 'pin'
		| 'pin_delete';
	id: string;
	type: 'chat' | 'groupChat';
	from: string;
	res: string;
	ts: number;
	ext?: string;
}

export interface NotificationMessage {
	type: 'user_notification_mute';
	values: (
		| {
				operator_resource: string;
				group: string;
				data: {
					type: 'AT' | 'NONE' | 'ALL';
					ignoreDuration: number;
				};
		  }
		| {
				operator_resource: string;
				user: string;
				data: {
					type: 'AT' | 'NONE' | 'ALL';
					ignoreDuration: number;
				};
		  }
		| {
				operator_resource: string;
				user: string;
				data: {
					ignoreInterval: string;
				};
		  }
	)[];
}

/** this is a handler event for users use listen event */
/** 给用户监听的处理事件 */
function handleNotify(this: Connection, meta: any) {
	const msg: NotifyMessage = utils.parseNotify(meta.payload);
	switch (msg.type) {
		case 'presence':
			handlePresence.call(this, msg);
			break;
		case 'reaction':
			handleReaction.call(this, msg);
			break;
		case 'thread':
			handleThread.call(this, msg);
			break;
		case 'roaming_delete':
			handleDeleteRoaming.call(this, msg);
			break;
		case 'conv':
			handleConversation.call(this, msg);
			break;
		case 'user_notification_mute':
			// 会话免打扰多设备通知
			handleNotification.call(this, msg as NotificationMessage);
			break;
		default:
			logger.error(`unexpected notify type: ${msg.type}`);
			break;
	}
}

function handlePresence(this: Connection, msg: NotifyMessage) {
	const data: PresenceType[] = [];
	const statusArray: StatusDetailsType[] = [];
	const presenceData = msg.data as PresenceMsgToFix;
	presenceData &&
		presenceData.values &&
		presenceData.values.forEach((item: ItemType) => {
			Object.entries(item.status).forEach((val) => {
				statusArray.push({
					device: val[0],
					status: Number(val[1]),
				});
			});
			data.push({
				userId: item.uid,
				lastTime: Number(item.last_time),
				expire: Number(item.expiry),
				ext: item.ext,
				statusDetails: statusArray,
			});
		});
	/** Occurs when the presence state of a subscribed user changes. */
	/** 被订阅用户的在线状态变更回调。 */
	this.onPresenceStatusChange && this.onPresenceStatusChange(data);
	this.eventHandler?.dispatch('onPresenceStatusChange', data);
}

function handleReaction(this: Connection, msgs: NotifyMessage) {
	const reactionMsgs = msgs.data as ReactionServerMsg[];
	reactionMsgs.forEach((item: ReactionServerMsg) => {
		const reactionMsg: ReactionMessage = {
			from: item.from,
			to: item.to,
			chatType: item.channel_type === 'chat' ? 'singleChat' : 'groupChat',
			messageId: item.messageId,
			reactions: item.reactions,
			ts: item.ts,
		};
		this.eventHandler?.dispatch('onReactionChange', reactionMsg);
	});
}
function handleThread(this: Connection, msg: NotifyMessage) {
	if (msg.data) {
		const notify = msg.data as ThreadNotifyServerMsg;
		const notifyInfo: ThreadChangeInfo = {
			id: notify.id || '',
			name: notify.name || '',
			parentId: notify.muc_parent_id || '',
			messageId: notify.msg_parent_id || '',
			timestamp: notify.timestamp || 0,
			operator: notify.from || '',
			operation: '' as onChatThreadChangeType,
		};
		switch (notify.operation) {
			case 'create': {
				notifyInfo.operation = 'create';
				notifyInfo.createTimestamp = notifyInfo.timestamp;
				notifyInfo.messageCount = 0;
				this.eventHandler?.dispatch('onChatThreadChange', notifyInfo);
				break;
			}
			case 'update_msg': {
				notifyInfo.operation = 'update';
				notifyInfo.messageCount = notify.message_count;
				notify.last_message &&
				JSON.stringify(notify.last_message) !== '{}'
					? (notifyInfo.lastMessage = formatMessage.call(
							this,
							notify.last_message
					  ))
					: JSON.stringify(notify.last_message) === '{}'
					? (notifyInfo.lastMessage = {})
					: null;
				this.eventHandler?.dispatch('onChatThreadChange', notifyInfo);
				break;
			}
			case 'update': {
				notifyInfo.operation = 'update';
				notifyInfo.messageCount = notify.message_count;
				this.eventHandler?.dispatch('onChatThreadChange', notifyInfo);
				break;
			}
			case 'delete': {
				notifyInfo.operation = 'destroy';
				this.eventHandler?.dispatch('onChatThreadChange', notifyInfo);
				break;
			}
			default:
				break;
		}
	}
}

function handleDeleteRoaming(this: Connection, msg: NotifyMessage) {
	const info = msg.data as DeleteRoamingMessage;
	if (info.resource === this.clientResource) {
		// Block notifications to yourself
		return;
	}
	const roamingDeleteInfo: RoamingDeleteMultiDeviceInfo = {
		operation: 'deleteRoaming',
		conversationId: info.to,
		chatType: info.chatType === 'chat' ? 'singleChat' : 'groupChat',
		resource: info.resource,
	};
	this.eventHandler?.dispatch('onMultiDeviceEvent', roamingDeleteInfo);
}
function handleConversation(this: Connection, msg: NotifyMessage) {
	/*
    {
        "type":"conv",
        "data":
        {
            "op":"top",// 会话置顶/not_top(取消置顶)/del(删除会话)/markConversation(标记会话)/unMarkConversation(移除会话标记)/pin(置顶消息)/pin_delete(取消置顶消息)
            "id" : conversation id, // 会话id
            "type" : "chat", // 会话类型, 单聊chat/群聊groupchat
            "from" : operator, // 操作者
            "res": "resource", // 设备 resource
            "ts": 666666   // 操作时间
						"ext" 'mark_1' // 会话标记
        }
    }
    */
	const info = msg.data as ConversationServerMsg;
	let operation = '' as
		| ConversationChangedInfoOperation
		| MessagePinOperation;
	if (this.clientResource === info.res && info.from === this.user) {
		// Block notifications to yourself
		return;
	}
	switch (info.op) {
		case 'del':
			operation = 'deleteConversation';
			break;
		case 'top':
			operation = 'pinnedConversation';
			break;
		case 'not_top':
			operation = 'unpinnedConversation';
			break;
		case 'mark':
			operation = 'markConversation';
			break;
		case 'mark_delete':
			operation = 'unMarkConversation';
			break;
		case 'pin':
			operation = 'pin';
			break;
		case 'pin_delete':
			operation = 'unpin';
			break;
		default:
			logger.error('unexpected conversation op:', info.op);
			// Block unknown operation
			return;
	}

	if (operation === 'pin' || operation === 'unpin') {
		const { ext, from, id, type, ts } = info;
		const dt: MessagePinEvent = {
			messageId: ext || '',
			conversationId: type === 'chat' ? from : id,
			conversationType: CHAT_TYPE_MAP[
				type as keyof typeof CHAT_TYPE_MAP
			] as MessagePinEvent['conversationType'],
			operation,
			operatorId: from,
			time: ts,
		};
		this.eventHandler?.dispatch('onMessagePinEvent', dt);
		return;
	}

	const conversationChangedInfo: ConversationChangedInfo = {
		operation: operation,
		conversationId: info.id,
		conversationType: info.type === 'chat' ? 'singleChat' : 'groupChat',
		timestamp: info.ts,
	};

	if (
		conversationChangedInfo.operation === 'markConversation' ||
		conversationChangedInfo.operation === 'unMarkConversation'
	) {
		if (info.ext) {
			conversationChangedInfo.conversationMark =
				MarkType[info.ext as keyof typeof MarkType];
		}
	}

	this.eventHandler?.dispatch('onMultiDeviceEvent', conversationChangedInfo);
}

function handleNotification(this: Connection, msg: NotificationMessage) {
	msg.values?.forEach((item) => {
		if (item.operator_resource === this.clientResource) {
			return;
		}
		if ('ignoreInterval' in item.data) {
			const data: NotificationUserMultiDeviceInfo = {
				operation: 'setSilentModeForUser',
				resource: item.operator_resource,
				data: item.data,
			};
			this.eventHandler?.dispatch('onMultiDeviceEvent', data);
		} else if ('group' in item) {
			const data: NotificationConMultiDeviceInfo = {
				operation:
					Object.keys(item.data ?? {}).length === 0
						? 'removeSilentModeForConversation'
						: 'setSilentModeForConversation',
				resource: item.operator_resource,
				conversationId: item.group,
				type: 'groupChat',
				data: item.data,
			};

			this.eventHandler?.dispatch('onMultiDeviceEvent', data);
		} else {
			const data: NotificationConMultiDeviceInfo = {
				operation:
					Object.keys(item.data ?? {}).length === 0
						? 'removeSilentModeForConversation'
						: 'setSilentModeForConversation',
				resource: item.operator_resource,
				conversationId: item.user,
				type: 'singleChat',
				data: item.data,
			};
			this.eventHandler?.dispatch('onMultiDeviceEvent', data);
		}
	});
}

export default handleNotify;
